/* stylelint-disable */

#amd-hero-scene--wrapper {
    position: relative;
    width: 100%;
    padding-top: 51.88%;
}

#amd-hero-scene {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

#hero-grid {
    position: absolute;
    left: 2.71%;
    top: 17.46%;
    width: 27.17%;

    svg {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
    }

    &:before {
        content: '';
        display: block;
        padding-top: 99.72%;
    }
}

#hero-dots {
    position: absolute;
    left: 78.82%;
    top: 62.19%;
    width: 13.47%;

    svg {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
    }

    &:before {
        content: '';
        display: block;
        padding-top: 100%;
    }
}

@keyframes rotateClockwise {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@keyframes rotateCounterClockwise {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(-360deg);
    }
}

@keyframes objectFloating {
    0% {
        transform: translate(0, 0);
    }
    50% {
        transform: translate(0, 10px);
    }
    100% {
        transform: translate(0, 0);
    }
}

@keyframes floating {
    from { transform: rotate(0deg) translate3d(20px, 10px, 0) rotate(0deg); }
    to   { transform: rotate(360deg) translate3d(20px, 10px, 0) rotate(-360deg); }
}

@keyframes floating-2 {
    from { transform: rotate(0deg) translateX(20px) rotate(0deg); }
    to   { transform: rotate(360deg) translateX(20px) rotate(-360deg); }
}

@keyframes grow {
    0% { transform: scale(0.25) rotate(25deg); }
    30%   { transform: scale(1) rotate(0); }
    60%   { transform: scale(1) rotate(0); }
    90%   { transform: scale(0.25) rotate(25deg); }
    100%   { transform: scale(0.25) rotate(25deg); }
}

