/*
 * Start Webpage styles
 */

/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */

.page-template-page_template-start-webpage {
    background-color: $gray9;
    color: $steel;
    font-size: 15px;
    line-height: 1.6;

    .site-content__primary,
    .content-new {
        background: none;
    }

    .content-new {
        padding: 0;
        margin: 0;
        max-width: none;

        > .container {
            margin: 0;
            max-width: none;
            padding: 0;
        }
    }
}

// anchor color
.sw-sections,
.faq-section.style-01 .notes,
.faq-section.style-01 .slide-content,
.sw-disclosure {
    a:not(.btn) {
        text-decoration: none;
        color: $bayside;

        &:hover {
            text-decoration: underline;
        }
    }
}

.sw-sections {
    position: relative;
    z-index: 1;
    background-color: white;

    .bg-wave {
        position: absolute;
        top: -160px;
        right: 0;
        bottom: 0;
        width: 34%;
        max-width: 520px;
        overflow: hidden;
        z-index: -1;

        @include breakpoints($to: 's-medium') {
            bottom: auto;
            height: 174px;
            width: 320px;
            top: -94px;

            .admin-bar & {
                top: -140px;
            }
        }

        svg {
            position: absolute;
            right: -20px;
            top: 0;

            @include breakpoints($to: 's-medium') {
                top: auto;
                bottom: 33px;
            }
        }
    }
}

.sw-intro {
    padding: 43px 0 55px;

    h1 {
        font-size: 30px;
        line-height: 1.13;
        margin-bottom: 16px;

        @include breakpoints(s-medium) {
            font-size: 40px;
            line-height: 1.15;
            margin-bottom: 24px;
        }

        span {
            color: $poppy;
        }
    }

    p {
        margin-bottom: 24px;
    }

    .btns {
        display: flex;
        flex-wrap: wrap;
        gap: 8px;
        padding-top: 3px;

        > a {
            display: flex;
            align-items: center;
            height: 36px;
            padding: 4px 20px;
            color: $white;
            font-weight: 600;
            text-transform: uppercase;
            letter-spacing: -0.005em;
            text-decoration: none;
            transition: background-color 0.25s ease;

            &:hover {
                text-decoration: none;
            }
        }

        .bg-avocado {
            background-color: $avocado;

            &:hover {
                background-color: darken($avocado, 10%);
            }
        }

        .bg-bayside {
            background-color: $bayside;

            &:hover {
                background-color: #39a9e5;
            }
        }

        .bg-cablecar {
            background-color: $cable_car;

            &:hover {
                background-color: darken($cable_car, 10%);
            }
        }

        .bg-citrus {
            background-color: $citrus;

            &:hover {
                background-color: darken($citrus, 10%);
            }
        }

        .bg-deep-ocean {
            background-color: $deep_ocean;

            &:hover {
                background-color: darken($deep_ocean, 10%);
            }
        }

        .bg-grapevine {
            background-color: $grapevine;

            &:hover {
                background-color: darken($grapevine, 10%);
            }
        }

        .bg-marina {
            background-color: $marina;

            &:hover {
                background-color: darken($marina, 10%);
            }
        }

        .bg-pine {
            background-color: $pine;

            &:hover {
                background-color: darken($pine, 10%);
            }
        }
    }
}

.sw-starters {
    padding-bottom: 50px;

    h2 {
        font-size: 18px;
        line-height: 1.34;
        margin-bottom: 2px;
        color: $poppy;

        ~ p {
            margin-bottom: 15px;
        }
    }

    .sw-starter-slider {
        .slick-list {
            margin: 0 -10px;
        }

        .slick-track,
        .slick-slide {
            display: flex;
        }

        .slick-slide {
            height: auto;
        }
    }

    .slick-dots {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 2px;
        gap: 8px;

        li {
            line-height: 1;

            &.slick-active button,
            button:hover {
                background: $poppy;
            }
        }

        button {
            display: block;
            padding: 0;
            margin: 0;
            font-size: 0;
            line-height: 0;
            border: none;
            width: 12px;
            height: 13px;
            background: #e3e4e3;
            transition: background 0.3s linear;
        }
    }
}

.sw-starter-box {
    background-color: #f6f7f7;
    padding: 25px 19px;
    border: 1px solid #9ea3ab;
    border-radius: 2px;
    margin: 10px;

    h3 {
        font-size: 15px;
    }
}

.faq-section.style-01 {
    padding: 40px 0 45px;

    @include breakpoints(s-medium) {
        padding: 58px 0 0;
    }

    .faq-row .accordion {
        > li {
            border-top-width: 0;

            > a {
                font-size: 18px;
                padding: 22px 0;

                &:after {
                    color: $poppy;
                }
            }
        }

        .slide-content {
            @include breakpoints($to: 's-medium') {
                font-size: 18px;
                line-height: 1.34;
            }
        }
    }

    h2 {
        font-size: 30px;
        line-height: 1.13;
        margin-bottom: 20px;

        @include breakpoints(s-medium) {
            font-size: 34px;
            line-height: 1.21;
        }
    }

    .notes {
        margin-top: 32px;

        @include breakpoints(s-medium) {
            margin-top: 48px;
        }
    }
}

.sw-disclosure {
    border-top: 1px solid $fog;
    margin-top: 50px;
    padding-top: 32px;
    font-style: italic;
    color: #abaeb1;

    p + p {
        margin-top: 24px;
    }
}
