/*
 * Portfolio page grid & layout styles
 */

.page-template-page_template-crypto-startup-school-demo,
.post-type-archive-a16z_demo,
.single-a16z_demo {
  .site {
    overflow-x: hidden;
  }

  .content-new {
    /* stylelint-disable selector-max-specificity */
    &,
    & > .container {
      max-width: none;
      padding: 0;
    }
  }
}

.crypto-banner {
  padding: 69px 0 65px;
  background-size: cover;
  background-position: center;
  text-align: center;

  h1 {
    font-weight: 300;
    font-size: 68px;
    line-height: 1.2;
    color: $white;

    @include breakpoints(none, s-medium) {
      font-size: 32px;
    }
  }
}

.crypto-content {
  max-width: 1172px;

  .intro {
    padding: 60px 0 58px;
    max-width: 829px;
    margin: 0 auto;
    font-weight: 300;
    text-align: center;

    a {
      color: $poppy;
      font-weight: 700;
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }

  .filter-bar {
    margin-bottom: 30px;

    @include breakpoints(medium) {
      margin-bottom: 59px;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
    }

    h2 {
      font-size: 36px;
      line-height: 1.2;
      font-weight: 700;

      @include breakpoints(none, s-medium) {
        margin-bottom: 30px;
        font-size: 30px;
      }
    }

    .filter {
      display: flex;
      align-items: center;
      justify-content: flex-end;

      .title {
        margin-right: 21px;
        font-weight: 700;
        flex: 1 0 auto;
      }
    }
  }
}

.nice-select {
  min-width: 206px;
  position: relative;

  a {
    color: $steel;
    text-decoration: none;
    display: block;
  }

  > a {
    border: 1px solid $poppy;
    padding: 7px 52px 7px 12px;
    position: relative;
    background: $white;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    transition: border-color 0.25s ease;

    &::after,
    &::before {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      width: 42px;
      transition: all 0.25s ease;
    }

    &::after {
      background: url('data:image/svg+xml;base64,') no-repeat center/9px auto;
    }

    &::before {
      background-color: $poppy;
    }

    &.active,
    &:hover {
      border-color: $steel;

      &::before {
        background-color: $steel;
      }
    }

    &.active::after {
      transform: scale(1,-1);
    }
  }

  > ul {
    position: absolute;
    z-index: 99;
    border: 1px solid $steel;
    top: 100%;
    left: 0;
    right: 0;
    transform: scale(0.8);
    opacity: 0;
    visibility: hidden;
    transition: all 0.25s ease;
    background: #f2f2f2;
    margin-top: -1px;
    padding: 4px 0;

    &.active {
      opacity: 1;
      transform: scale(1);
      visibility: visible;
    }

    a {
      padding: 4px 10px;
      transition: all 0.25s ease;

      &:hover {
        background-color: $steel;
        color: #f2f2f2;
      }
    }
  }
}

.crypto-article--item {
  h3 {
    transition: color 0.3s ease-out;
  }

  &:hover {
    h3 {
      color: $blue4;
    }
  }
}

[data-filter-list] {
  li {
    &.hidden {
      display: none;
    }
  }
}

.crypto-article {
  &:not(.single) {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;

    @include breakpoints(large) {
      margin: 0 -25px;
    }

    > li {
      width: 100%;
      padding: 0 12px;
      margin-bottom: 36px;

      @include breakpoints(marketomobilebkpt) {
        margin-bottom: 48px;
        width: 50%;
      }

      @include breakpoints(large) {
        padding: 0 25px;
        width: 33.33%;
      }
    }
  }

  .team-members {
    span {
      color: $steel;
    }
  }

  &.single {
    max-width: 850px;
    margin: 0 auto;

    .video-container {
      position: relative;
      padding-bottom: 56.25%;
      padding-top: 30px;
      height: 0;
      overflow: hidden;
      margin-bottom: 39px;
    }

    .video-container iframe,
    .video-container object,
    .video-container embed {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    .video-poster {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: no-repeat 50% 50% / cover;
      overflow: hidden;
      opacity: 1;
      transition: opacity 0.5s;
      border: 0 none;

      &.hidden {
        opacity: 0;
        pointer-events: none;
      }

      &:hover {
        cursor: pointer;

        svg {
          circle {
            stroke: $poppy;
          }

          path {
            fill: $poppy;
          }
        }
      }

      svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 106px;
        height: 106px;

        circle {
          transition: stroke 0.3s ease-out;
        }

        path {
          transition: fill 0.3s ease-out;
        }
      }
    }

    .video {
      margin-bottom: 39px;

      &::after {
        @include breakpoints(medium) {
          width: 106px;
          height: 106px;
        }
      }
    }

    .cat {
      margin-bottom: 22px;
    }

    p {
      margin-bottom: 26px;
    }
  }

  a {
    color: $steel;
    text-decoration: none;
    transition: all 0.25s ease;

    &:hover {
      color: $blue4;
    }
  }

  .article-item {
    display: block;
    padding-top: 56.4%;
    margin-bottom: 25px;

    @extend %objectfit;
  }

  .video {
    display: block;
    padding-top: 56.4%;
    margin-bottom: 25px;

    @extend %objectfit;

    &::after {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      width: 61px;
      height: 61px;
      background: url('../../images/crypto-startup/play.svg') no-repeat center/100% auto;
    }
  }

  .head {
    margin-bottom: 29px;
  }

  h1 {
    font-weight: 400;
    font-size: 36px;
    line-height: 1.2;
    margin-bottom: 18px;

    @include breakpoints(none, s-medium) {
      font-size: 30px;
    }
  }

  h3 {
    font-size: 22px;
    line-height: 1.15;
    font-weight: 600;
    margin-bottom: 10px;
  }

  .cat {
    display: flex;
    flex-wrap: wrap;

    a,
    span {
      background: $blue4;
      color: $white;
      text-transform: uppercase;
      font-size: 13px;
      line-height: 1.2;
      font-weight: 700;
      padding: 4px 6px 3px;
      margin: 0 10px 8px 0;
      letter-spacing: 0.08em;
    }
  }

  p {
    color: $concrete;
  }

  .social-links {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: 24px;
    padding: 10px 0 33px;

    li {
      margin-right: 20px;
      margin-bottom: 10px;
    }

    a {
      color: $concrete;

      &:hover {
        color: $blue4;
      }
    }
  }

  .breadcrumbs {
    margin-bottom: 10px;

    a {
      display: inline-block;
      vertical-align: middle;
    }

    .icon-next {
      vertical-align: middle;
      display: inline-block;
      position: relative;
      top: -1px;
      font-size: 8px;
      transform: rotate(90deg);
    }
  }
}

.post-password-form-custom {
  padding: 60px 0 58px;
  max-width: 829px;
  margin: 0 auto;
  font-weight: 300;

  h2 {
    font-size: 36px;
    line-height: 1.2;
    font-weight: 700;
    margin-bottom: 36px;

    @include breakpoints(none, s-medium) {
      margin-bottom: 30px;
      font-size: 30px;
    }
  }

  .form-password-input {
    margin-top: 24px;
    background: $white;
    position: relative;
    max-width: 460px;
    padding: 0 125px 0 0;
    font: 1rem/1.667 font-family(sans-serif);
    box-shadow: inset 0 0 0 1px $poppy;
  }

  input[type="password"] {
    background: none;
    border: none;
    outline: none;
    padding: 11px 15px;
    width: 100%;
    font-size: 0.938rem;
    line-height: 1.2;
  }

  input[type="submit"] {
    transition: background 0.3s linear;
    position: absolute;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
    background: $poppy;
    border: none;
    outline: none;
    border-radius: 0;
    cursor: pointer;
    color: $white;
    font-size: 1rem;
    font-weight: 700;
    line-height: 1;
    max-width: 125px;

    &:hover,
    &:focus {
      background: #b06613;
    }
  }
}
