/*
* Club page layout & content styles
*/

/* stylelint-disable selector-max-specificity */

body.page-template-page_template-club {

    .content-new {
        padding: 0;

        > .container {
            margin: 0;
        }
    }

    .site-content__primary,
    .archive-content--author {
        margin-bottom: 0;
    }

    #main {
        margin-bottom: rem(50);

        @include breakpoints(large) {
            padding-top: rem(16);
            margin-bottom: 0;
        }
    }

    .footer-new {
        margin-top: rem(16);
    }
}

.club-banner {
    background: $gray9;
    padding: rem(40);
    border-radius: 10px 0 10px 10px;
    position: relative;
    overflow: hidden;
    margin-bottom: rem(20);
    z-index: 1;
    line-height: 1.5;

    @include breakpoints(s-medium) {
        margin-bottom: rem(50);
    }

    &::before,
    &::after {
        content: '';
        position: absolute;
        left: -2px;
        top: -104px;
        background: url('/wp-content/themes/a16z-2015/client/images/club/pattern1.svg') no-repeat 0 0 / 300px 230px;
        width: 300px;
        height: 230px;

        @include breakpoints(s-medium) {
            background-size: 326px 262px;
            width: 326px;
            height: 262px;
        }

        @include breakpoints(s-large) {
            background-size: 466px 375px;
            width: 466px;
            height: 375px;
        }
    }

    &::after {
        background-image: url('/wp-content/themes/a16z-2015/client/images/club/pattern2.svg');
        background-size: 174px 184px;
        width: 174px;
        height: 184px;
        left: auto;
        top: auto;
        right: 2px;
        bottom: 0;
        z-index: 1;

        @include breakpoints(s-medium) {
            background-size: 248px 262px;
            width: 248px;
            height: 262px;
        }

        @include breakpoints(s-large) {
            background-size: 354px 375px;
            width: 354px;
            height: 375px;
        }
    }

    h1 {
        font-size: rem(36);
        line-height: 1;
        letter-spacing: -0.01em;
        text-align: center;
        position: relative;
        z-index: 2;
        color: $steel;

        span {
            color: $poppy;
        }

        @include breakpoints(s-medium) {
            font-size: rem(40);
        }
    }
}

.club-content-area {
    display: flex;
    flex-wrap: wrap;
    color: $steel;
    line-height: 1.625;
    letter-spacing: -0.005em;

    .aside {
        margin-bottom: rem(30);

        @include breakpoints(s-medium) {
            width: 220px;
            margin-bottom: 0;
        }

        @include breakpoints(s-large) {
            width: 250px;
        }

        @include breakpoints(m-large) {
            width: 282px;
        }

        .inner {
            height: 100%;

            @include breakpoints(s-medium) {
                padding-right: rem(30);
            }

            @include breakpoints(m-large) {
                padding-right: rem(60);
            }

            // stylelint-disable
            @media only screen and (max-width: 767px) {
                position: static !important;
                transform: none !important;
                width: 100% !important;
            }
        }
    }

    .club-content {
        flex: 1;

        .intro-desc {
            color: $steel;

            p {

                + p {
                    margin-top: rem(20);
                }
            }
        }
    }

    .section-wrap {
        padding-top: 38px;

        .heading {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            margin-bottom: 20px;

            .emo {
                margin-right: 10px;
            }

            h2 {
                color: $bayside;
                font-size: rem(30);
                line-height: 1.117;
                font-weight: 700;

                @include breakpoints(s-medium) {
                    font-size: rem(34);
                }
            }
        }

        .desc {
            margin-bottom: rem(20);
        }
    }

    .accordion {
        font-size: rem(18);
        line-height: 1.333;
        letter-spacing: -0.005em;

        > li {
            border-bottom: 1px solid $fog;

            &:first-child {
                border-top: 1px solid $fog;
            }
        }

        a[data-more] {
            position: relative;
            display: block;
            padding: rem(24 0);
            text-decoration: none;
            font-size: rem(20);
            line-height: 1.1;
            color: $steel;
            font-weight: 700;

            &::after {
                content: '';
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                background: url('/wp-content/themes/a16z-2015/client/images/club/chevron-down.svg') no-repeat 0 0 / 19px 11px;
                width: 19px;
                height: 11px;
                transition: transform 0.3s linear;
            }

            &.active::after {
                transform: translateY(-50%) rotate(-180deg);
            }
        }

        .slide {
            padding: rem(3 0 42);
        }

        ul {
            li {
                position: relative;
                padding-left: rem(32);

                &::before {
                    content: "";
                    position: absolute;
                    left: 12px;
                    top: 9px;
                    width: 5px;
                    height: 5px;
                    background: $steel;
                }

                + li {
                    margin-top: rem(10);
                }
            }

            a {
                text-decoration: none;
                color: $bayside;
                transition: color 0.3s linear;

                &:hover {
                    color: $poppy;
                }
            }
        }
    }

    .foot {
        display: flex;
        flex-wrap: wrap;
        background: $gray9;
        margin-top: rem(61);

        .img-wrap {
            width: 100%;

            @include breakpoints(s-medium) {
                width: 170px;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .desc-wrap {
            width: 100%;
            padding: rem(20);

            @include breakpoints(s-medium) {
                flex: 1;
                padding: rem(21 25);
            }

            @include breakpoints(s-large) {
                padding: rem(21 60 21 25);
            }

            .title {
                font-size: rem(20);
                line-height: 1.1;
                font-weight: 700;
                letter-spacing: -0.015em;
                display: block;
                margin-bottom: rem(6);
            }

            p {
                margin-bottom: rem(6);
            }

            .btn {
                padding: 12px 36px 11px 18px;
                position: relative;

                &::after {
                    content: "";
                    position: absolute;
                    right: 18px;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 0;
                    height: 0;
                    border-style: solid;
                    border-width: 8px 0 8px 8px;
                    border-color: transparent transparent transparent #fff;

                }

                &:hover {
                    background: #b06613;
                }
            }
        }
    }
}





// Club v2
.page-template-page_template-club-v2 {
    @extend .page-template-page_template-crypto-startup-school-demo;
    background-color: $white;

    &.header-player-mini-show {

         > .site {
             margin-top: 52px;

             #header {
                 top: 52px;
             }
         }

        &.admin-bar {
            > .site {
                margin-top: 20px;

                #header {
                    top: 20px;
                }
            }
        }

    }

    .site-content__primary {
        margin-bottom: 0;
    }

    .content-new > .container {
        margin: 0;
    }

    .container {
        max-width: 1188px;
    }

    #header {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: 100;
        background: none;

        .container {
            max-width: 1440px;
        }
    }

    h2 {
        font-size: 30px;
        line-height: 1.25;
        margin-bottom: 20px;

        @include breakpoints(s-medium) {
            font-size: 36px;
            margin-bottom: 25px;
        }
    }

    #footer {
        position: relative;

        @include breakpoints(s-large) {
            margin-top: rem(80);
        }

        &:before,
        &:after {
            content: "";
            position: absolute;
            bottom: 0;
            background-size: 100% auto;
            pointer-events: none;
        }

        &:before {
            left: 0;
            width: 384px;
            height: 478px;
            background: url('/wp-content/themes/a16z-2015/client/images/decor03.svg') no-repeat 0 0 / 384px 478px;

            @include breakpoints(none, s-large) {
                background-size: 230px 286px;
                width: 230px;
                height: 286px;
            }

            @include breakpoints(none, s-medium) {
                background-size: 138px 172px;
                width: 138px;
                height: 172px;
            }
        }

        &:after {
            width: 468px;
            height: 383px;
            right: 0;
            background: url('/wp-content/themes/a16z-2015/client/images/decor04.svg') no-repeat 0 0 / 468px 383px;

            @include breakpoints(none, s-large) {
                background-size: 286px 230px;
                width: 286px;
                height: 230px;
            }

            @include breakpoints(none, s-medium) {
                background-size: 172px 138px;
                width: 172px;
                height: 138px;
            }
        }
    }
}

.ch-border-list {
    >li {
        padding: 16px 9px;

        &+ li {
            border-top: 1px solid $fog;
        }
    }
}

.club-banner.cb--full {
