    text-align: center;
    margin-bottom: 0;
    border-radius: 0;

    .info-bar ~ #content & {
        padding-top: rem(190);
    }

    &:before,
    &:after {
        background-size: 100% auto;
        z-index: unset;
    }

    &:before {
