        width: 608px;
        top: 9px;
        left: -500px;
        background-image: url('/wp-content/themes/a16z-2015/client/images/decor01.svg');

        @include breakpoints(s-medium) {
            width: 769px;
            top: -208px;
        }

        @include breakpoints(s-large) {
            left: -324px;
        }
    }

    &:after {
        width: 560px;
        height: 538px;
        top: -85px;
        right: -300px;
        background-image: url('/wp-content/themes/a16z-2015/client/images/decor02.svg');

        @include breakpoints(s-medium) {
            width: 722px;
        }

        @include breakpoints(s-large) {
            right: -200px;
        }
    }

    .container {
        position: relative;
        z-index: 2;
        max-width: 800px;
    }

    // h1 {
    //     font-size: 50px;
    //     line-height: 1;
    //     margin-bottom: 13px;

    //     @include breakpoints(none, s-medium) {
    //         font-size: 38px;
    //     }
    // }

    p {
        margin-bottom: 22px;
    }

    a:not(.btn) {
        text-decoration: none;
        color: $bayside;
        transition: color 0.3s linear;

        &:hover {
            color: darken($bayside, 10%);
        }
    }
}

.ch-upcoming {
    padding: 50px 0 65px;
    line-height: 1.5;

    h2 {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    .logo-clubhouse {
        width: 166px;
        margin-top: -4px;

        @include breakpoints(none, s-medium) {
            width: 140px;
        }

        img {
            display: block;
        }
    }
}

.ch-latest-episodes {
    margin-bottom: 52px;
    line-height: 1.5;

    .ch-border-list {
        >li {
            border-top: 1px solid $fog;

            &.hidden {
                display: none;
            }

            @include breakpoints(s-medium) {
                padding-left: 17px;
            }
        }
    }

    .link-wrap {
        margin-top: 25px;
        text-align: center;
    }

    .link {
        font-size: 16px;
        padding-right: 26px;

        &:after {
            content: "\e90d";
            font-size: 12px;
        }
    }
}

.ch-upcoming-item,
.ch-latest-item {
    color: $concrete;
    text-decoration: none;

    h3 {
        font-weight: 800;
        font-size: 22px;
        line-height: 1.18;
        color: $steel;
        margin-bottom: 3px;
    }
}

.ch-upcoming-item {
    display: flex;
    align-items: center;

    .time {
        width: 74px;
        height: 86px;
        border: 1px solid $fog;
        text-align: center;
        padding: 9px 5px;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.2;
        margin-right: 20px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        @include breakpoints(s-medium) {
            margin-right: 24px;
            width: 88px;
            height: 95px;
            padding: 15px 5px;
        }

        > * {
            display: block;
            width: 100%;
        }

        > strong {
            font-weight: 800;
            color: $poppy;
            font-size: 18px;
        }

        > span {
            margin-top: 7px;
            letter-spacing: 2px;
        }
    }

    .desc {
        flex: 1;
    }

    h3 {
        span {
            font-weight: 400;

            &:before {
                @include breakpoints(479px) {
                    content: '•';
                    padding: 0 8px 0 3px;
                }
            }

            @include breakpoints($to: 479px) {
                display: block;
                font-size: 80%;
            }
        }
    }
}

.ch-latest-item {
    display: block;
    position: relative;

    @include breakpoints(s-medium) {
        padding-right: 9%;
    }

    .more {
        color: $poppy;
        display: flex;
        align-items: center;
        margin-top: 11px;

        @include breakpoints(s-medium) {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 22px;
            margin-top: 0;
        }

        .icon-play-b {
            margin-right: 12px;
            font-size: 32px;

            @include breakpoints(s-medium) {
                margin-right: 0;
            }
        }

        span {
            @include breakpoints(s-medium) {
                display: none;
            }
        }
    }

    .date {
        display: block;
        color: $concrete;
        margin-bottom: 5px;
    }
}

.ch-shows {
    background: $gray9;
    padding: rem(64 0);
    position: relative;
    color: $steel;

    @include breakpoints(none, s-medium) {
        padding: rem(50 0);
    }

    + .ch-subscribe {
        margin-top: rem(40);
    }

    h2 {
        margin-bottom: rem(29);
        color: $steel;
    }

    .row {
        margin: -20px;
        display: flex;
        flex-wrap: wrap;

        @include breakpoints(none, sm-large) {
            margin: -10px;
        }

        .col {
            padding: 20px;
            display: flex;
            width: 33.33%;

            @include breakpoints(none, sm-large) {
                padding: 10px;
            }

            @include breakpoints(none, s-large) {
                width: 50%;
                padding: 10px;
            }

            @include breakpoints(none, s-medium) {
                width: 100%;
            }
        }
    }

    .col-content {
        width: 100%;
        min-height: 267px;
        position: relative;
        background: $white;
        color: $white;
        font-size: rem(16);
        line-height: 1.5;
        padding: rem(24);
        display: flex;
        align-items: center;
        cursor: pointer;

        &:before {
            content: "";
            background: linear-gradient(to top, rgba(0,0,0,0.65) 0%, rgba(255,255,255,0) 100%);
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            height: 88px;
            z-index: 2;
            transition: all 0.3s linear;
        }

        &.hover,
        &:hover {

            &:before,
            .desc,
            .img {
                opacity: 0;
                visibility: hidden;
            }

            .hover-desc {
                opacity: 1;
                visibility: visible;
            }
        }

        .img {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-size: cover;
            background-position: 50% 50%;
            z-index: 1;
            transition: all 0.3s linear;
        }

        .desc {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            transition: all 0.3s linear;
            padding: rem(18 16);
            z-index: 3;
        }

        h3 {
            font-size: rem(22);
            line-height: 1.27;
            font-weight: 600;
            color: $white;

            @include breakpoints(none, s-medium) {
                font-size: rem(20);
            }
        }

        time {
            display: block;
            font-size: rem(18);
            line-height: 1.27;
            color: $poppy;
        }

        .hover-desc {
            opacity: 0;
            visibility: hidden;
            color: $steel;
            transition: all 0.3s linear;

            h3 {
                color: $steel;
                margin-bottom: 12px;
                font-size: rem(20);
                line-height: 1.4;
            }

            time {
                padding-top: 16px;
                margin-top: 16px;
                position: relative;

                &:before {
                    content: "";
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    width: 112px;
                    height: 1px;
                    background: $fog;
                }
            }
        }
    }
}

.a16z-live {
