    line-height: 1.5;
    color: $steel;

    @include breakpoints(none, s-large) {
        padding: rem(60 0 40);
    }

    @include breakpoints(none, s-medium) {
        padding: rem(50 0);
    }

    .row {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    .img-wrap {
        width: 416px;
        margin-right: rem(56);

        @include breakpoints(none, sm-large) {
            width: 340px;
            margin-right: rem(30);
        }

        @include breakpoints(none, s-large) {
            width: 50%;
            padding-right: rem(10);
            margin-right: 0;
        }

        @include breakpoints(none, s-medium) {
            width: 100%;
            padding: 0;
        }

        img {
            width: 100%;
        }
    }

    .desc {
        flex: 1;

        @include breakpoints(none, s-large) {
