            }
        }
    }

    h2 {
        font-size: rem(50);
        line-height: 1.1;
        margin-bottom: rem(26);

        @include breakpoints(none, s-large) {
            font-size: rem(44);
        }

        @include breakpoints(none, s-medium) {
            font-size: rem(36);
            margin-bottom: rem(18);
        }
    }

    .social-network {
        padding-top: rem(24);
    }

    .podcast-wrap {
        margin-bottom: rem(22);
    }
}

.social-network {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    .title {
        font-size: rem(16);
        line-height: 1.5;
        font-weight: 600;
        padding-right: rem(16);
    }

    ul {
        display: flex;
        align-items: center;
        margin: rem(0 -8);

        li {
            padding: rem(0 8);
        }

        a {
            display: block;
            font-size: 20px;
            color: $poppy;
            text-decoration: none;
            transition: color 0.3s linear;

            &:hover {
                color: darken($poppy, 10%);
            }
        }
    }
}

.podcast-wrap {
    display: flex;
    flex-wrap: wrap;
    font-size: rem(16);
    line-height: 1.4;
    margin: rem(-3 0);

    .title {
        font-weight: 500;
        padding: rem(3 31 3 0);

        @include breakpoints(none, sm-large) {
            padding-right: rem(20);
        }

        @include breakpoints(none, s-medium) {
            width: auto;
        }

        @media only screen and (max-width: 374px) {
            padding-right: rem(20);
        }
    }

    .podcast-list {
        @extend %listreset;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: rem(-5 -13);
        font-weight: 500;
        padding: rem(3 0);

        @include breakpoints(none, s-medium) {
            flex: 1;
        }

        > li {
            padding: rem(5 13);
            margin: 0;

            @include breakpoints(none, s-medium) {
                width: 100%;
            }

            > a {
                color: $poppy;
                transition: color 0.3s linear;

                &:hover {
                    color: darken($poppy, 10%);
                }
            }
        }
    }
}

.activities-nav {
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    text-transform: capitalize;

    @include breakpoints(none, s-medium) {
        padding-top: rem(3);
    }

     @media only screen and (max-width: 374px) {
        display: block;
    }

    .icon-caret-down {
        font-size: 12px;
        display: inline-block;
        vertical-align: middle;
        margin-top: rem(-3);
        transition: transform 0.3s linear;
    }

    &.up {

        .dropdown {

            ul {
                top: auto;
                bottom: 100%;
                transform: translateY(-10px);
            }

            &:hover ,
            &.hover {
                ul {
                    transform: translateY(-5px);
                }

                .icon-caret-down {
                    transform: rotate(180deg);
                }
            }
        }
    }

    > li {
        font-weight: 500;
        margin: rem(0 29 13 0);

        @include breakpoints(none, s-medium) {
            margin: rem(0 20 8 0);
        }

         @media only screen and (max-width: 374px) {
            margin-right: 0;
        }

        a {
            color: $poppy;
            text-decoration: none;
            transition: color 0.3s linear;

            &:hover {
                color: darken($poppy, 10%);
            }
        }
    }

    .dropdown {
        position: relative;
        z-index: 2;
        display: inline-block;

        ul {
            @extend %listreset;
            font-size: 16px;
            text-transform: capitalize;
            background: #fff;
            border: 1px solid $black;
            border-radius: 3px;
            padding: rem(10 0 15);
            position: absolute;
            right: -29px;
            top: 100%;
            width: 187px;
            visibility: hidden;
            opacity: 0;
            transform: translateY(10px);
            transition: visibility 0.3s linear, opacity 0.3s linear, transform 0.3s linear;

            @include breakpoints(none, s-medium) {
                right: -2px;
            }

            [class^="icon-"] {
                font-size: 22px;
                margin-right: 8px;
            }

            li {
                font-weight: 400;
            }

            a {
                display: flex;
                padding: rem(4 10 4 21);
                align-items: center;
                text-decoration: none;
                color: $black;
                transition: color 0.3s linear;

                [class^="icon-"] {
                    color: $poppy;
                }

                &:hover {
                    color: $poppy;
                }
            }
        }

        &:hover ,
        &.hover {
            ul {
                opacity: 1;
                visibility: visible;
                transform: translateY(5px);
            }
        }
    }

    &.green-theme {

        > li {

            .dropdown {

                > a {
                    color: $avocado;

                    &:hover {
                        color: darken($avocado, 10%);
                    }
                }
            }
        }

        .dropdown {

            ul {

                a {

                    [class^="icon-"] {
                        color: $avocado;
                    }

                    &:hover {
                        color: $avocado;
                    }
                }
            }
        }
    }
}

.ch-subscribe {
    color: $steel;
    line-height: 1.5;
    position: relative;
    z-index: 10;

    .inner {
        padding: rem(36 40);
        background: $gray9;

        @include breakpoints(none, s-medium) {
            padding: rem(30 25);
        }
    }

    h2 {
        font-size: rem(36);
        line-height: 1.25;
        margin-bottom: rem(16);
        color: $steel;

        @include breakpoints(none, s-medium) {
            font-size: rem(28);
        }
    }

    p {
        margin-bottom: rem(16);
    }
}

.rotating-text {

    h1 {
        font-size: 50px;
        line-height: 1;
        letter-spacing: -0.01em;
        text-align: center;
        position: relative;
        z-index: 2;
        color: $steel;
        overflow: hidden;
        padding: 2px 0;
        margin-bottom: 14px;

        @include breakpoints(none, s-medium) {
            font-size: 38px;
            overflow: hidden;
        }
    }

    .rt {

        &-item-container {
            position: relative;
            display: block;
        }

        &-item {
            display: inline-block;
            position: relative;
            color: $poppy;

            &.is-inactive {
                position: absolute;
                right: 0;
                bottom: 100%;
            }

            &.is-hidden {
                display: none !important;
            }
        }

        &-heading-item {
            display: inline-block;
            color: $steel;

            @include breakpoints(none, s-medium) {
                display: block;
            }
        }
    }
}

#header-player-mini {
    position: fixed;
    width: 100%;
    top: 0;
    display: flex;
    background: #F1FAFF;
    height: 52px;
    z-index: 99999;

    .enable-announcement-bar & {
        top: 62px;

        @include breakpoints(none, large) {
            top: 92px;
        }

        @include breakpoints(none, s-medium) {
            top: 62px;
        }
    }

    iframe {
        height: 52px !important;
    }

    #header-player {
        width: 100%;
    }

    .header-player-title-wrap {
        font-size: 13px;
        line-height: 1.3;
        padding: 10px 14px;
        border-bottom: 2px solid rgba(15, 15, 15, 0.05);
        height: 100%;

        @include breakpoints(none, s-medium) {
            display: none;
        }
    }

    .header-player-title {
        font-weight: 700;
        color: #000;
        white-space: nowrap;
    }

    .header-player-subtitle {
        color: #4100FF;
    }

    .header-player-img {
        width: 52px;
        height: 52px;
        display: block;
        padding: 9px;
        border-bottom: 2px solid rgba(15, 15, 15, 0.05);
        flex: 0 0 52px;

        @include breakpoints(none, s-medium) {
            display: none;
        }

        img {
            display: block;
            width: 34px;
            height: 34px;
        }
    }

    .header-player-simplecast-wrap {
        width: 52px;
        height: 52px;
        padding: 11px;
        flex: 0 0 52px;
        border-bottom: 2px solid rgba(15, 15, 15, 0.05);
        border-left: 2px solid rgba(15, 15, 15, 0.05);

        @include breakpoints(none, s-medium) {
            display: none;
        }
    }

    .header-player-simplecast-wrap a {
        display: block;
        width: 30px;
        height: 30px;
        background: url("../../images/icon-simplecast-logo.svg") no-repeat 50% 50%/contain;
    }
}