/*
* Crypot Policy page layout & content styles
*/

/* stylelint-disable */
/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */

body.page-template-page_template-crypto-policy {
	@extend .page-template-page_template-crypto-startup-school-demo;
	color: $steel;
	line-height: 1.625;

	.content-new {
		padding: 0;
		margin: 0;
		max-width: none;

		> .container {
			margin: 0;
			max-width: none;
			padding: 0;
		}
	}

	.site-content__primary,
	.archive-content--author {
		margin-bottom: 0;
	}

	#main {
		margin-bottom: rem(50);

		@include breakpoints(large) {
			padding-top: rem(16);
			margin-bottom: 0;
		}
	}

	.footer-new {
		margin-top: rem(16);
	}

	h2 {
		color: $grapevine;
		font-size: 50px;
		line-height: 1.1;
		margin-bottom: 24px;

		@include breakpoints(none, s-large) {
			font-size: 40px;
		}

		@include breakpoints(none, s-medium) {
			font-size: 30px;
		}
	}

	.header-new {
		z-index: 9998;
		background: none;
	}

	#footer {
		background: #f2f2f2;
	}

	.footer-new,
	.footer-new .copyright,
	.footer-nav ul li a,
	.footer-new .social-networks li a {
		color: $white;
	}

	.footer-nav ul li a:hover,
	.footer-new .social-networks li a:hover {
		color: $poppy;
	}

	.footer-nav ul li + li::before {
		background: $white;
	}

	#footer {
		background: $grapevine;
		margin-top: 0;
		padding: 32px 0 50px;
	}

	.site-footer__social-media-menu a[href*="simplecast.com"],
	.site-footer__social-media-menu a[href*="twitter.com"] {
		background: $white;
		transition: background 0.3s linear;

		&:hover {
			background: $poppy;
		}
	}

	.site-content__primary {
		margin-bottom: 0;
	}

	.input-addon .mktoFormRow .mktoFieldWrap .mktoEmailField {
		color: $steel;

		&::placeholder {
			color: #a4a6a8;
			opacity: 1;
		}
	}

	.input-addon .mktoButtonRow .mktoButtonWrap button.mktoButton {
		background: $marina !important;
		transition: background 0.3s linear;
	}

	.input-addon .mktoButtonRow .mktoButtonWrap button.mktoButton:hover {
		background: darken($marina, 10%) !important;
	}

	.summit-event-newsletter {
		background: $grapevine;
		padding: 98px 0 66px;
		color: $white;

		@include breakpoints(none, s-medium) {
			padding: 60px 0 30px;
		}

		h2 {
			color: $white !important;
			font-size: 36px;
			line-height: 1;
			font-weight: 700;
			letter-spacing: -0.005em;
			margin-bottom: 17px;

			@include breakpoints(none, s-medium) {
				font-size: 32px;
			}
		}

		.summit-event-newsletter__text {
			letter-spacing: -0.035em;
		}

		.input-addon {
			max-width: 332px;
		}
	}

	.link-load-more {
		color: $marina;
		text-decoration: none;
		display: inline-block;
		vertical-align: middle;
		font-size: 16px;
		font-weight: 700;
		text-transform: uppercase;

		&:hover {
			&::after {
				transform: translateY(0);
			}
		}

		&::after {
			content: "\e902";
			font-size: 16px;
			line-height: 1;
			font-family: "icomoon";
			margin-left: 8px;
			display: inline-block;
			vertical-align: middle;
			transition: transform 0.3s ease-in;
			transform: translateY(-2px);
		}
	}
}

[class*="cp-"] {

	.container {
		max-width: 1354px;
		padding-left: 20px;
		padding-right: 20px;
	}
}

.modal.modal-cp {
	background: rgba(0,0,0,0.3);
	color: $steel;

	.popup-box {
		max-width: 636px;
		margin: 0 auto;
		background: $white;
		padding: 36px 40px;
		box-shadow: 0 4px 40px rgba(0, 0, 0, 0.08);
		position: relative;

		@include breakpoints(none, s-medium) {
			padding: 30px;
		}
	}

	.title {
		display: block;
		color: $steel;
		margin-bottom: 16px;
		font-size: 24px;
		line-height: 1.333;
	}

	a {
		color: $marina;
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}
	}

	.close {
		position: absolute;
		right: 18px;
		top: 18px;
		color: $steel;
		cursor: pointer;
		text-decoration: none;

		&:hover {
			color: $poppy;
		}
	}
}

.cp-banner {
	background: $black;
	min-height: 450px;
	padding: 68px 0;
	display: flex;
	flex-wrap: wrap;
	align-items: flex-end;
	color: $white;
	position: relative;
	margin-top: -112px;

	@include breakpoints(none, m-large) {
		margin-top: -135px;
	}

	@include breakpoints(none, s-medium) {
		padding: 50px 0;
		margin-top: -95px;
		min-height: 350px;
	}

	&::before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 2;
		background: rgba(0,0,0,0.5);

	}

	.img {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-size: cover;
		background-position: 50% 50%;
		z-index: 1;
	}

	> .container {
		width: 100%;
		position: relative;
		z-index: 3;
	}

	.title {
		display: block;
		font-weight: 400;
		font-size: 24px;
		line-height: 1.22;
		margin-bottom: 22px;

		@include breakpoints(none, s-large) {
			font-size: 22px;
			margin-bottom: 20px;
		}

		@include breakpoints(none, s-medium) {
			font-size: 20px;
			margin-bottom: 18px;
		}
	}

	h1 {
		font-size: 60px;
		line-height: 1;
		font-weight: 700;
		color: $white;

		@include breakpoints(none, s-large) {
			font-size: 50px;
		}

		@include breakpoints(none, s-medium) {
			font-size: 40px;
		}
	}
}

.cp-intro {
	padding: 72px 0 88px;
	color: $steel;

	a {
		color: #00A3AD;

		&:hover {
			text-decoration: none;
		}
	}

	@include breakpoints(none, s-medium) {
		padding: 50px 0;
	}

	.intro-row {
		margin-bottom: 32px;
	}

	.key-benefits {

		.title {
			display: block;
			font-size: 24px;
			line-height: 1.333;
			color: $steel;
			margin-bottom: 40px;

			@include breakpoints(none, s-medium) {
				font-size: 20px;
			}
		}
	}

	.list {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin: -18px;

		>li {
			> a {
				text-decoration: none;
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				transition: margin 0.3s linear;
			}

			&.hover {
				> a {
					margin-top: -10px;
					margin-bottom: 10px;
				}
			}
		}

		li {
			padding: 18px;
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			max-width: 338px;
			position: relative;

			@include breakpoints(none, s-large) {
				width: 50%;
			}

			@include breakpoints(none, s-medium) {
				width: 100%;
			}

			&.hover {
				.popup-box {
					opacity: 1;
					visibility: visible;
					transform: translateY(-8px) scale(1);
				}
			}
		}

		.icon-wrap {

			@include breakpoints(none, s-medium) {
				max-width: 60px;
			}
		}

		strong {
			flex: 1;
			padding-left: 21px;
			font-size: 22px;
			line-height: 1.13;
			font-weight: 700;
			color: #00A3AD;

			@include breakpoints(none, s-medium) {
				font-size: 18px;
			}
		}

		.popup-box {
			background-color: white;
			box-shadow: 0 0 17px 5px rgba(black, .06);
			padding: 18px 20px;
			position: absolute;
			top: 100%;
			left: 0;
			opacity: 0;
			visibility: hidden;
			transform: translateY(30px) scale(0.9);
			transition: all 0.3s ease;
			z-index: 1;
		}
	}
}

.cp-team {
	background: $gray9;
	padding: 72px 0 82px;

	@include breakpoints(none, s-medium) {
		padding: 50px 0;
	}

	.heading {
		margin-bottom: 44px;
	}

	.team-list {
		display: flex;
		flex-wrap: wrap;
		margin: -18px -16px;

		@include breakpoints(none, s-medium) {
			margin: -10px;
		}

		li {
			width: 16.66%;
			padding: 18px 16px;

			@include breakpoints(none, s-1350) {
				width: 20%;
			}

			@include breakpoints(none, s-large) {
				width: 25%;
			}

			@include breakpoints(none, s-medium) {
				width: 50%;
				padding: 10px;
			}
		}

		a {
			display: block;
			text-decoration: none;

			&:hover strong {
				color: $marina;
			}
		}

		img {
			width: 100%;
			margin-bottom: 12px;
		}

		strong {
			display: block;
			font-size: 22px;
			line-height: 1.136;
			font-weight: 600;
			color: $steel;
			transition: color 0.3s linear;

			@include breakpoints(none, s-medium) {
				font-size: 20px;
			}
		}

		span {
			display: block;
			color: $concrete;
		}
	}
}

.cp-perspectives {
	padding: 73px 0 88px;

	@include breakpoints(none, s-medium) {
		padding: 50px 0;
	}

	.heading {
		margin-bottom: 43px;
	}

	.accordion {
		border-top: 1px solid #d0d0ce;

		li {
			border-bottom: 1px solid #d0d0ce;

			.opener {
				display: block;
				padding: 24px 0;
				position: relative;
				font-size: 22px;
				line-height: 1.3636;
				font-weight: 700;
				text-decoration: none;
				color: $steel;
				padding-right: 50px;

				@include breakpoints(none, s-medium) {
					font-size: 20px;
				}

				&::after {
					content: "\e90d";
					position: absolute;
					right: 27px;
					top: 33px;
					color: #00a3ad;
					font: 12px/1 "icomoon";
					transition: transform 0.3s linear;

					@include breakpoints(none, s-medium) {
						right: 16px;
					}
				}

				&.active::after {
					transform: rotate(180deg);
				}
			}

			.slide {
				padding: 0 112px 22px 0;
				margin-top: -10px;

				@include breakpoints(none, s-medium) {
					padding-right: 0;
				}

				a {
					color: $marina;
					text-decoration: underline;

					&:hover {
						text-decoration: none;
					}
				}
			}

			p {
				margin: 0;

				+ p {
					margin-top: 8px;
				}
			}
		}
	}
}

.cp-post-list {
	border-top: 1px solid $fog;
	margin-bottom: 41px;

	li {
		border-bottom: 1px solid $fog;

		&.hidden {
			display: none;
		}

		a {
			padding: 20px 0;
			display: block;
			text-decoration: none;
			color: $steel;

			&.has-icon {
				display: flex;
				padding: 24px 0 23px;

				.icon {
					width: 42px;
					margin-right: 24px;

					path {
						transition: fill 0.25s linear;
						fill: #d0d0ce;
					}
				}

				.desc {
					flex: 1;
				}
			}

			&:hover {
				.title {
					color: $marina;
				}

				.inline-this-svg path {
					fill: $marina;
				}
			}

			.title {
				margin-bottom: 4px;
				font-size: 22px;
				display: block;
				line-height: 1.363;
				transition: color 0.25s linear;

				@include breakpoints(none, s-medium) {
					font-size: 20px;
				}
			}

			.source {
				display: block;
				font-size: 16px;
				line-height: 1.625;

				> span {
					color: $concrete;
					margin-right: 2px;
				}
			}
		}
	}
}

.cp-resources {
	padding: 73px 0 87px;
	background: $gray9;

	@include breakpoints(none, s-medium) {
		padding: 50px 0;
	}

	.heading {
		margin-bottom: 44px;

		h2 {
			margin-bottom: 24px;
		}
	}

	.resource-list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -16px 32px;

		li {
			width: 33.333%;
			padding: 0 16px;
			margin-bottom: 32px;

			@include breakpoints(none, s-large) {
				width: 50%;
			}
	
			@include breakpoints(none, s-medium) {
				width: 100%;
			}

			a {
				display: block;
				color: $steel;
				text-decoration: none;

				&:hover {
					.img {
						img {
							transform: scale(1.05);
						}
					}

					.title {
						color: $marina;
					}
				}
			}

			.img {
				margin-bottom: 22px;
				overflow: hidden;

				img {
					width: 100%;
					transition: transform 0.25s ease-in;
				}
			}

			.title {
				font-size: 24px;
				line-height: 1.333;
				display: block;
				margin-bottom: 5px;
				transition: color 0.25s linear;

				@include breakpoints(none, s-medium) {
					font-size: 22px;
				}
			}

			.source {
				display: block;
				font-size: 16px;
				line-height: 1.625;

				> span {
					color: $concrete;
					margin-right: 2px;
				}
			}
		}
	}
}

.cp-web-policy {
	padding: 73px 0 87px;

	@include breakpoints(none, s-medium) {
		padding: 50px 0;
	}

	.heading {
		margin-bottom: 44px;

		h2 {
			margin-bottom: 24px;
		}
	}
}

.cp-latest-twitter {
	background: $gray9;
	padding: 72px 0 55px;

	@include breakpoints(none, s-medium) {
		padding: 50px 0;
	}

	.heading-row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 44px;

		@include breakpoints(none, s-medium) {
			display: block;
			margin-bottom: 30px;
		}

		h2 {
			margin-bottom: 0;

			@include breakpoints(none, s-medium) {
				margin-bottom: 10px;
			}
		}
	}

	.twitter-page-link {
		font-size: 20px;
		line-height: 1.3;
		font-weight: 700;

		@include breakpoints(none, s-medium) {
			font-size: 18px;
		}

		strong {
			color: $concrete;
			margin-right: 5px;
		}

		a {
			color: #1D9BF0;
			text-decoration: none;
			transition: color 0.25s linear;

			&:hover {
				color: $marina;
			}

			> i {
				margin-right: 6px;
				display: inline-block;
				vertical-align: middle;
			}
		}
	}

	.tweet-list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -16px;

		> li {
			width: 33.333%;
			padding: 0 16px;
			margin-bottom: 32px;

			@include breakpoints(none, s-large) {
				width: 50%;
			}
	
			@include breakpoints(none, s-medium) {
				width: 100%;
			}
		}
	}
}

.tweet {
	border: 1px solid $fog;
	padding: 24px 22px 22px;
	border-radius: 8px;
	background: white;

	.tweet-head {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 10px;

		.user-info {
			flex: 1;
			display: flex;
			align-items: center;

			.dp {
				width: 48px;
				height: 48px;
				overflow: hidden;
				border-radius: 50%;
				margin-right: 12px;
				text-decoration: none;
				display: block;
			}

			.desc {
				flex: 1;

				> strong,
				> span {
					display: block;
					line-height: 1;
					color: $steel;

					a {
						color: $steel;
						text-decoration: none;
					}
				}

				> strong {
					font-size: 18px;
					margin-bottom: 6px;
				}

				> span {
					font-size: 16px;
				}
			}
		}

		.time {
			width: 95px;
			text-align: right;
			margin-left: 10px;
			font-size: 16px;
			line-height: 1.5;
			color: $concrete;
		}
	}

	.tweet-text {
		font-size: 16px;
		line-height: 1.5;
		color: $steel;
		margin-bottom: 10px;

		a {
			color: $marina;
			text-decoration: none;

			&:hover {
				text-decoration: underline;
			}
		}
	}

	.link-more {
		text-decoration: none;
		color: $marina;
		font-size: 16px;
		line-height: 1.5;

		&:hover {
			text-decoration: underline;
		}
	}	
}

