/*
 * Summit Event Styles
 */

/* stylelint-disable */
/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */

[class*="page-template-page_template-summit-event"] {
    @extend .page-template-page_template-crypto-startup-school-demo;
    color: $steel;
    line-height: 1.625;

    .content-new > .container,
    .footer-new { margin: 0 auto; }

    .content-new {

        > .container {
            max-width: none;
            padding: 0;
        }
    }

    #header {

        .container {
            max-width: 84.375rem;
            padding-left: 1rem;
            padding-right: 1rem;

            @include breakpoints(s-medium) {
                padding-left: 1.25rem;
                padding-right: 1.25rem;
            }
        }
    }

    .container {
        width: 100%;
        max-width: 960px;
        padding-left: 20px;
        padding-right: 20px;
    }

    .info-bar {
        background: #3c3e43;
    }

    #footer {
        background: #f2f2f2;
    }
}

.page-template-page_template-summit-event .info-bar {

    ~ .site-content {
        padding-top: 62px;
    }
}

.hero-summit {
    background: $steel;
    padding: rem(148 0 96);
    text-align: center;
    color: $white;
    position: relative;
    overflow: hidden;

    &:before,
    &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        background: url('/wp-content/themes/a16z-2015/client/images/summit-event/se-illustration01.svg') no-repeat 0 0 / 456px 466px;
        width: 456px;
        height: 466px;
        z-index: 1;

        @include breakpoints(none, s-1350) {
            background-size: 320px 326px;
            width: 320px;
            height: 326px;
        }

        @include breakpoints(none, s-large) {
            background-size: 224px 228px;
            width: 224px;
            height: 228px;
        }

        @include breakpoints(none, s-medium) {
            background-size: 156px 160px;
            width: 156px;
            height: 160px;
        }
    }

    &:after {
        right: 0;
        left: auto;
        background: url('/wp-content/themes/a16z-2015/client/images/summit-event/se-illustration02.svg') no-repeat 0 0 / 490px 484px;
        width: 490px;
        height: 484px;

        @include breakpoints(none, s-1350) {
            background-size: 343px 338px;
            width: 343px;
            height: 338px;
        }

        @include breakpoints(none, s-large) {
            background-size: 240px 160px;
            width: 240px;
            height: 160px;
        }

        @include breakpoints(none, s-medium) {
            background-size: 168px 112px;
            width: 168px;
            height: 112px;
        }
    }

    > .container {
        max-width: 648px;
        position: relative;
        z-index: 2;
    }

    h1 {
        font-size: rem(85);
        line-height: 1;
        color: $white;
        font-weight: 400;
        letter-spacing: 0;
        margin-bottom: rem(8);

        @include breakpoints(none, s-large) {
            font-size: rem(65);
        }

        @include breakpoints(none, s-medium) {
            font-size: rem(45);
        }

        span {
            font-weight: 700;
        }
    }

    p {
        margin-bottom: rem(24);
    }

    .event-info {
        font-size: rem(26);
        line-height: 1.23;
        font-weight: 600;
        margin-bottom: rem(24);

        @include breakpoints(none, s-medium) {
            font-size: rem(22);
        }

        span,
        time {
            display: block;
        }
    }
}

.jquery-background-video-wrapper {
    overflow: hidden;
    background-position: center center;
    background-repeat: no-repeat;
    position: absolute !important;
    top: 0;
    right: -1px;
    bottom: 0;
    left: -1px;

    .bg-video {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;

        @media only screen and (min-width: 1025px) {
            display: none;
        }
    }

    .bg-image {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-size: cover;
    }
}

.jquery-background-video {
    position: absolute;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    top: 50%;
    left: 50%;
    object-fit: cover;
    transform: translate(-50%,-50%);

    @media only screen and (max-width: 1024px) {
        display: none;
    }
}

.js {

    .jquery-background-video {
        opacity: 0;
        transition: opacity 300ms linear;

        &.is-visible {
            opacity: 1;
        }
    }
}

.jquery-background-video-pauseplay {
    display: none !important;
}


.se-register {
    background: $gray9;
    text-align: center;

    .top-row {
        padding: rem(74 0);

        @include breakpoints(none, s-medium) {
            padding: rem(50 0);
        }
    }

    h2 {
        font-size: rem(36);
        line-height: 1.167;
        margin-bottom: rem(30);

        @include breakpoints(none, s-medium) {
            font-size: rem(32);
        }
    }

    p {
        margin-bottom: rem(27);
    }

    .img {

        img {
            width: 100%;
        }
    }
}

.se-agenda {
    padding-top: rem(38);

    @include breakpoints(none, s-medium) {
        padding-top: rem(20);
    }

    .heading {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        margin-bottom: rem(25);

        @include breakpoints(s-medium) {
            flex-direction: row-reverse;
            margin-bottom: rem(20);
        }

        h2 {
            font-size: rem(36);
            line-height: 1.167;
            margin: 0;

            @include breakpoints(none, s-medium) {
                font-size: rem(32);
            }
        }

        .print {
            font-size: rem(18);
            line-height: 1.278;

            @include breakpoints(none, s-medium) {
                width: 100%;
                text-align: right;
                margin-bottom: 10px;
            }

            a {
                display: inline-block;
                padding: 4px 0 4px 32px;
                position: relative;
                color: $concrete;
                transition: color 0.3s linear;
                text-decoration: none;

                &:hover {
                    color: $poppy;
                }

                &:before {
                    content: "\e939";
                    font: 18px/1 "icomoon";
                    position: absolute;
                    top: 50%;
                    left: 0;
                    transform: translateY(-50%);
                }
            }
        }
    }

    .top-row {
        font-size: rem(22);
        line-height: 1.3636;

        @include breakpoints(none, s-medium) {
            font-size: rem(18);
        }

        p {
            margin-bottom: rem(28);
        }
    }

    .category-nav {
        margin-bottom: rem(28);
    }

    .tab-nav-wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .tab-nav {
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        li {
            position: relative;

            &.active {

                a:before {
                    background: $gray9;
                }
            }

            + li {

                &:before {
                    content: "";
                    background: $fog;
                    width: 1px;
                    height: 40px;
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    z-index: 1;
                }
            }

            a {
                display: block;
                padding: rem(20);
                min-width: 200px;
                text-decoration: none;
                transition: background 0.3s linear;
                text-align: center;
                position: relative;

                @include breakpoints(none, small) {
                    min-width: 1px;
                    padding: rem(20);
                }

                &:before {
                    content: "";
                    position: absolute;
                    top: 0;
                    right: -1px;
                    bottom: -1px;
                    left: 0;
                    transition: background 0.3s linear;
                    z-index: 2;
                }

                > span {
                    position: relative;
                    z-index: 3;
                }
            }
        }

        .day {
            display: block;
            font-size: rem(18);
            line-height: 1.27;
            font-weight: 700;
            color: $steel;

            @include breakpoints(none, small) {
                font-size: rem(14)
            }
        }

        .date {
            display: block;
            font-size: rem(22);
            line-height: 1.318;
            font-weight: 700;
            color: $poppy;

            @include breakpoints(none, small) {
                font-size: rem(18)
            }
        }
    }

    .tab-content {
        background: $gray9;
        padding: rem(42 0 88);
        position: relative;

        @include breakpoints(none, s-large) {
            padding: rem(20 0 50);
        }

        &:before,
        &:after {
            content: "";
            position: absolute;
            background: $gray9;
            top: 0;
            bottom: 0;
            right: 100%;
            left: -9999px;
        }

        &:after {
            right: -9999px;
            left: 100%;
        }
    }
}

.schedule-accordion {

    > li {
        border-bottom: 1px solid $fog;

        > a {
            position: relative;
            display: block;
            padding: rem(32 76 32 256);
            font-size: rem(18);
            line-height: 1.278;
            font-weight: 500;
            text-decoration: none;

            @include breakpoints(none, s-large) {
                padding-left: rem(200);
            }

            @include breakpoints(none, s-medium) {
                padding: rem(20 40 20 0);
            }

            &:after {
                content: "\e90d";
                font: 13px/1 "icomoon";
                color: $poppy;
                position: absolute;
                right: 16px;
                top: 50%;
                transform: translateY(-50%);
                transition: transform 0.3s linear;

                @include breakpoints(none, s-medium) {
                    right: 0;
                    transform: none;
                    top: 26px;
                }
            }

            &.active:after {
                transform: translateY(-50%) rotate(180deg);
            }

            .time {
                position: absolute;
                left: 0;
                top: 0;
                padding: rem(32 0 32 16);
                color: $poppy;

                @include breakpoints(none, s-medium) {
                    display: block;
                    position: static;
                    padding: 0;
                    margin-bottom: rem(10);
                }
            }

            .schedule-title {
                display: block;
                color: $steel;
            }
        }
    }

    .slide-content {
        padding: rem(0 76 32 256);
        margin-top: rem(-14);

        @include breakpoints(none, s-large) {
            padding-left: rem(200);
        }

        @include breakpoints(none, s-medium) {
            padding: rem(0 0 30);
            margin-top: 0;
        }
    }
}

.se-company-list {
    padding: rem(90 0 108);
    font-size: rem(22);
    line-height: 1.36;

    @include breakpoints(none, s-medium) {
        padding: rem(50 0 80);
        font-size: rem(18);
    }

    h2 {
        font-size: rem(36);
        line-height: 1.167;
        padding-bottom: rem(24);

        @include breakpoints(none, s-medium) {
            font-size: rem(32);
        }
    }

    p {
        margin-bottom: rem(31);
    }

    .category-nav {
        margin-bottom: rem(57);
    }

    .list {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        margin: rem(-34 0);

        li {
            width: 25%;
            padding: rem(34 20);
            text-align: center;

            @include breakpoints(none, s-large) {
                width: 33.33%;
            }

            @include breakpoints(none, s-medium) {
                width: 50%;
            }
        }
    }
}

.se-venue-info {
    padding: rem(73 0);
    background: $bayside;
    color: $white;
    text-align: center;
    position: relative;

    .container {
        max-width: 700px;
        position: relative;
        z-index: 2;
    }

    .bg-img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;

        &:before {
            content: "";
            background: rgba(#131313, 0.3);
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 2;
        }
    }

    h2 {
        color: $white;
        font-size: rem(36);
        line-height: 1.167;
        margin-bottom: rem(31);

        @include breakpoints(none, s-medium) {
            font-size: rem(32);
            margin-bottom: rem(20);
        }
    }

    address {
        font-style: normal;
        font-size: rem(22);
        line-height: 1.54;
        letter-spacing: -0.01em;
        margin-bottom: rem(29);

        @include breakpoints(none, s-medium) {
            font-size: rem(18);
        }

        .place {
            display: block;
            font-weight: 700;
        }

        .address {
            display: block;
        }
    }

    .btn.btn-white {
        color: $steel;
        background: $white;

        &:hover {
            background: darken($white, 10);
        }
    }
}

.se-quote-slider-wrap {
    padding: rem(67 0 73);
    position: relative;
    background: $steel;
    color: $white;
    text-align: center;
    overflow: hidden;

    &:before,
    &:after {
        content: "";
        position: absolute;
        top: -200px;
        left: -160px;
        background: url('/wp-content/themes/a16z-2015/client/images/summit-event/se-illustration03.svg') no-repeat 0 0 / 375px 658px;
        width: 375px;
        height: 658px;

        @include breakpoints(none, s-medium) {
            background-size: 262px 460px;
            width: 262px;
            height: 460px;
            left: -120px;
        }
    }

    &:after {
        right: -315px;
        top: -369px;
        left: auto;
        background: url('/wp-content/themes/a16z-2015/client/images/summit-event/se-illustration04.svg') no-repeat 0 0 / 588px 868px;
        width: 588px;
        height: 868px;

        @include breakpoints(none, s-medium) {
            background-size: 412px 322px;
            width: 412px;
            height: 322px;
            right: -200px;
            top: -120px;
        }
    }

    .se-quote-slider {
        padding-bottom: rem(53);
        position: relative;
    }

    blockquote, q{
        margin:0;
        padding:0;
        border:0;
        outline:0;
        font-size:100%;
        vertical-align:baseline;
        background:transparent;
        quotes:none;
    }

    blockquote:before, blockquote:after,
    q:before, q:after {
        content:'';
        content:none;
    }

    blockquote {
        position: relative;
        padding-top: 54px;

        &:before {
            content: "\e93a";
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            font: 29px/1 "icomoon";
            color: $concrete;
        }

        q {
            display: block;
            font-size: rem(26);
            line-height: 1.307;
            margin-bottom: rem(16);

            @include breakpoints(none, s-medium) {
                font-size: rem(22);
            }
        }

        cite {
            display: inline-block;
            vertical-align: top;
            padding-left: 16px;
            position: relative;
            font-style: normal;
            font-size: rem(18);
            line-height: 1.278;
            font-weight: 700;

            &:before {
                content: "–";
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }

    .slick-dots {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 -4px;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;

        li {
            line-height: 1;
            padding: 0 4px;

            &.slick-active button,
            button:hover {
                background: $poppy;
            }
        }

        button {
            display: block;
            padding: 0;
            margin: 0;
            font-size: 0;
            line-height: 0;
            border: none;
            width: 24px;
            height: 3px;
            background: $concrete;
            transition: background 0.3s linear;
        }
    }
}

.speakers-summit {
    padding: rem(72) 0;

    @include breakpoints(none, s-medium) {
        padding: rem(50) 0;
    }

    .container {
        max-width: rem(960);
    }

    .heading {
        margin-bottom: rem(38);
        font-size: rem(22);
        line-height: 1.5;

        @include breakpoints(none, s-medium) {
            font-size: rem(18);
            margin-bottom: rem(30);
        }

        p {
            margin-bottom: rem(28);

            @include breakpoints(none, s-medium) {
                margin-bottom: rem(20);
            }
        }
    }

    h2 {
        font-size: rem(36);
        line-height: 1.25;
        margin-bottom: rem(23);

        @include breakpoints(none, s-medium) {
            margin-bottom: rem(10);
        }
    }
  }

  .category-nav {
    @extend %listreset;
    display: flex;
    flex-wrap: wrap;

    li {
        padding-right: rem(16);

        a  {
            font-size: rem(17);
            display: block;
            color: $fog;
            text-decoration: none;
            transition: color 0.3s linear;

            @include breakpoints(none, s-medium) {
                font-size: rem(16);
            }
        }

        &.active a,
        a:hover {
            color: $poppy;
        }
    }
  }

  .speaker-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 rem(-20);

    @include breakpoints(none, s-large) {
        margin: 0 rem(-15);
    }

    &.alt-layout {
        margin: 0 rem(-4);

        .speaker-list__item {
            padding: rem(4);
            margin: 0;
        }
    }

    &__item {
        width: 25%;
        padding: 0 rem(20);
        margin-bottom: rem(32);

        @include breakpoints(none, s-large) {
            width: 33.333%;
            padding: 0 rem(15);
        }

        @include breakpoints(none, s-medium) {
            width: 50%;
        }



        .speaker-inner {
            position: relative;
            overflow: hidden;

            &:hover {
                .rollover {
                    transform: translateY(0);
                    opacity: 1;
                }
            }

            .img {
                position: relative;
                margin: 0;

                img {
                    transform: scale(1) !important;
                }
            }

            .info {
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                padding: rem(25 16 16);
                background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,.5) 100%);

                @include breakpoints(none, s-medium) {
                    padding: rem(25 13 13);
                }
            }

            .designation {
                margin: 0;
                color: $white;
            }
        }

        .rollover {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: $poppy;
            color: $white;
            font-size: rem(16);
            line-height: 1.25;
            text-align: center;
            padding: rem(26 16);
            overflow: hidden;
            pointer-events: none;
            transform: translateY(100%);
            opacity: 0;
            transition: transform 0.2s cubic-bezier(.5,.23,0,.97), opacity 0.15s linear;

            @include breakpoints(none, s-medium) {
                font-size: rem(12);
                padding: rem(12 10);

            }

            @include breakpoints(none, 374px) {
                font-size: rem(10);
            }

            p {
                margin-bottom: rem(15);
            }

            .social {
                justify-content: center;
                pointer-events: auto;

                li {
                    padding: 0 rem(7);

                    a {
                        color: $white;

                        &:hover {
                            color: $white;
                        }
                    }
                }
            }
        }

        .img {
            margin-bottom: rem(16);
            text-decoration: none;
            display: block;
            overflow: hidden;

            @include breakpoints(none, s-medium) {
                margin-bottom: rem(12);
            }

            &:hover {
                img {
                    transform: scale(1.1);
                }
            }

            img {
                width: 100%;
                max-width: none;
                transition: transform 0.25s linear;
            }
        }

        .name {
            display: block;
            font-size: rem(20);
            line-height: 1.1;
            font-weight: 600;
            color: $poppy;
            text-decoration: none;
            margin-bottom: rem(4);

            @include breakpoints(none, s-medium) {
                font-size: rem(16);
            }
        }

        .designation {
            display: block;
            font-size: rem(16);
            line-height: 1.25;
            color: $concrete;
            margin-bottom: rem(9);

            @include breakpoints(none, s-medium) {
                font-size: rem(13);
            }
        }

        .social {
            @extend %listreset;
            display: flex;
            flex-wrap: wrap;
            font-size: rem(17);

            @include breakpoints(none, s-medium) {
                font-size: rem(14);
            }

            li {
                padding-right: rem(14);

                a {
                    color: #A0A3A9;
                    text-decoration: none;

                    &:hover {
                        color: $poppy;
                    }
                }
            }
        }
    }
}

.modal {
    .modal-speaker {
        max-width: rem(680);
        padding: rem(20 20 24);
        display: flex;
        flex-wrap: wrap;

        .close {
            right: rem(10);
            top: rem(10);
            color: $concrete;
            font-size: rem(15);

            &:hover {
               color: $poppy;
            }
        }

        .speaker-info {
            width: rem(200);
            margin-right: rem(20);

            @include breakpoints(none, s-medium) {
                width: 100%;
                margin: 0 0 rem(20);
            }

            .info {
                padding-left: rem(16);
                border-left: 3px solid $poppy;
                margin-bottom: rem(17);
            }

            .name {
                font-size: rem(24);
                line-height: 1.166;
                font-weight: 600;
                display: block;
                margin-right: rem(10);
                margin-bottom: rem(7);
            }

            .designation {
                font-size: rem(16);
                line-height: 1.37;
                display: block;
                margin-bottom: rem(13);
            }

            .social-link {
                @extend %listreset;
                font-size: rem(15);
                line-height: 1.37;
                font-weight: 600;

                li {
                    margin-bottom: rem(2);

                    a {
                        color: $fog;
                        text-decoration: none;
                        transition: color 0.3s linear;

                        &:hover {
                            color: $poppy;
                        }
                    }
                }
            }
        }

        .speaker-desc {
            flex: 1;
            font-size: rem(16);
            line-height: 1.375;

            h3 {
                font-size: rem(18);
                line-height: 1.22;
                font-weight: 600;
                margin-bottom: rem(4);
            }

            p {
                margin-bottom: rem(9);
            }

            .desc-foot {
                padding-top: rem(5);
            }
        }
    }
}
