/*
* American Dynamism page layout & content styles
*/

/* stylelint-disable */
/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */

body.page-template-page_template-american-dynamism-50,
body.page-template-page_template-american-dynamism,
body.page-template-page_template-american-dynamism-static {
    background: $marina;
    color: $white;

    &.sticky-aside-active {
        .header-new {
            opacity: 0;
            visibility: hidden;
        }
    }

    .site {
        @include breakpoints(s-large) {
            overflow: unset;

            /* @supports (clip-path: polygon(-1000% 0, 1000% 0, 1000% 100%, -1000% 100%)) {
                clip-path: polygon(-1000% 0, 1000% 0, 1000% 100%, -1000% 100%);
            } */
        }
    }

    @include breakpoints(none, s-large) {
        font-weight: 300;
    }

    .site-content__primary,
    .header-new {
        background: $marina;
        margin: 0;
    }

    .header-new .bg-header {
        background: $marina;
    }

    .header-new {
        z-index: 2;

        .container {
            max-width: 1452px;
            padding-left: 20px;
            padding-right: 20px;
        }

        .header-holder {

            @include breakpoints(s-medium) {
                padding: 42px 0;
            }
        }
    }

    .site-content {
        position: relative;
        z-index: 1;
    }

    .content-new {
        background: $marina;
        margin: 0;
        max-width: none;
        min-height: 1px;
        padding: 0;
        position: relative;

        > .container {
            padding: 0;
            max-width: none;
            margin: 0;
        }
    }

    .logo {
        color: $white;
    }

    .tagline {
        display: none;
    }

    .header-new .header-holder .search .icon-search,
    #nav > ul > li > a:not(.btn) {
        color: $white;

        &:hover {
            color: $poppy;
        }
    }

    .footer-new {
        background: #0057bf;
        margin: 0;
        color: $white;
    }

    .footer-new .footer-above,
    .footer-new .footer-above--mobile {
        color: #fff;
    }

    .footer-new .social-networks li a {
        background: $white;
    }

    .footer-nav ul li a {
        color: $white;

        &:hover {
            color: $poppy;
        }
    }

    .nav-opener span, .nav-opener::before, .nav-opener::after,
    .footer-nav ul li + li::before {
        background: $white;
    }

    .widget--subscribe-inline {
        padding: 20px 0;
        margin-top: -20px;
        border-bottom: 1px solid $white;
        border-top: 1px solid $white;
        margin-bottom: 50px;

        h3 {
            color: $white;
        }

        .thankyou-message h4{
            color: $citrus;
        }

        .mktoForm button[type="submit"] {
            background-color: $citrus !important;

            &:hover {
                background-color: darken($citrus, 20%) !important;
            }
        }
    }

    .mktoForm.subscription-form--v2 {
        position: relative;
        border-bottom: 2px solid $white;

        .mktoOffset {
            display: none !important;
        }

        @include breakpoints(none, s-medium) {
            padding: 0;
        }

        .mktoFormRow {
            width: 100%;

            &:nth-child(3) {
                display: none !important;
            }
        }

        /*input[type=text],
        input[type=email] {
            border-radius: 0;
            width: 100%;
            outline: none;
            color: $white;
            border: none !important;
            height: 48px !important;
            font-size: 32px !important;
            line-height: 1.444 !important;
            padding: 0 0 4px 0;
            font-weight: 300 !important;

            &::placeholder {
                color: white;
            }
        }*/

        input[type="email"] {
            width: 100%;
            height: 100%;
            outline: none;
            font-size: 36px !important;
            line-height: 1.444 !important;
            font-weight: 300 !important;
            background: none !important;
            border: none !important;
            color: $white;
            padding: 0 0 4px 0;
            border-radius: 0;
            font-family: font-family(sans-serif) !important;

            @include breakpoints(none, s-medium) {
                font-size: 26px;
            }

            &:focus {
                outline: none;
            }

            &::-webkit-input-placeholder {
                color: $white;
                opacity: 1;
            }

            &:-ms-input-placeholder {
                color: $white;
                opacity: 1;
            }

            &::placeholder {
                color: $white;
                opacity: 1;
            }
        }

        .input-active {
            .mktoLabel {
                bottom: 40px;
                font-size: 12px;
            }
        }

        .mktoLabel {
            font-size: 32px;
            color: white;
            font-weight: 300 !important;
            width: 100% !important;
            padding: 0;
            position: absolute;
            bottom: 0;
            left: 0;
            font-family: font-family(sans-serif) !important;
            pointer-events: none;
            line-height: 1.444;
            text-transform: lowercase;
            //@include animate(bottom font-size);
        }

        .mktoButtonRow {
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            width: 52px;
        }

        .mktoFieldDescriptor {
            margin-bottom: 0 !important;
        }

        .mktoField--input {
            border-bottom: 2px solid $white;
        }

        .mktoButtonWrap.mktoSimple {
            width: 100%;

            .mktoButton {
                position: absolute;
                right: 0;
                top: 0;
                bottom: 0;
                width: 32px;
                height: 52px;
                background: none !important;
                border: none !important;
                outline: none !important;
                padding: 0;

                img {
                    display: none;
                }

                &:before {
                    content: "";
                    background: url('/wp-content/themes/a16z-2015/client/images/amd/arrow-right.svg') no-repeat 0 0 / 26px 18px;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 26px;
                    height: 18px;
                }
            }
        }

        .mktoError  {
            position: absolute;
            right: auto !important;
            bottom: auto !important;
            top: 100%;
            left: 0;
            width: 100%;
            overflow: hidden;

            .mktoErrorArrowWrap {
                display: none;
            }

            .mktoErrorMsg {
                display: block;
                background: none;
                border: none;
                border-radius: 0;
                box-shadow: none;
                color: #E20E34;
                font-size: 1em;
                line-height: 1.2em;
                max-width: none;
                padding: 7px 0 0;
                text-shadow: none;
                margin: 0;

                .mktoErrorDetail {
                    display: inline;
                }
            }
        }

        input:-webkit-autofill {
            -webkit-background-clip: text;
            -webkit-text-fill-color: $white;
        }
    }


}

[class*="amd-"] {

    .container {
        max-width: 1452px;
        padding-left: 20px;
        padding-right: 20px;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
    }
}

.amd-hero {
    width: 100%;
    padding: 125px 0 71px;
    position: relative;
    color: $white;
    overflow: hidden;

    @include breakpoints(none, s-large) {
        padding: 70px 0;
    }

    @include breakpoints(none, s-medium) {
        padding: 27px 0;
    }

    .bg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-size: cover;
        background-position: 50% 50%;
        z-index: 1;
    }

    .container {
        position: relative;
        z-index: 2;
    }

    h1 {
        color: $white;
        font: 700 100px/1 $adellemono;
        margin-bottom: -45px;
        position: relative;
        z-index: 2;

        @include breakpoints(none, sm-large) {
            font-size: 72px;
        }

        @include breakpoints(none, s-large) {
            font-size: 60px;
        }

        @include breakpoints(none, s-medium) {
            font-size: 25px;
            line-height: 31px;
            margin-bottom: -12px;
        }

        .js-letter {
            overflow: hidden;
            vertical-align: bottom;
        }

        .js-inner {
            display: block;
            transform: translate(0, 100%);
            will-change: transform;
        }
    }

    .amd-hero-img {
        position: relative;
        z-index: 1;
        padding: 0 46px 0 28px;
        width: 100%;

        @include breakpoints(none, s-medium) {
            padding: 0 20px;
        }

        img {
            width: 100%;
        }
    }
}

.amd-body {
    padding-bottom: 200px;
    position: relative;
    background: url('/wp-content/themes/a16z-2015/client/images/amd/bg-grad.png') repeat;

    @include breakpoints(none, s-large) {
        padding-bottom: 100px;
    }

    > * {
        position: relative;
        z-index: 2;
    }

    .sticky-aside-active & {
        background: rgba(0,0,0,0.4);
    }

    &:before {
        content: '';
        position: absolute;
        height: 450vh;
        right: 0;
        bottom: 0;
        left: 0;
        background: linear-gradient(180deg, rgba(2,87,191,0) 0%, rgba(2,87,191,1) 10%, rgba(2,87,191,1) 100%);
        //background: #0257bf;
        z-index: 0;
    }

    &:after {
        content: '';
        position: absolute;
        height: 200px;
        right: 0;
        bottom: 0;
        left: 0;
        background: linear-gradient(0deg, rgba(0,164,177,1) 0%, rgba(2,87,191,1) 100%);
        //background: #0257bf;
        z-index: 0;

        @include breakpoints(none, s-large) {
            height: 100px;
        }
    }
}

.amd-aside {
    width: 358px;
    padding: 0 20px 0 0;
    font-size: 12px;
    line-height: 1.667;
    font-weight: 300;

    @include breakpoints(none, sm-large) {
        width: 300px;
    }

    @include breakpoints(none, s-large) {
        width: 100%;
    }

    .txt-wrap {
        padding-right: 58px;
        text-transform: capitalize;
    }

    .funding {
        width: 150px;
        font-size: 16px;
        line-height: 1.5;
        letter-spacing: -0.005em;

        .data {
            display: flex;

            .sep {
                flex: 1;
                margin: 0 4px;
                position: relative;

                &:before {
                    content: "";
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    left: 0;
                    right: 0;
                    height: 1px;
                    border: 1px dashed $white;
                }
            }
        }
    }
}

.amd-sticky-aside {
    @include breakpoints(none, s-large) {
        position: fixed;
        top: 139px;
        left: 0;
        right: 0;
        z-index: 9999;
        background: #0057bf;
        padding: 36px 0 0 0;
        opacity: 0;
        visibility: hidden;
        transition: all 0.3s linear;
    }

    @include breakpoints(none, s-medium) {
        //top: 77px;
        top: 0;
    }

    .sticky-aside-active & {
        opacity: 1;
        visibility: visible;
    }

    .amd-rows {
        @include breakpoints(s-large) {
            position: sticky;
            top: 0;
        }

        @include breakpoints(none, s-large) {
            padding: 20px;
            max-height: calc(100vh - 265px);
            overflow: auto;
        }

        @include breakpoints(none, s-medium) {
            padding: 20px;
            max-height: calc(100vh - 169px);
            overflow: auto;
        }
    }

    .aside-inner {
        @include breakpoints(s-large) {
            //overflow-y: auto;
            //height: 100vh;
        }
    }

    .aside-inner-scroll {
        padding: 10px 0 6px;

        @include breakpoints(s-large) {
            padding: 20px 0 10px;
        }
    }

}

#announcement-bar ~ .site-content {

    .amd-sticky-aside {

        @include breakpoints(none, s-large) {
            top: 235px;
        }

        @include breakpoints(none, s-medium) {
            top: 139px;
        }
    }
}

body.sticky-aside-active {
    overflow: hidden;
}

.amd-toc-opener.toc-open {
    position: absolute;
    top: 0;
    left: 24px;
    z-index: 9999;
    transition: all 0.3s linear;

    .sticky-aside-active &{
        opacity: 0;
        visibility: hidden;
    }
}

.amd-toc-opener.toc-close {
    position: absolute;
    top: 100%;
    left: 24px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s linear;

    .sticky-aside-active &{
        opacity: 1;
        visibility: visible;
    }
}

.amd-toc-opener {

    @include breakpoints(s-large) {
        display: none;
    }
}

.amd-content {
    width: calc(100% - 360px);

    @include breakpoints(none, sm-large) {
        width: calc(100% - 300px);
    }

    @include breakpoints(none, s-large) {
        width: 100%;
    }

    h3 {
        font: 700 50px/1.44 $adellemono;
        color: $citrus;
        margin-bottom: 10px;

        @include breakpoints(none, s-large) {
            font-size: 44px;
        }

        @include breakpoints(none, s-medium) {
            font-size: 30px;
        }
    }

    .people {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 6px;

        span {
            display: inline-block;
            vertical-align: middle;
            width: 19px;
            margin: 0 4px 4px 0;
        }
    }
}

.amd-intro {
    padding: 102px 0 0;
    font-size: 22px;
    line-height: 1.2727;
    letter-spacing: -0.005em;

    @include breakpoints(none, sm-large) {
        font-size: 20px;
    }

    @include breakpoints(none, s-large) {
        padding-top: 70px;
    }

    @include breakpoints(none, s-medium) {
        font-size: 18px;
        padding-top: 30px;
    }

    > .container {
        position: relative;
        justify-content: flex-end;
    }

    .heading {
        font: 300 36px/1 $adellemono;
        margin-bottom: 35px;
        letter-spacing: -0.6px;

        @include breakpoints(none, sm-large) {
            font-size: 30px;
        }

        @include breakpoints(none, s-large) {
            font-size: 26px;
        }

        @include breakpoints(none, s-medium) {
            font-size: 24px;
            line-height: 28px;
        }
    }

    h2 {
        font: 700 72px/1 $adellemono;
        color: $white;
        letter-spacing: -0.025em;
        margin-bottom: 10px;

        @include breakpoints(none, sm-large) {
            font-size: 60px;
        }

        @include breakpoints(none, s-large) {
            font-size: 54px;
            margin-bottom: 30px;
        }

        @include breakpoints(none, s-medium) {
            font-size: 47px;
            line-height: 1.255;
        }
    }

    .desc {
        max-width: 960px;

        a {
            color: $white;
            text-decoration: underline;
            transition: color 0.3s linear;

            &:hover {
                color: $citrus;
            }
        }
    }

    p {

        + p {
            margin-top: 14px;
        }
    }

    .intro-aside {
        width: 350px;
        position: absolute;
        left: 20px;
        top: 0;
        padding-top: 0;
        font-size: 12px;
        line-height: 20px;
        font-weight: 300;

        @include breakpoints(none, s-large) {
            position: static;
            width: 100%;
            border: solid $white;
            border-width: 1px 0;
            padding: 12px 0;
            font-weight: 300;
            margin-bottom: 28px;
        }

        .txt-wrap {
            max-width: 295px;
            text-transform: capitalize;

            @include breakpoints(none, s-large) {
                max-width: none;
            }
        }
    }
}

.link_to_government_employees {
    color: #0057bf;
    text-decoration: none;
    transition: color 0.3s linear;

    &:hover {
        color: $citrus;
    }
}

.amd-share {
    display: inline-block;
    vertical-align: top;
    position: relative;
    margin-bottom: 45px;

    @include breakpoints(none, s-medium) {
        margin-bottom: 37px;
    }

    &:hover {

        .share-opener:after {
            transform: translateY(-50%) rotate(180deg);;
        }

        .social-networks {
            opacity: 1;
            visibility: visible;
            transform: none;
        }
    }

    .share-opener {
        display: block;
        background: #0057bf;
        padding: 15px 50px 15px 21px;
        position: relative;
        color: $white;
        font-size: 16px;
        line-height: 1;
        letter-spacing: -0.5px;
        text-transform: uppercase;
        font-weight: 700;
        text-decoration: none;

        &:after {
            content: "";
            background: url('/wp-content/themes/a16z-2015/client/images/amd/arrow-down-a.svg') no-repeat 0 0 / 20px 20px;
            width: 20px;
            height: 20px;
            position: absolute;
            right: 14px;
            top: 50%;
            transform: translateY(-50%);
            transition: transform 0.3s linear;
        }
    }

    .social-networks {
        background: #fff;
        font-size: 18px;
        padding: 10px 0;
        position: absolute;
        left: 0;
        top: 100%;
        width: 187px;
        visibility: hidden;
        opacity: 0;
        border: 1px solid #0057bf;
        transform: translateY(5px);
        transition: visibility .3s ease-in-out,opacity .3s ease-in-out,transform .3s ease-in-out;

        li {

            a {
                display: flex;
                padding: 6px 10px 6px 21px;
                align-items: center;
                color: #0057bf;
                text-decoration: none;
                transition: color 0.3s linear;

                &:hover {
                    color: $citrus;
                }

                span {
                    margin-right: 8px;
                }
            }
        }
    }
}

@keyframes pin-glow {
    0% {
        transform: translate3d(-50%, -50%, 0) scale(0.1) ;
        opacity: 0;
    }
    50% {
        transform: translate3d(-50%, -50%, 0) scale(1) ;
        opacity: 0.7;
    }
    100% {
        transform: translate3d(-50%, -50%, 0) scale(0.1);
        opacity: 0;
    }
}

.amd-map {
    padding: 56px 0;

    .map-image {
        position: relative;
        overflow: hidden;
        padding-top: 65.8%;
        width: 100%;

        svg {
            transition: transform 0.3s ease-out;
            transform-origin: 4.75% 35%;
            width: 100%;
            position: absolute;
            height: 100%;
            left: 0;
            top: 0;

            path {
                transition: stroke-width 0.3s ease-out;
            }
        }
    }

    .container {
        max-width: 1602px;

        @include breakpoints(none, small) {
            padding: 0;
        }
    }

    .map-holder {
        position: relative;
        width: 100%;

        @include breakpoints(none, small) {
            width: calc(100% + 45px);
            margin: 0 -20px 0 -30px;
        }
    }

    .show-pins {
        .pin {
            opacity: 1;

            &:before {
                animation: pin-glow 3.5s infinite linear;
                animation-delay: var(--delay);
            }
        }
    }

    .ad-map--zoomed {
        .map-image {
            svg {
                transform: scale(10);
                path {
                    stroke-width: 0.3px;
                }
            }
        }

        .pin {
            &:not(.is-ba) {
                pointer-events: none;
                opacity: 0;
                transition-delay: unset;
            }

            &.is-ba {
                opacity: 1;
                transition-delay: 0.3s;
                pointer-events: auto;
            }
        }

        .btn-search {
            .img-zoom-in {
                display: none;
            }

            .img-zoom-out {
                display: block;
            }
        }
    }

    .pin {
        position: absolute;
        width: 13px;
        height: 13px;
        border-radius: 50%;
        background: $citrus;
        cursor: pointer;
        opacity: 0;
        transition: opacity 0.5s ease-out var(--delay2);

        @include breakpoints(none, small) {
            width: 6px;
            height: 6px;
        }

        &:before {
            pointer-events: none;
            content: "";
            width: 30px;
            height: 30px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) scale(0.1);
            transform-origin: 50% 50%;
            background: $citrus;
            opacity: 0;
            filter: blur(4px);
            border-radius: 50%;

            @include breakpoints(none, small) {
                width: 14px;
                height: 14px;
            }
        }

        &.is-ba {
            pointer-events: none;
            opacity: 0;
            transition-delay: unset;
            width: 26px;
            height: 26px;
            margin-left: -7px;
            margin-top: -7px;

            @include breakpoints(none, small) {
                width: 13px;
                height: 13px;
                margin-left: -3px;
                margin-top: -3px;
            }

            &:before {
                width: 60px;
                height: 60px;

                @include breakpoints(none, small) {
                    width: 28px;
                    height: 28px;
                }
            }
        }

        &:hover {
            &:before {
                opacity: 0 !important;
                transform: translate3d(-50%, -50%, 0) scale(0.1) !important;
                animation-play-state: paused;
            }
        }
    }

    .info-box {
        background: $fog;
        box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 10px;
        padding: 20px 32px;
        position: absolute;
        color: $black;
        opacity: 0;
        z-index: 2;
        visibility: hidden;
        transition: opacity 0.3s ease-out, visibility 0.3s ease-out;

        &.is-active {
            opacity: 1;
            visibility: visible;
        }

        .list {
            @extend %listreset;
            margin: -6px 0;

            &.horizontal {
                column-count: 2;
                column-gap: 40px;
            }

            li {
                padding: 6px 0;
                -webkit-column-break-inside: avoid;
                page-break-inside: avoid;
                break-inside: avoid-column;
                display:table;
            }

            strong {
                display: block;
            }

            span {
                display: block;
            }
        }
    }

    .btn-search {
        left: 7%;
        top: 34%;
        width: 36px;
        height: 36px;
        border-radius: 50%;
        background: $white;
        overflow: hidden;
        position: absolute;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;

        > img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 20px;
            height: 20px;
        }

        .img-zoom-out {
            display: none;
        }
    }
}

.amd-filter-content {
    padding-top: 88px;

    @include breakpoints(none, s-medium) {
        padding-top: 40px;
    }

    > .container {
        justify-content: flex-end;
        position: relative;
    }
}

.amd-row {

    + .amd-row {
        margin-top: 75px;

        @include breakpoints(none, s-medium) {
            margin-top: 35px;
        }

        @media screen and (max-height: 935px) {
