.amd-filter-list {
    font-weight: 600;
    font-size: 18px;
    line-height: 1.778;
    letter-spacing: -0.005em;

    li {

        + li {
            margin-top: 12px;

            @media screen and (max-height: 935px) {
                margin-top: 4px;
            }
        }

        a {
            display: inline-block;
            color: $white;
            text-decoration: none;
            transition: color 0.3s linear;
            position: relative;

            &:before {
                content: "";
                background: rgba(0, 0, 0, 0.2);
                display: block;
                position: absolute;
                top: -3px;
                left: -10px;
                right: -10px;
                bottom: -3px;
                border-radius: 6px;
                opacity: 0;
                pointer-events: none;
                transition: opacity 0.3s ease-out;

                @media screen and (max-height: 935px) {
                    top: -1px;
                    bottom: -1px;
                    left: -5px;
                    right: -5px;
                }
            }

            &.back {
                font-weight: 800;
            }

            &:hover {
                color: $citrus;
            }

            &.active {
                color: $citrus;

                &:before {
                    opacity: 1;
                }
            }
        }
    }
}

.amd-key-list {
