}

.amd-content-row {
    margin-bottom: 80px;

    @include breakpoints(none, s-medium) {
        margin-bottom: 40px;
    }

    &.hidden {
        display: none;
    }

    .img-wrap {
        position: relative;
        margin-bottom: 36px;
        padding: 45px 56px 38px 76px;

        @include breakpoints(none, s-medium) {
            padding: 20px;
        }

        [class*="decor-"] {
            position: absolute;
            z-index: 1;

            @include breakpoints(none, s-medium) {
                width: 72px;
                height: 72px;
            }
        }

        .decor-tl {
            top: 7px;
            left: 38px;

            @include breakpoints(none, s-medium) {
                left: 0;
                top: 0;
            }
        }

        .decor-br {
            bottom: 0;
            right: 19px;

            @include breakpoints(none, s-medium) {
                right: 7px;
                bottom: 7px;
            }
        }

        .decor-tr {
            top: 7px;
            right: 18px;

            @include breakpoints(none, s-medium) {
                right: 7px;
            }
        }

        .decor-bl {
            left: 38px;
            bottom: 0;

            @include breakpoints(none, s-medium) {
                left: 0;
            }
        }

        .image--16-9 {
            position: relative;
            width: 100%;
            background-image: linear-gradient(135deg,#f5f7fa75 0,#c3cfe275 100%);

            &:before {
                content: '';
                display: block;
                padding-top: 56.25%;
            }

            > img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .img {
            position: relative;
            z-index: 2;
        }

        .c-logo {
            position: absolute;
            bottom: 45px;
            right: 40px;

            @include breakpoints(none, s-medium) {
                bottom: 15px;
                right: 15px;
                transform: scale(0.65);
                transform-origin: 100% 100%;
            }

            &.tr {
                bottom: auto;
                top: 45px;
                transform-origin: 100% 0;
                @include breakpoints(none, s-medium) {
                    top: 15px;
                }
            }

            &.lw {
                bottom: 20px;

                @include breakpoints(none, s-medium) {
                    bottom: 10px;
                }
            }

            &.full-img {
                img {
                    width: 100%;
                }
            }
        }
    }

    .info-list {
        margin-bottom: 40px;

        li {
            font-size: 18px;
            line-height: 1.333;
            letter-spacing: -0.005em;

            &.mission {
                max-width: 446px;
            }

            &.mission-v2 {
                max-width: 613px;
            }

            + li {
                margin-top: 12px;
            }

            strong {
                font-weight: 700;
                margin-right: 6px;
                display: inline-block;
                vertical-align: middle;
            }

            .people {
                display: inline-block;
                vertical-align: middle;
                letter-spacing: -5px;
                margin: 0;

                span {
                    margin: 2px 4px 2px 0;
                }
            }
        }

        .funding-scale {
            width: 156px;
            height: 17px;
            display: inline-block;
            vertical-align: middle;
            position: relative;

            .indicator {
                width: 2px;
                height: 22px;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                background: $white;
            }
        }
    }

    .desc {
        font-size: 18px;
        line-height: 1.333;
        letter-spacing: -0.005em;
        max-width: 960px;

        @include breakpoints(none, s-medium) {
