    }

    .container {
        max-width: 976px;
    }

    .header {
        margin-bottom: 40px;
    }

    h4 {
        color: $white;
        font-weight: 800;
        font-size: 48px;
        line-height: 1.222;
        letter-spacing: -0.5px;
        margin-bottom: 17px;

        @include breakpoints(none, s-large) {
