        }

        @include breakpoints(none, s-medium) {
            font-size: 36px;
        }
    }

    .subscription-form {
        width: 100%;
    }

    .input-wrap {
        border-bottom: 2px solid $white;
        height: 52px;
        position: relative;
        padding-right: 40px;

        input[type="email"] {
            width: 100%;
            height: 100%;
            font-size: 36px;
            line-height: 1.444;
            font-weight: 300;
            background: none;
            border: none;
            color: $white;

            @include breakpoints(none, s-medium) {
                font-size: 26px;
            }

            &:focus {
                outline: none;
            }

            &::-webkit-input-placeholder {
              color: $white;
              opacity: 1;
            }

            &:-ms-input-placeholder {
              color: $white;
              opacity: 1;
            }

            &::placeholder {
              color: $white;
              opacity: 1;
            }
        }

        button[type="submit"] {
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            width: 32px;
            height: 52px;
            background: none;
