/*
* Creator Stack page layout & content styles
*/

/* stylelint-disable selector-max-specificity */

body.page-template-page_template-clf-creator-stack {

    .header-new {
        display: none;
    }

    .content-new {
        padding: 0;
        max-width: none;

        > .container {
            margin: 0;
            padding: 0;
            max-width: none;
        }
    }

    .site-content__primary,
    .archive-content--author {
        margin-bottom: 0;
    }

    #main {
        margin: 0;
    }

    .site-footer-container {
        background: $cable_car;

        .site-footer {
            padding-bottom: 3rem;
            padding-top: 3rem;
        }

        .site-footer__social-media-menu {
            .menu a {
                background-color: $white;
            }
        }

        .site-footer__copyright {
            color: $white;
        }

        .site-footer__utilities-menu {

            li,
            a {
                color: $white;
            }
        }
    }

    .container {
        max-width: 1426px;
        padding-left: 15px;
        padding-right: 15px;
        margin: 0 auto;
    }
}

.clf-page {
    position: relative;
}

#clf-header {
    position: absolute;
    z-index: 9;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding: 40px 0;

    @include breakpoints(none, s-medium) {
        padding: 15px 0;
    }

    .container {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
    }

    .logo-wrap {
        display: inline-flex;
        flex-wrap: wrap;
        align-items: flex-end;
        color: $white;

        @include breakpoints(none, s-medium) {
            width: 100%;
        }

    }

    .logo {
        width: 234px;
        margin-right: 15px;

        @include breakpoints(none, s-medium) {
            width: 196px;
            margin: 0 auto 9px;
        }

    }

    .tagline {
        font-size: 21px;
        line-height: 1;

        @include breakpoints(none, s-medium) {
            width: 100%;
            font-size: 16px;
            text-align: center;
            display: block;
        }
    }

    .crown {
        width: 42px;
        height: 42px;
        border-radius: 50%;
        background: $cable_car;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
        transition: background 0.25s linear;

        @include breakpoints(none, s-medium) {
            position: absolute;
            top: 10px;
            right: 10px;
            width: 30px;
            height: 30px;
            padding: 7px;
        }

        .icon {
            filter: brightness(0) invert(1);
        }

        &:hover {
            background: $white;

            .icon {
                filter: unset;
            }
        }
    }
}

.clf-hero {
    background: $cable_car;
    padding: 250px 0 134px;
    color: $white;
    overflow: hidden;
    position: relative;

    @include breakpoints(none, s-medium) {
        padding: 138px 0 78px;
    }

    .content-wrap {
        max-width: 654px;
        font-size: 30px;
        line-height: 1.13;
        letter-spacing: -0.5px;
        position: relative;
        z-index: 1;

        @include breakpoints(none, s-large) {
            font-size: 24px;
        }

        @include breakpoints(none, s-medium) {
            font-size: 20px;
        }
    }

    h1 {
        font-size: 60px;
        line-height: 1.2;
        font-weight: 700;
        color: $white;
        letter-spacing: -0.015em;
        margin-bottom: 10px;

        @include breakpoints(none, s-large) {
            font-size: 50px;
        }

        @include breakpoints(none, s-medium) {
            font-size: 40px;
        }
    }

    .hero-img {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        width: 50%;

        @include breakpoints(none, s-large) {
            opacity: 0.5;
        }

        .clf-hero-img {
            /* stylelint-disable declaration-no-important */
            right: -16px !important;
            left: -16px !important;
            top: -18px !important;
            bottom: -18px !important;
            position: absolute !important;
            /* stylelint-enable declaration-no-important */

            img {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: 0 50%;
            }
        }
    }

    .hero-img-parallax {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        transform-origin: 50% 50%;
    }
}

.clf-content {
    padding: 54px 0 85px;

    @include breakpoints(none, s-large) {
        padding: 20px 0 50px;
    }

    .intro-txt {
        font-size: 26px;
        line-height: 1.307;
        letter-spacing: -0.5px;
        color: $steel;
        padding-right: 70px;
        margin-bottom: 50px;

        @include breakpoints(none, s-large) {
            font-size: 22px;
            margin-bottom: 40px;
        }

        @include breakpoints(none, s-medium) {
            font-size: 18px;
            padding-right: 40px;
            margin-bottom: 30px;
        }
    }

    .open-close {
        border-bottom: 1px solid $concrete;

        .opener {
            color: $grapevine;
            font-size: 36px;
            line-height: 1.222;
            font-weight: 800;
            letter-spacing: -0.5px;
            text-decoration: none;
            display: block;
            position: relative;
            padding: 40px 36px 40px 0;

            @include breakpoints(none, s-large) {
                font-size: 32px;
            }

            @include breakpoints(none, s-medium) {
                font-size: 22px;
                padding: 25px 36px 25px 0;
            }

            &.active {
                &::after {
                    transform: rotate(-180deg);
                }
            }

            &::after {
                content: '';
                position: absolute;
                right: 0;
                top: 58px;
                width: 25px;
                height: 15px;
                background-position: contain;
                background-repeat: no-repeat;
                background-image: url('data:image/svg+xml;base64,');

                @include breakpoints(none, s-large) {
                    top: 52px;
                }

                @include breakpoints(none, s-medium) {
                    top: 34px;
                    width: 18px;
                    height: 11px;
                }
            }
        }

        .slide {
            margin-top: -36px;
            padding-bottom: 23px;

            @include breakpoints(none, s-medium) {
                margin-top: -25px;
            }

            @include breakpoints(none, s-medium) {
                margin-top: -10px;
                padding-bottom: 0;
            }
        }
    }

    .media {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 20px;

        @include breakpoints(none, s-medium) {
            margin-bottom: 40px;
        }

        .media-img {
            width: 450px;
            height: 255px;
            margin-right: 73px;
            position: relative;
            overflow: hidden;
            perspective: 50em;
            transition: all 0.35s ease;
            display: block;

            @include breakpoints(none, large) {
                width: 330px;
                margin-right: 30px;
            }

            @include breakpoints(none, s-medium) {
                width: 100%;
                max-width: 450px;
                margin-bottom: 30px;
                margin-right: 0;
            }

            &:hover {
                .wrap {
                    transform: rotate3d(-1, 1, 0, 100deg);
                    opacity: 0;
                }

                .bg-img-rollover {
                    opacity: 1;
                    transform: rotate3d(0, 0, 0, 0deg);
                    transition-delay: 0.35s * 0.4;
                }
            }

            .wrap {
                padding: 50px;
                display: flex;
                height: 100%;
                width: 100%;
                align-items: center;
                justify-content: center;
                backface-visibility: hidden;
                transition: opacity 0.35s ease, transform 0.35s ease;

                .logo {
                    transition: none;
                    display: block;
                    width: auto;
                    max-height: 100px;
                }
            }

            .bg-img,
            .bg-img-rollover {
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;
                width: 100%;
                max-width: none;
                object-fit: cover;
                object-position: center;
            }

            .bg-img-rollover {
                opacity: 0;
                transform: rotate3d(1, -1, 0, 100deg);
                transition: opacity 0.35s ease, transform 0.35s ease;
            }

            .bg-img {
                filter: grayscale(100%);
                opacity: 0.12;
            }
        }

        .media-body {
            flex: 1;
            font-size: 24px;
            line-height: 1.219;
            color: $steel;

            @include breakpoints(none, s-medium) {
                font-size: 20px;
            }

            @include breakpoints(none, s-medium) {
                flex: initial;
                width: 100%;
                font-size: 18px;
            }

            h3 {
                font-size: 32px;
                line-height: 1.218;
                color: $steel;
                letter-spacing: -0.5px;
                font-weight: 800;
                margin-bottom: 10px;

                @include breakpoints(none, s-large) {
                    font-size: 28px;
                }

                @include breakpoints(none, s-medium) {
                    font-size: 24px;
                }
            }

            p {
                margin-bottom: 20px;

                @include breakpoints(none, s-medium) {
                    margin-bottom: 15px;
                }
            }

            .url {
                color: $marina;
                text-decoration: none;
                position: relative;
                display: inline-block;
                vertical-align: top;

                &:hover {
                    &::after {
                        width: 0;
                        right: 0;
                        left: auto;
                    }
                }

                &::after {
                    content: '';
                    position: absolute;
                    width: 100%;
                    left: 0;
                    bottom: 3px;
                    height: 0.5px;
                    background: $marina;
                    transition: width 0.25s linear;
                }
            }
        }
    }
}

.clf-testimonial {
    padding: 133px 0;
    position: relative;
    z-index: 1;
    background: $grapevine;
    color: $white;

    @include breakpoints(none, s-medium) {
        padding: 80px 0;
    }

    .bg-img {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: -1;
        overflow: hidden;
        background-size: cover;
        background-position: center;
        background-blend-mode: multiply;
    }

    .blockquote {
        margin: 0;
        text-align: center;

        .icon {
            width: 50px;
            color: $marina;
            margin: 0 auto 24px;

            img {
                width: 100%;
            }
        }

        q {
            display: block;
            font-size: 36px;
            line-height: 1.222;
            font-weight: 700;
            letter-spacing: -1px;
            margin-bottom: 41px;

            @include breakpoints(none, s-medium) {
                font-size: 24px;
                letter-spacing: -0.5px;
                margin-bottom: 30px;
            }
        }

        cite {
            display: block;
            font-style: normal;
            font-size: 28px;
            line-height: 1.215;
            font-weight: 700;

            @include breakpoints(none, s-medium) {
                font-size: 20px;
            }
        }
    }

    .slick-slide {
        height: auto;
    }

    .slick-dots {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding-top: 40px;

        li {
            padding: 0 5px;

            &.slick-active {
                button {
                    background: $marina;
                }
            }
        }

        button {
            border: none;
            outline: none;
            box-shadow: none;
            border-radius: 50%;
            background: $white;
            width: 8px;
            height: 8px;
            font-size: 0;
            color: transparent;
            display: block;
            padding: 0;
            margin: 0;
            transition: background 0.25s linear;

            &:hover {
                background: $marina;
            }
        }
    }
}
