/*
 * Team page layout & content styles
 */

$thumbnail-width-mobile: calc((100% / 2) - (#{rem(20)} * (1 / 2)));
$thumbnail-width-tablet: calc((100% / 4) - (#{rem(20)} * (3 / 4)));
$thumbnail-width-desktop: calc((100% / 6) - (#{rem(20)} * (5 / 6)));

.page-template-page_template-team {

	.categories-list__list {
		flex-flow: row wrap;
	}
}

.page-content--team {
	@include vertical-rhythym(spacer(4), spacer(4), $border-light);
}

.team-section__header {
	margin-bottom: spacer(2);
}

.team-section__subheader {
  margin-top: spacer(3);
  margin-bottom: spacer(2);
}

.team-section__title {
	@include section-title();
}

.team-section__subtitle {
  @include section-subheader();

}

.team-section__list {
	display: flex;
	flex-flow: row wrap;
	justify-content: left;
}

.team-member {
	position: relative;
	flex: 0 0 auto;
	width: $thumbnail-width-mobile;
	margin-right: rem(20);
	margin-bottom: rem(40);

	@include breakpoints(none, small) {

		&:nth-child(2n) {
			margin-right: 0;
		}

		&:nth-last-child(-n+2) {
			margin-bottom: 0;
		}
	}

	@include breakpoints(small, large) {
		width: $thumbnail-width-tablet;

		&:nth-child(4n) {
			margin-right: 0;
		}

		&:nth-last-child(-n+4) {
			margin-bottom: 0;
		}
	}

	@include breakpoints(large) {
		width: $thumbnail-width-desktop;

		&:nth-child(6n) {
			margin-right: 0;
		}

		&:nth-last-child(-n+6) {
			margin-bottom: 0;
		}
	}
}

.team-member__thumbnail-link,
.team-member__link {
	@include link-hover();
}

.team-member__thumbnail-link,
.team-member__thumbnail {
	display: block;
	width: 100%;
}

.team-member__thumbnail-link {
	background: color(gray-light);
	margin-bottom: rem(10);
	overflow: hidden;
	padding-top: 100%;
	position: relative;
}

.team-member__thumbnail {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	object-fit: cover;
}

.team-member__name {
	font-size: rem(18);
	font-weight: 500;
}
