/* stylelint-disable selector-max-specificity */
.page-template-page_template-landing-podcasts,
.page-template-page_template-landings {
    @extend .page-template-page_template-crypto-startup-school-demo;

    #footer {
        background: #F2F2F2;
        margin-top: 0;
    }

    .container {
        margin-bottom: 0;
    }
}

.bio-lp-intro {
    padding: 55px 0;

    @include breakpoints(none, s-medium) {
        padding: 40px 0 30px;
    }

    p {
        margin-bottom: 16px;
    }

    &__container {
        max-width: 1118px;
        padding: 0 15px;
        margin: 0 auto;
        display: flex;

        @include breakpoints(none, s-medium) {
            display: block;
        }
    }

    &__media {
        width: 416px;
        margin-right: 69px;

        @include breakpoints(none, s-large) {
            width: 300px;
            margin-right: 50px;
        }

        @include breakpoints(none, s-medium) {
            margin-right: 0;
            margin-bottom: 10px;
            width: 300px;
        }
    }

    &__desc {
        flex: 1;
        color: #4F4F4F;
        font-size: 16px;
        line-height: 1.61;
        padding-top: 9px;

        @include breakpoints(none, s-medium) {
            font-size: 14px;
        }

        p {
          a {
            color: #4F4F4F;

            &:hover {
              color: $poppy;
            }
          }
        }
    }

    &__heading {
        font-size: 26px;
        line-height: 1.1;
        margin-bottom: 18px;

        @include breakpoints(none, s-medium) {
            font-size: 18px;
        }

        h1 {
            margin-bottom: 0;
            font-size: 50px;
            color: $steel;

            @include breakpoints(none, s-medium) {
                font-size: 30px;
            }
        }

        p {
            font-weight: 300;
        }
    }

    &__apps {
        @extend %listreset;
        display: flex;
        flex-wrap: wrap;
        margin: 0 -10px;

        li {
            width: 165px;
            margin: 0 10px 20px;
            flex: 1 0 auto;

            @include breakpoints(none, s-large) {
                width: calc(33.333% - 10px);
                margin: 0 5px 10px;
            }
        }

      .btn {
        display: flex;
        align-items: center;
        justify-content: center;
      }

      a {
        width: 100%;
        height: 100%;
        border-radius: 6px;
        display: block;
      }
    }
}

.bio-lp-latest-ep {
    padding: 67px 0 60px;

    @include breakpoints(none, s-medium) {
        padding: 40px 0;
    }

    h2 {
        margin-bottom: 31px;
        font-size: 24px;
        color: #333;
    }

    p {
        margin-bottom: 16px;
    }

    &__container {
        max-width: 1118px;
        padding: 0 15px;
        margin: 0 auto;
    }

    &__slider {
        margin: 0 -16px;

        @include breakpoints(none, m-large) {
            padding-top: 30px;
        }

		@include breakpoints(s-large) {
			.slick-track {
				display: flex;
				margin-left: inherit;
			}

			.slick-slide {
				height: auto;
				float: none;

				> div {
					height: 100%;
				}
			}

			.bio-lp-latest-ep__post {
				height: 100%;
				position: relative;
				padding-bottom: 40px;
			}

			.lp-btn {
				position: absolute;
				left: 16px;
				bottom: 0;
			}
		}

        .slick-arrow {
            border: none;
            background: none;
            font-size: 0;
            line-height: 1;
            padding: 0;
            box-shadow: none;
            outline: none;
            width: 12px;
            height: 26px;
            background-image: url('data:image/svg+xml;base64,');
            background-repeat: no-repeat;
            background-position: center;
            position: absolute;
            top: 50%;
            margin-top: -10px;
            cursor: pointer;

            @include breakpoints(none, m-large) {
                top: 0;
            }

            &.slick-prev {
                left: -5%;
                transform: rotate(180deg);

                @include breakpoints(none, m-large) {
                    left: 15px;
                }
            }

            &.slick-next {
                right: -5%;

                @include breakpoints(none, m-large) {
                    left: 50px;
                    right: auto;
                }
            }

            &.slick-disabled {
                opacity: 0.1;
            }
        }
    }

    &__post {
        padding: 0 16px;
        font-size: 14px;
        line-height: 1.61;
        color: #4F4F4F;

        .time {
            color: #828282;
            font-size: 13px;
            display: block;
            margin-bottom: 6px;
        }

        h3 {
            font-size: 20px;
            line-height: 1.2;
            color: $black;
            font-weight: 500;
            margin-bottom: 14px;

          a {
            color: $black;
            text-decoration: none;

            &:hover {
              color: $poppy;
            }
          }
        }

        p {
            margin-bottom: 15px;

          a {
            color: #4F4F4F;
            text-decoration: none;

            &:hover {
              color: #4F4F4F;
            }
          }
        }
    }
}

.lp-btn-icon__play {
    text-decoration: none;
    color: #333;
    transition: opacity 0.25s linear;
    outline: none;

    &:hover {
        opacity: 0.8;
    }

    .icon__play {
        background-image: url('data:image/svg+xml;base64,');
        width: 30px;
        height: 30px;
        background-repeat: no-repeat;
        display: inline-block;
        vertical-align: middle;
        margin-right: 5px;
    }

    span {
        display: inline-block;
        vertical-align: middle;
        font-size: 14px;
        font-weight: 500;
    }
}

.bio-lp-subscribe {
    padding: 64px 0 55px;

    @include breakpoints(none, s-medium) {
        padding: 40px 0;
    }

    h2 {
        margin-bottom: 25px;
        font-size: 24px;
        color: #333;
        text-align: center;
    }

    p {
        margin-bottom: 16px;
    }

    &__container {
        max-width: 1118px;
        padding: 0 15px;
        margin: 0 auto;
    }

    &__apps--wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    &__apps {
        @extend %listreset;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0 -15px;
        max-width: 1070px;
		width: 100%;

        @include breakpoints(none, s-medium) {
            margin: 0 -5px;
        }

        li {
            display: flex;
            margin: 0 0 20px;
            padding: 0 15px;
            max-width: 25%;
            width: 25%;
            flex: 1 0 25%;

            @include breakpoints(none, s-large) {
                width: 33.333%;
				max-width: 33.333%;
                margin: 0;
                padding: 0 5px;
                flex: 1 0 33.333%;
				margin-bottom: 15px;
            }

            @include breakpoints(none, small) {
                width: 50%;
                flex: 1 0 50%;
				max-width: 50%;
            }

            a {
				position: relative;
              	width: 100%;
              	height: 0;
              	border-radius: 8px;
              	display: block;
				overflow: hidden;
				padding: 0 0 24.35% 0;

				img {
					display: block;
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top: 0;
				}

				span {
					display: flex;
					align-items: center;
					justify-content: center;
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top: 0;
				}
            }
        }
    }
}

.bio-lp-reviews {
    padding: 75px 0 40px;

    @include breakpoints(none, s-medium) {
        padding: 40px 0 20px;
    }

    h2 {
        margin-bottom: 25px;
        font-size: 24px;
        color: #333;
    }

    p {
        margin-bottom: 16px;
    }

    &__container {
        max-width: 1118px;
        padding: 0 15px;
        margin: 0 auto;
    }

    &-list {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -16px;
    }

    &-card {
        width: 50%;
        padding: 0 16px;
        margin-bottom: 30px;
        color: #4F4F4F;
        display: flex;

        @include breakpoints(none, s-medium) {
            width: 100%;
        }

        .inner {
            background: $white;
            padding: 27px 31px 33px;
            width: 100%;
        }

        .rating {
            @extend %listreset;
            margin-bottom: 10px;
        }

        .icon__twitter {
            background-image: url('data:image/svg+xml;base64,');
            width: 15px;
            height: 12px;
            background-repeat: no-repeat;
            display: inline-block;
            vertical-align: middle;
            display: block;
            margin-bottom: 17px;
        }

        .icon__star {
            background-image: url('data:image/svg+xml;base64,');
            width: 13px;
            height: 13px;
            background-repeat: no-repeat;
            display: inline-block;
            vertical-align: middle;
        }

        .quote {
            font-size: 16px;
            line-height: 1.6;
            margin-bottom: 22px;
        }

        .cite {
            font-size: 14px;
            font-weight: 600;
        }
    }
}

.bio-lp-discover {
    padding: 63px 0;

    @include breakpoints(none, s-medium) {
        padding: 40px 0;
    }

    h2 {
        margin-bottom: 25px;
        font-size: 24px;
        color: #333;
    }

    p {
        margin-bottom: 16px;
    }

    &__container {
        max-width: 1118px;
        padding: 0 15px;
        margin: 0 auto;
    }

    &-cards {
        @extend %listreset;
        display: flex;
        flex-wrap: wrap;
        margin: 0 -16px;

        li {
          padding: 0 16px;
          margin-bottom: 32px;
          width: 100%;

          @include breakpoints(small) {
            width: 50%;
          }

          @include breakpoints(medium) {
            width: 33.33%;
          }
        }

        a {
          position: relative;
          display: block;
          width: 100%;
          height: 0;
          padding-bottom: 100%;

          span {
            display: block;
            position: absolute;
            left: 0;
            width: 100%;
            top: 0;
            height: 100%;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: cover;
          }
        }
    }
}

.bio-lp-newsletter {
    padding: 100px 0 70px;

    @include breakpoints(none, s-medium) {
        padding: 60px 0 0;
    }

    &__thanks-msg {
        color: rgba(38, 39, 41, 0.8);
        text-align: center;

        h2 {
            color: rgba(38, 39, 41, 0.8);
            font-size: rem(36);
            margin-bottom: rem(24);
            font-weight: 600;
            line-height: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: relative;
            align-items: flex-start;
            padding-left: 65px;
            padding-top: 20px;

            @include breakpoints(none, s-medium) {
                font-size: rem(28);
                margin-bottom: rem(15);
            }

            &::before {
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                width: 45px;
                height: 45px;
                background: url("data:image/svg+xml,%3Csvg width='46' height='46' viewBox='0 0 46 46' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M35.6254 24.3987C36.4397 28.2706 35.9178 32.3041 34.145 35.8412C32.3722 39.3784 29.453 42.2104 25.8638 43.8753C22.2746 45.5401 18.2272 45.9396 14.3817 45.0085C10.5363 44.0774 7.11986 41.8707 4.68977 38.7484C2.25969 35.626 0.959377 31.7724 1.00097 27.816C1.04256 23.8597 2.42359 20.0342 4.91878 16.9637C7.41396 13.8931 10.876 11.7587 14.7402 10.9087C18.6043 10.0586 22.6425 10.5431 26.1959 12.283' stroke='%23ED8B00'/%3E%3Cpath d='M8.5 26L17.5 35L45.5 1' stroke='white'/%3E%3C/svg%3E%0A") no-repeat 50% 50% / contain;
            }
        }

        p {
            padding-left: 40px;
        }
    }

    &__container {
        max-width: 1118px;
        padding: 0 15px;
        margin: 0 auto;
    }

    &__title {
        color: #262729;
        font-size: rem(36);
        font-weight: 600;
        line-height: 1;
        margin-bottom: rem(20);
        text-align: center;

        @include breakpoints(none, s-medium) {
            font-size: rem(28);
            margin-bottom: rem(15);
        }
    }

    &__text {
        color: rgba(38, 39, 41, 0.8);
        font-size: rem(24);
        font-weight: 300;
        line-height: 1.3;
        margin-bottom: rem(38);
        text-align: center;

        @include breakpoints(none, s-medium) {
            font-size: rem(16);
            margin-bottom: rem(30);
        }
    }

    &__form {
        align-items: center;
        display: flex;
        justify-content: center;

        input {
            color: red;
            font-size: rem(18);
            padding: rem(15);
            width: rem(370);
        }

        button {
            appearance: none;
            border: none;
            background-color: $steel;

            &:hover,
            &:focus {
                cursor: pointer;
            }
        }
    }

    &__submit {
        height: rem(55);
        width: rem(55);
    }
}

