  background: $white;
}

.background--secondary {
  background: #F2F2F2;
}

.bio-lp-reviews__icon {
  margin-bottom: 15px;
}

.bio-lp--share {
	display: flex;

	span {
		font-weight: 600;
		margin-right: rem(20);
	}

	.blog-social-networks {
		li {
			padding: 0 rem(8);

      a {
        color: $poppy;
        font-size: rem(16);
      }
		}
	}
}

.bio-lp-player {
  iframe {
    margin-top: 20px;
  }
}
