/*
* Growth Metics page layout & content styles
*/

/* stylelint-disable */
/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */

$ease-inout: cubic-bezier(.37, .01, 0, .98);

[class*="page-template-page_template-growth-metrics"] {

    #header {
        z-index: 9999;
    }

    #footer {
        display: none;
    }

    #content,
    main,
    .content-new,
    .content-new > .contanier,
    #main {
        height: calc(100vh - 112px);
        overflow: hidden;
        margin: 0;

        @include breakpoints(none, sm-large) {
            height: calc(100vh - 134px);
        }

        @include breakpoints(none, s-medium) {
            height: calc(100vh - 92px);
        }
    }

    #main {
        overflow: auto;
    }

    #announcement-bar {

        ~ #content {
            height: calc(100vh - 174px);
            overflow: hidden;
            margin: 0;

            @include breakpoints(none, sm-large) {
                height: calc(100vh - 196px);
            }

            @include breakpoints(none, s-large) {
                height: calc(100vh - 230px);
            }

            @include breakpoints(none, s-medium) {
                height: calc(100vh - 188px);
            }

            @include breakpoints(none, small) {
                height: calc(100vh - 154px);
            }

            main,
            .content-new,
            .content-new > .contanier,
            #main {
                height: calc(100vh - 174px);
                overflow: hidden;
                margin: 0;

                @include breakpoints(none, sm-large) {
                    height: calc(100vh - 196px);
                }

                @include breakpoints(none, s-large) {
                    height: calc(100vh - 230px);
                }

                @include breakpoints(none, s-medium) {
                    height: calc(100vh - 188px);
                }

                @include breakpoints(none, small) {
                    height: calc(100vh - 154px);
                }
            }

            #main {
                overflow: auto;
            }

            .growth-metrics .progress-bar {
                top: 174px;

                @include breakpoints(none, sm-large) {
                    top: 196px;
                }

                @include breakpoints(none, s-large) {
                    top: 230px;
                }

                @include breakpoints(none, s-medium) {
                    top: 188px;
                }

                @include breakpoints(none, small) {
                    top: 154px;
                }
            }
        }
    }

    .content-new {
        max-width: none;
        padding: 0;

        &:has(.post-password-form-wrapper) {
            background: #3c3e43;

            .post-password-form-wrapper {
                padding: unset;
            }
        }

        > .container {
            padding: 0;
            max-width: none;
            margin: 0;
        }
    }

    #main {
        overflow: auto;
    }
}

.growth-metrics {
    //position: absolute;
    //left: 0;
    //top: 0;
    //height: 100%;
    position: relative;

    color: #fff;
    text-align: center;
    display: flex;
    align-items: center;
    overflow: hidden;
    min-height: calc(100vh - 110px);

    .skip {
        position: absolute;
        right: 62px;
        bottom: 52px;
        font-weight: 500;
        font-size: 16px;
        line-height: 1;
        color: $concrete;
        transition: color 0.3s linear;
        z-index: 101;
        text-decoration: none;

        @include breakpoints(none, s-medium) {
            right: 25px;
        }

        &:hover {
            color: #d0d1db;
        }
    }
}

.gm-content-area {
    position: relative;
    z-index: 100;
    padding: 50px 0;
    width: 100%;
    font-size: 24px;
    line-height: 1.4167;
    transition: transform 0.5s $ease-inout;

    @include breakpoints(none, s-large) {
        font-size: 22px;
        line-height: 1.2;
    }

    @include breakpoints(none, s-medium) {
        font-size: 20px;
        padding: 50px 0;
    }

    .container {
        max-width: 980px;
        position: relative;

        @include breakpoints(none, s-medium) {
            padding-left: 10px;
            padding-right: 10px;
        }

        .interview-items {
            position: absolute;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: calc(100% - 40px);

            transform: translate3d(0, calc(-100% + 150px), 0);

            @include breakpoints(none, s-medium) {
                width: calc(100% - 20px);
            }
        }
    }

    p {
        font-size: 36px;
        font-weight: 500;
        line-height: 43.85px;
        letter-spacing: -0.5px;

        @include breakpoints(none, small) {
            font-size: 18px;
            line-height: 1.3;
        }
    }

    .vertical {
        font-size: 48px;
        line-height: 1.083;
        font-weight: 700;
        letter-spacing: -0.5px;
        color: $white;
        margin: 0;
        position: relative;
        transition: all 0.5s $ease-inout;

        @include breakpoints(none, s-medium) {
            font-size: 25px;
            line-height: 1.2;
        }

        &.style-1 {

            @include breakpoints(none, s-medium) {
                font-size: 36px;
                line-height: 1.222;
            }
        }

        + .vertical {
            margin-top: 40px;

            @include breakpoints(none, s-medium) {
                margin-top: 35px;
            }
        }

        &.selected {
            //font-size: 38px;
            //line-height: 1.368;
            //font-weight: 600;
            color: #5f636a;

            transform: scale(.75);

            @include breakpoints(none, s-medium) {
                //font-size: 25px;
            }

            .filled {
                color: #a0a3a9;

                &:after {
                    height: 3px;
                    background: #a0a3a9;
                    bottom: 2px;
                    right: 0;

                    @include breakpoints(none, s-medium) {
                        bottom: 1px;
                    }
                }
            }

            .pending {
                opacity: 0;
            }
        }

        &.style-1 {

            .blank {
                max-width: 352px;

                @include breakpoints(none, s-large) {
                    display: block;
                    max-width: 352px;
                    min-width: 1px;
                    margin: 121px auto 51px;
                }

                @include breakpoints(none, s-medium) {
                    max-width: 256px;
                }
            }
        }

        .pending {
            display: block;
            position: absolute;
            font-size: 18px;
            font-weight: 600;
            letter-spacing: -0.5px;
            text-align: center;
            margin: 0 auto;
            bottom: -35px;
            width: 100%;
            color: $white;
            transition: opacity 0.5s $ease-inout;

            @include breakpoints(none, s-medium) {
                bottom: -30px;
                font-size: 12px;
            }

            &.hide {
                opacity: 0;
            }
        }

        .top-info {
            position: absolute;
            font-size: 18px;
            font-weight: 600;
            letter-spacing: -0.5px;
            text-align: center;
            margin: 0 auto;
            bottom: -35px;
            width: 100%;

            @include breakpoints(none, s-medium) {
                bottom: -30px;
                font-size: 12px;
            }
        }
    }

    .blank {
        display: inline-block;
        vertical-align: bottom;
        min-width: 266px;
        height: 3px;
        background: #fff;
        margin-bottom: 3px;
        cursor: default;
        position: relative;

        @include breakpoints(none, s-large) {
            min-width: 110px;
        }
    }

    .filled {
        position: relative;
        display: inline-block;
        color: #93ca15;

        &:after {
            content: "";
            position: absolute;
            bottom: 1px;
            left: 0;
            right: 0;
            height: 4px;
            background: #93ca15;

            @include breakpoints(none, s-medium) {
                height: 3px;
            }
        }
    }

    .tooltip {
        position: absolute;
        bottom: 100%;
        right: -2px;
        margin-bottom: -11px;
        cursor: pointer;

        @include breakpoints(none, s-medium) {
            right: -6px;
            margin-bottom: -8px;
        }

        i {
            display: block;
            font-weight: 800;
            font-size: 8.5px;
            line-height: 1.17;
            letter-spacing: 2.5px;
            width: 13px;
            height: 13px;
            background: $concrete;
            border-radius: 50%;
            color: $steel;
            padding: 3px 5.5px;
            font-style: normal;

            @include breakpoints(none, s-medium) {
                width: 10px;
                height: 10px;
                padding: 1px 4px;
            }
        }

        .tooltip-content {
            position: absolute;
            bottom: 100%;
            left: 50%;
            transform: translateX(-50%);
            margin-bottom: 15px;
            //background: #3c3e43;
            //background: $concrete;
            background: #3C3E43;
            border-radius: 6px;
            padding: 30px;
            font-weight: 600;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: -0.5px;
            color: $fog;
            white-space: nowrap;
            transition: opacity 0.3s linear;

            @include breakpoints(none, s-medium) {
                white-space: normal;
                min-width: 200px;
                padding: 15px;
            }

            &:after {
                content: "";
                top: calc(100% - 2px);
                width: 0;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                height: 0;
                border-style: solid;
                border-width: 10px 10px 0 10px;
                border-color: #3C3E43 transparent transparent transparent;
            }
        }
    }

    .edit {
        position: absolute;
        top: 100%;
        right: 0;
        font-weight: 400;
        font-size: 20px;
        line-height: 1;
        letter-spacing: -0.015em;
        text-decoration: none;
        margin: 4px 8px 0;

        @include breakpoints(none, s-medium) {
            font-size: 12px;
            line-height: 1;
            margin-right: 0;
        }

        a {
            text-decoration: none;
            color: $concrete;
            transition: all 0.3s linear;

            &:hover {
                color: #d0d1db;
            }
        }
    }

    .btns-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: -8px;
        padding-top: 55px;

        &.hide {
            opacity: 0;
            //transition: opacity 0.3s ease-out;
        }

        @include breakpoints(none, s-medium) {
            //flex-wrap: nowrap;
            flex-direction: column;
            width: 100%;
            padding: 52px 18px 0;
        }

        &.intro-btns {
            @include breakpoints(none, s-1366) {
                padding-top: 40px;
            }
        }

        li {
            padding: 8px;
            display: flex;
            align-items: center;

            @include breakpoints(none, s-medium) {
                width: 100%;
            }
        }

        &.padding-lg {

            @include breakpoints(none, s-medium) {
                padding-top: 111px;
            }
        }
    }

    .button-tooltip {
        position: absolute;

        font-style: normal;
        font-weight: 800;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        letter-spacing: 2.5px;
        color: #d0d1db;
        pointer-events: none;

        z-index: 100;
        opacity: 0;
        transition: opacity 0.3s ease-out;

        &.show {
            opacity: 1;
        }
    }

    .button {
        background: $steel;
        color: $concrete;
        border: 1px solid $steel;
        padding: 15px;
        min-width: 250px;
        text-align: center;
        display: inline-block;
        vertical-align: top;
        text-decoration: none;
        transition: all 0.3s linear;
        position: relative;
        font-weight: 800;
        font-size: 16px;
        line-height: 1.1875;
        letter-spacing: 2.5px;

        @include breakpoints(none, s-medium) {
            min-width: 1px;
            width: 100%;
        }

        &.button-style-1 {
            font-weight: 700;
            letter-spacing: 0.5px;
            padding: 12px 20px;
            font-size: 17px;
        }

        &.button-blue {
            background: $bayside;
            border-color: $bayside;
            color: $white;

            &:hover {
                background: #0057bf;
                border-color: #0057bf;
            }
        }

        &.is-hover-blue {

            &:hover {
                background: $deep-ocean;
                border-color: $deep-ocean;
                color: $white;
            }
        }

        &.is-hover-green {
            &.hover,
            &:hover {
                background: $avocado;
                border-color: $avocado;
                color: $white;
            }
        }

        &.is-pending {

            &:hover {
                border-color: $steel;
                background: transparent;
                color: $concrete;

                .warn-txt {
                    opacity: 1;
                    visibility: visible;
                }
            }

            //&:focus {
            //    background: $steel;
            //    color: $concrete;
            //    border: 1px solid $steel;
            //}
        }

        .warn-txt {
            pointer-events: none;
            position: absolute;
            bottom: -59px;
            right: -38px;
            font-size: 16px;
            line-height: 1.1875;
            letter-spacing: 2.5px;
            color: $fog;
            opacity: 0;
            visibility: hidden;
            transition: all 0.3s linear;

            @include breakpoints(none, s-medium) {
                right: 6px;
                bottom: -30px;
            }
        }
    }

    .logo-wrap {
        max-width: 256px;
        margin: 0 auto 110px;

        @include breakpoints(none, s-large) {
            max-width: 200px;
            margin-bottom: 60px;
        }

        @include breakpoints(none, s-medium) {
            max-width: 166px;
            margin-bottom: 48px;
        }

        @include breakpoints(none, s-1366) {
            margin-bottom: 60px;
        }
    }

    h1 {
        color: $white;
        font-weight: 700;
        font-size: 72px;
        line-height: 1;
        letter-spacing: -0.015em;
        margin-bottom: 38px;

        @include breakpoints(none, s-large) {
            font-size: 56px;
        }

        @include breakpoints(none, s-medium) {
            font-size: 40px;
            line-height: 1.04;
            margin-bottom: 32px;
        }

        @include breakpoints(none, s-1366) {
            margin-bottom: 20px;
        }
    }
}

.gm-calculating {
    width: 290px;
    height: 250px;
    margin: 50px auto;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 36px;
    line-height: 1.222;
    letter-spacing: -0.5px;
    color: $white;
    font-weight: 700;
    top: 50%;
    position: absolute;
    z-index: 100;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);

    &.lg {
        width: 600px;
        height: 390px;
    }

    .calc-bg {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        right: 0;

        svg {
            width: 100%;
        }
    }

    span {
        position: absolute;
        top: 46px;
        z-index: 2;
    }
}

.gm-pagination {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 138px;
    z-index: 100;

    @include breakpoints(none, sm-large) {
        right: 20px;
    }

    @include breakpoints(none, s-medium) {
        display: none;
    }

    li {
        padding: 4px 0;

        &.active a,
        a:hover {
            border-color: $bayside;
            background: $bayside;
        }

        a {
            display: block;
            width: 15px;
            height: 15px;
            border: 1px solid $steel;
            border-radius: 50%;
            transition: all 0.3s linear;
        }
    }
}

.background-wrapper {
    .bg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;
        height: 100%;

        svg {
            width: 100%;
            height: 100%;
        }
    }
}

.growth-metrics-app {
    position: relative;

    &:not(.is-result) {
        overflow: hidden;
        height: 100vh;
    }
}

.growth-metrics-background {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    background: #3c3e43;
    transition: top 0.5s 0.25s cubic-bezier(0.37, 0.01, 0, 0.98), opacity 0.5s 0.25s cubic-bezier(0.37, 0.01, 0, 0.98), background-color 2.5s cubic-bezier(0.37, 0.01, 0, 0.98);

    .bg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;
        height: 100%;

        svg {
            width: 100%;
            height: 100%;
        }
    }

    &.lp {
        background: url('/wp-content/themes/a16z-2015/client/images/growth-metrics/bg-lp.png') no-repeat 50% 50% / cover;
    }

    path {
        transition: stroke 2.5s cubic-bezier(0.37, 0.01, 0, 0.98);
    }

    &.bg-green {
        background: #93ca15;

        path {
            stroke: #83A33F !important;
        }
    }

    &.bg-blue {
        background: $bayside;

        path {
            stroke: #39A9E5 !important;
        }
    }

    &.hide {
        opacity: 0;
        top: -100%;
    }

    .compass {
        z-index: 2;
        mix-blend-mode: overlay;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        max-width: 1428px;
        margin: 0 auto;
        left: 0;
        right: 0;

        @include breakpoints(none, large) {
            left: -100px;
            right: -100px;
        }

        @include breakpoints(none, medium) {
            left: -200px;
            right: -200px;
        }

        @include breakpoints(none, small) {
            left: -300px;
            right: -300px;
        }

        &.compass-mobile {
            @include breakpoints(none, large) {
                left: -100px;
                right: -100px;
                width: calc(100% + 200px);
            }

            @include breakpoints(none, medium) {
                left: -150px;
                right: -150px;
                width: calc(100% + 300px);
            }

            @include breakpoints(none, small) {
                width: calc(100% + 400px);
                left: -200px;
                right: -200px;
            }
        }

        svg {
            //max-width: 100%;
        }
    }

    .compass-mobile {
        width: 100%;
        position: relative;
        will-change: transform;

        &:before {
            content: '';
            padding-top: 100%;
            display: block;
            position: relative;
        }

        div {
            will-change: transform;
            backface-visibility: hidden;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: no-repeat center center / contain;
            z-index: 1;
        }

        .compass-center {
            background-image: url('/wp-content/themes/a16z-2015/client/images/growth-metrics/compass-center.png');
        }

        .compass-numbers {
            background-image: url('/wp-content/themes/a16z-2015/client/images/growth-metrics/compass-numbers.png');
        }

        .compass-ring-a {
            background-image: url('/wp-content/themes/a16z-2015/client/images/growth-metrics/compass-ring-a.png');
        }

        .compass-ring-b {
            background-image: url('/wp-content/themes/a16z-2015/client/images/growth-metrics/compass-ring-b.png');
        }

        .compass-ring-c {
            background-image: url('/wp-content/themes/a16z-2015/client/images/growth-metrics/compass-ring-c.png');
        }
    }

    .lights-wrapper {
        z-index: 3;
        mix-blend-mode: overlay;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        right: 0;
        max-width: 1428px;
        margin: 0 auto;
        opacity: 0;

        .light {
            width: 798px;
            height: 798px;
            z-index: 4;
            position: absolute;

            &.blue {
                background: radial-gradient(33.67% 33.67% at 50% 50%, rgba(0, 87, 191, 0.7) 0%, rgba(8, 31, 60, 0) 100%);
                transform: translate3D(0, -50%, 0);
                right: 0;
            }

            &.green {
                background: radial-gradient(33.67% 33.67% at 50% 50%, rgba(147, 202, 21, 0.7) 0%, rgba(147, 202, 21, 0) 100%);
                transform: translate3D(0, -50%, 0);
            }
        }
    }
}

.growth-interview-progress-bar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    width: 100%;
    z-index: 4;

    span {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        background: $bayside;
        transition: width 0.5s ease-in-out;
    }
}
