/*
* Digital Health Builders page layout & content styles
*/

/* stylelint-disable */
/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */

body.page-template-page_template-digital-health-builders {
	@extend .page-template-page_template-crypto-startup-school-demo;
	color: $steel;
	line-height: 1.625;

	&.header-player-mini-show {

		> .site {
			margin-top: 52px;

			#header {
				top: 52px;
			}
		}

		&.admin-bar {
			> .site {
				margin-top: 20px;

				#header {
					top: 20px;
				}
			}
		}

	}

	.content-new {
		padding: 0;
		margin: 0;
		max-width: none;

		> .container {
			margin: 0;
			max-width: none;
			padding: 0;
		}
	}

	.site-content__primary,
	.archive-content--author {
		margin-bottom: 0;
	}

	#main {
		margin-bottom: rem(50);

		@include breakpoints(large) {
			padding-top: rem(16);
			margin-bottom: 0;
		}
	}

	.footer-new {
		margin-top: rem(16);
	}

	h2 {
		color: $steel;
		font-size: 40px;
		line-height: 1;
		margin-bottom: 24px;

		@include breakpoints(none, s-medium) {
			font-size: 30px;
		}
	}

	.dhb-latest {

		h2 {
			margin-bottom: 40px;
		}
	}

	.header-new {
		z-index: 9998;
		background: none;
	}

	#footer {
		background: #f2f2f2;
	}

	.footer-new,
	.footer-new .copyright,
	.footer-nav ul li a,
	.footer-new .social-networks li a {
		color: $white;
	}

	.footer-nav ul li a:hover,
	.footer-new .social-networks li a:hover {
		color: $poppy;
	}

	.footer-nav ul li + li::before {
		background: $white;
	}

	#footer {
		background: $bayside;
		margin-top: 0;
		padding: 32px 0 50px;
	}

	.site-footer__social-media-menu a[href*="simplecast.com"],
	.site-footer__social-media-menu a[href*="twitter.com"] {
		background: $white;
		transition: background 0.3s linear;

		&:hover {
			background: $poppy;
		}
	}

	.site-content__primary {
		margin-bottom: 0;
	}

	.input-addon .mktoFormRow .mktoFieldWrap .mktoEmailField {
		color: $steel;

		&::placeholder {
			color: #a4a6a8;
			opacity: 1;
		}
	}

	.input-addon .mktoButtonRow .mktoButtonWrap button.mktoButton {
		background: $avocado !important;
		transition: background 0.3s linear;
	}

	.input-addon .mktoButtonRow .mktoButtonWrap button.mktoButton:hover {
		background: darken($avocado, 10%) !important;
	}

	.summit-event-newsletter {
		background: $bayside;
		padding: 98px 0 66px;
		color: $white;

		@include breakpoints(none, s-medium) {
			padding: 60px 0 30px;
		}

		h2 {
			color: $white !important;
			font-size: 36px;
			line-height: 1;
			font-weight: 600;
			letter-spacing: -0.005em;
			margin-bottom: 17px;

			@include breakpoints(none, s-medium) {
				font-size: 32px;
			}
		}

		.summit-event-newsletter__text {
			letter-spacing: -0.035em;
			font-weight: 400;

			@include breakpoints(s-large) {
				font-size: 24px;
			}
		}

		.input-addon {
			max-width: 332px;
			height: 44px;

			.mktoButtonRow {
				width: 95px;
				height: 44px;

				.mktoButtonWrap {
					button.mktoButton {
						width: 95px;
						height: 44px;
					}
				}
			}
		}
	}

	.link-load-more,
	.load-more__button {
		color: $avocado;
		text-decoration: none;
		display: inline-block;
		vertical-align: middle;
		font-size: 16px;
		font-weight: 700;
		text-transform: uppercase;
		border: none;
		width: auto;
		margin: 0;
		padding: 0;

			&:hover {
				&::after {
					transform: translateY(0);
				}
			}

			&::after {
				content: "\e902";
				font-size: 16px;
				line-height: 1;
				font-family: "icomoon";
				margin-left: 8px;
				display: inline-block;
				vertical-align: middle;
				transition: transform 0.3s ease-in;
				transform: translateY(-2px);
			}
		}

	.load-more {
		padding-top: 26px;
	}
}

[class*="dhb-"] {

	.container {
		max-width: 1354px;
		padding-left: 20px;
		padding-right: 20px;
	}
}

.dhb-banner {
	background: $bayside;
	position: relative;
	margin-top: -112px;
	padding: 168px 0 138px;
	color: $white;
	text-align: center;
	overflow: hidden;

	@include breakpoints(none, m-large) {
		margin-top: -134px;
	}

	@include breakpoints(none, s-medium) {
		margin-top: -92px;
		padding: 120px 0 80px;
	}

	&::before,
	&::after {
		content: '';
		position: absolute;
		left: 0;
		top: -7px;
		background: url('/wp-content/themes/a16z-2015/client/images/digital-health-builder/dh-pattern1.svg') no-repeat 0 0 / 357px 372px;
		width: 357px;
		height: 372px;
		z-index: 1;

		@include breakpoints(none, m-large) {
			background-size: 250px 260px;
			width: 250px;
			height: 260px;
		}

		@include breakpoints(none, s-medium) {
			background-size: 150px 160px;
			width: 150px;
			height: 160px;
		}
	}

	&::after {
		background-image: url('/wp-content/themes/a16z-2015/client/images/digital-health-builder/dh-pattern2.svg');
		background-size: 373px 367px;
		width: 373px;
		height: 367px;
		left: auto;
		top: -2px;
		right: 0;
		z-index: 1;

		@include breakpoints(none, m-large) {
			background-size: 250px 256px;
			width: 250px;
			height: 256px;
		}

		@include breakpoints(none, s-medium) {
			background-size: 150px 160px;
			width: 150px;
			height: 160px;
		}
	}

	.container {
		position: relative;
		z-index: 2;
	}

	h1 {
		font-size: 60px;
		line-height: 0.9;
		font-weight: 800;
		letter-spacing: -0.015em;

		@include breakpoints(none, s-large) {
			font-size: 50px;
		}

		@include breakpoints(none, s-medium) {
			font-size: 40px;
		}

        .navbar-color-light & {
            color: $white;
        }
	}

	&.dhb-banner-img {
		&::after,
		&::before {
			background: none;
		}
	}
}

.dhb-intro {
	padding: 72px 0;
	background: $white;
	text-align: center;

	@include breakpoints(none, s-medium) {
		padding: 50px 0;
	}

	.container {
		max-width: 904px;
	}

	p {
		margin-bottom: 24px;
	}

	.img-wrap {
		max-width: 750px;
		margin: 0 auto;
		padding-top: 40px;
	}
}

.dhb-upcoming {
	background: $white;
	padding: 72px 0;

	@include breakpoints(none, s-medium) {
		padding: 50px 0;
	}

	.top-row {
		margin-bottom: 40px;

		h2 {
			margin-bottom: 17px;
		}

		p {
			margin-bottom: 24px;
		}
	}

	.ch-border-list {
		border-bottom: 1px solid $fog;

		> li {
			padding: 16px 0;
			position: relative;

			&.hidden {
				display: none;
			}

			&:hover {
				z-index: 100;
			}
		}
	}

	.ch-upcoming-item {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		.time {
			width: 96px;
			height: 96px;

			@include breakpoints(none, s-medium) {
				width: 80px;
				height: 86px;
			}

			>  strong {
				color: $marina;
			}
		}
	}

	.left-col {
		flex: 1;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		width: calc(100% - 180px);
		padding-right: 20px;

		@include breakpoints(none, s-medium) {
			width: 100%;
			padding: 0 0 15px;
		}
	}

	.right-col {

		@include breakpoints(none, s-medium) {
			width: 100%;
		}
	}

	.video-wrap {
		width: 96px;
		margin-right: 24px;
		position: relative;

		@include breakpoints(none, s-medium) {
			width: 80px;
		}

		&:before {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: rgba($black, 0.4);
			z-index: 1;
		}

		img {
			width: 100%;
		}

		&:hover {

			.dh-play {
				background: darken($avocado, 15%);
			}
		}

		.dh-play {
			width: 32px;
			height: 32px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);

			&:before {
				background-size: 10px 12px;
				width: 10px;
				height: 12px;
				margin-left: 2px;
			}
		}
	}

	.activities-nav {

		ul {

			@include breakpoints(none, s-medium) {
				left: 0;
				right: auto;
			}
		}
	}
}

.dh-play {
	width: 24px;
	height: 24px;
	border-radius: 50%;
	background: $avocado;
	display: inline-block;
	vertical-align: middle;
	position: relative;
	z-index: 2;
	transition: background 0.3s linear;

	&:before {
		content: "";
		background: url('/wp-content/themes/a16z-2015/client/images/digital-health-builder/icon-play.svg') no-repeat 0 0 / 8px 10px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 8px;
		height: 10px;
		margin-left: 1px;
	}

	&:hover {
		background: darken($avocado, 15%);
	}
}

.dhb-link-wrap {
	padding-top: 26px;
}

.add-to-calendar {
	display: inline-block;
	vertical-align: top;
	background: #f6f9ee;
	padding: 11px 18px 11px 48px;
	position: relative;
	color: $steel;
	font-size: 16px;
	line-height: 1.375;
	letter-spacing: -0.005em;
	font-weight: 500;
	position: relative;
	text-decoration: none;
	transition: background 0.3s linear;

	&:hover {
		background: darken(#f6f9ee, 15%);
	}

	&:after {
		content: "";
		background: url('/wp-content/themes/a16z-2015/client/images/digital-health-builder/icon-calendar.svg') no-repeat 0 0 / 16px 18px;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 16px;
		height: 18px;
		left: 20px;
	}
}

.dhb-latest {
	background: $gray9;
	padding: 72px 0;

	h2 {
		margin-bottom: 40px;
	}

	.latest-list {
		@extend %listreset;
		border-top: 1px solid $fog;

		& + .latest-list {
			border-top: none;
		}

		li {
			border-bottom: 1px solid $fog;

			a {
				padding: 20px 0;
				display: block;
				text-decoration: none;
				color: $steel;
				transition: color 0.25s linear;

				&:hover {
					color: $avocado;
				}

				strong {
					display: inline-flex;
					flex-wrap: wrap;
					align-items: center;
					font-size: 22px;
					line-height: 1.1818;
					font-weight: 800;
					letter-spacing: 0.2px;
					margin-bottom: 5px;

					@include breakpoints(none, s-medium) {
						font-size: 20px;
					}

					.c-tag {
						margin: -2px 0 0 8px;

						img {
							vertical-align: middle;
						}
					}
				}

				span {
					display: block;
					font-size: 16px;
					line-height: 1.625;
					color: $concrete;
				}
			}
		}
	}
}

.dhb-media-section {
	background: $gray9;
	padding: 16px 0 66px;

	.dhb-media {
		padding: 56px 0 32px;
		border-bottom: 1px solid $fog;

		&.hidden {
			display: none;
		}

		@include breakpoints(none, s-medium) {
			padding: 40px 0 20px;
		}
	}

	.video-row {
		display: flex;
		align-items: center;
		margin-bottom: 40px;

		@include breakpoints(none, s-large) {
			align-items: flex-start;
		}

		@include breakpoints(none, s-medium) {
			display: block;
		}

		.video {
			flex: 1;
			padding-right: 60px;

			@include breakpoints(none, s-large) {
				padding-right: 30px;
			}

			@include breakpoints(none, s-medium) {
				padding-right: 0;
				margin-bottom: 25px;
				padding-right: 0;
			}

			&, a {
				position: relative;
				display: block;
				text-decoration: none;

				&:hover {
					.play-icon {
						background: $black;
					}
				}
			}

			.play-icon {
				background: rgba(0, 0, 0, 0.5);
				border: 3.6px solid $white;
				color: $white;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 60px;
				height: 60px;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				border-radius: 50%;
				transition: background 0.25s linear;

				> i {
					font-size: 20px;
					line-height: 1;
					margin-left: 2px;
				}
			}
		}

		.desc {
			width: 500px;
			color: $steel;
			font-size: 16px;
			line-height: 1.625;

			@include breakpoints(none, s-large) {
				width: 50%;
			}

			@include breakpoints(none, s-medium) {
				width: 100%;
			}

            a {
                color: $avocado;

                &:hover {
                    color: darken($avocado, 15%);
                }
            }
		}

		h2 {
			@include breakpoints(none, s-large) {
				font-size: 30px;
				margin-bottom: 15px;
			}
		}
	}

	.more-interviews {

		h3 {
			font-size: 22px;
			line-height: 1.18;
			margin-bottom: 24px;
		}
	}

	.interview-list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -16px;

		li {
			width: 20%;
			padding: 0 16px;
			margin-bottom: 22px;

			@include breakpoints(none, sm-large) {
				width: 25%;
			}

			@include breakpoints(none, s-large) {
				width: 33.333%;
			}

			@include breakpoints(none, s-medium) {
				width: 50%;
			}

			@include breakpoints(none, marketomobilebkpt) {
				width: 100%;
			}

			> strong {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				font-weight: 700;
				font-size: 20px;
				line-height: 1.2;
				margin-bottom: 6px;
				padding-left: 0;

				@include breakpoints(none, s-medium) {
					font-size: 18px;
				}

				a {
					color: $avocado;
					text-decoration: none;
					transition: color 0.25s linear;
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					position: relative;
					padding-left: 32px;

					&:hover {
						color: darken($avocado, 15%);

						.dh-play {
							background: darken($avocado, 15%);
						}
					}
				}

				.dh-play {
					position: absolute;
					top: 0;
					left: 0;
				}
			}

			> span {
				display: block;
				font-size: 16px;
				line-height: 1.375;
				color: $concrete;
				margin-bottom: 2px;
			}
		}
	}

	.link-hold {
		padding-top: 35px;
	}
}
