/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */
.page-template-page_template-social-strikes-back-lp {
  @import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');

  @extend .page-template-page_template-crypto-startup-school-demo;

  .content-new {
    background-color: $black;
    overflow: hidden;

    > .container {
      margin-bottom: 0;
    }
  }

  #header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background: none;

    .container {
      max-width: 1440px;
    }
  }

  .site-content__primary,
  .archive-content--author {
    margin-bottom: 0;
  }

  .footer-new {
    background: $black;
    color: $white;
    margin-top: 0;

    .site-footer__social-media-menu {
      .menu a {
        background-color: $white;
      }
    }

    .footer-nav ul li a {
      color: $white;

      &:hover {
        opacity: 0.7;
      }
    }
  }
}

.ssb-hero {
  background: $deep_ocean;
  position: relative;
  overflow: hidden;

  .hero-mobile {
    @media (min-width: 768px) {
      display: none;
    }
  }

  img {
    object-fit: cover;
    object-position: center;
    max-height: 825px;
    width: 100%;
    vertical-align: top;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 38px;
    background: url("/wp-content/themes/a16z-2015/client/images/ssb/ssb_frame.png") repeat-x 50% 100% / 42px 38px;
    z-index: 1;

    @media (max-width: 767px) {
      content: none;
    }

    @media (min-width: 768px) {
      height: 77px;
      background-size: 83px 77px;
    }
  }
}

.ssb-intro-animation--background {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  top: 0;
  background: $white;
  opacity: 0;
}

.ssb-single-hero {
  background-color: $poppy;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  padding: 100px 0 29px;

  @media (max-width: 767px) {
    text-align: center;
  }

  .container {
    max-width: 1440px;
    padding: 0 23px;

    @media (min-width: 768px) {
      display: flex;
    }
  }

  .left {
    @media (min-width: 768px) {
      max-width: 550px;
    }

    .img {
      margin-bottom: 8px;
    }
  }

  .txt {
    display: flex;
    flex-wrap: wrap;
    padding-left: 33px;

    @media (max-width: 767px) {
      padding-left: 0;
      justify-content: center;
    }

    span {
      font-size: 18px;
      font-weight: 900;
      margin-right: 20px;
      color: $black;
    }
  }

  .more {
    display: flex;
    align-items: flex-end;
    font-weight: 700;
    color: white;
    font-size: 16px;
    text-decoration: none;

    &::after {
      content: '\e91a';
      font: 26px/1 'icomoon';
      margin-left: 17px;
    }
  }
}

.ssb-intro {
  font-size: 16px;
  line-height: 1.41;
  font-weight: 300;
  padding: 65px 0 44px;
  color: $white;

  @media (min-width: 768px) {
    font-size: 20px;
  }

  .container {
    @media (min-width: 768px) {
      display: flex;
      align-items: center;
    }
  }

  .img {
    display: none;

    @media (min-width: 768px) {
      display: block;
      width: 271px;
      margin: 0 30px 0 0;
    }
  }

  .body {
    flex: 1;
  }
}

$ssb-theme: (
  'red': $cable_car,
  'purple': $grapevine,
  'orange': $poppy,
  'green-light': $avocado,
  'green': $pine,
  'blue-light': $bayside,
  'blue': $deep_ocean,
  'yellow': $citrus,
);

@each $color, $value in $ssb-theme {
  .ssb-article.#{$color} {
    .img {
      //background-color: $value;
    }

    h2,
    a {
      color: $value;
    }
  }
}

$ssb-article-overflow: 1px;
$ssb-article-overflow_dig: 1;

.ssb-article {
  background-color: $white;
  transition: background-color 0.3s ease-out;
  font-size: 16px;
  line-height: 1.26;
  position: relative;
  will-change: transform;

  @media (min-width: 768px) {
    clip-path: polygon(0 69px, 0 100%, 100% 89%, 100% 6px);
    padding-bottom: 39.29%;
    width: 100%;

    .ssb-article--inner {
      backface-visibility: hidden;
      position: absolute;
      will-change: transform;
      left: 0;
      display: flex;
      width: 100%;
      top: -$ssb-article-overflow;
      height: calc(100% + #{2 * $ssb-article-overflow});
    }
  }

  @media (min-width: 768px) and (max-width: 1023px) {
    font-size: 14px;
  }

  @media (max-width: 767px) {
    .ssb-article--inner {
      margin-bottom: 20px;
    }
  }

  &-wrap {
    padding: 0 15px;

    .line,
    .line-a,
    .line-c {
      @media (min-width: 768px) {
        padding: 37px;
        background: white;
        pointer-events: none;
        margin: -52px 0 -55px;
        clip-path: polygon(0 63px, 0 calc(100% - 2px), 100% 10px, 100% 0);
      }
    }

    .line-a {
      @media (min-width: 768px) {
        margin: -31px 0 -52px;
        clip-path: polygon(0 0, 0 12%, 100% 67%, 100% 54%);
      }
    }

    .line-c {
      @media (min-width: 768px) {
        clip-path: polygon(0 39px, 0 66%, 100% 23px, 100% 14px);
      }
    }

    .ml-a {
      @media (max-width: 767px) {
        display: block;
        background-color: white;
        clip-path: polygon(0 35px, 0 100%, 100% calc(100% - 35px), 100% 0);
        padding: 21px;
        margin: -46px 0 -25px;
      }
    }

    .ml-b {
      @media (max-width: 767px) {
        display: block;
        background-color: white;
        padding: 21px;
        margin: -48px 0 -27px;
        clip-path: polygon(0 0, 0 calc(100% - 35px), 100% 100%, 100% 35px);
      }
    }
  }

  &.alt {
    @media (min-width: 768px) {
      .ssb-article--inner {
        flex-direction: row-reverse;
      }
    }
  }

  .img {
    @media (min-width: 768px) {
      width: 50%;
      position: relative;
      overflow: hidden;
      transform: translateZ(0);
    }

    img {
      width: 100%;

      @media (min-width: 768px) {
        backface-visibility: hidden;
        position: absolute;
        left: 50%;
        top: 50%;
        //width: 100%;
        height: 100%;
        transform: translate3d(-50%, -50%, 0);
        object-fit: cover;
      }
    }
  }

  .body {
    flex: 1;
    //padding: 154px 4% 44px;
    padding: #{154px + $ssb-article-overflow} 4% #{44px + $ssb-article-overflow};

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media (max-width: 767px) {
      padding: 54px 24px 44px;
    }
  }

  &:hover {
    //background-color: transparent;

    .body {
      p {
        //color: $white;
      }
    }
  }

  a {
    text-decoration: none;
  }

  h2 {
    font-weight: 900;
    font-size: 36px;
    line-height: 1.12;
    max-width: 555px;
    margin-bottom: 57px;

    @media (max-width: 1279px) {
      font-size: 30px;
    }

    @media (max-width: 1023px) {
      font-size: 20px;
      margin-bottom: 30px;
    }
  }

  .byline {
    color: #b3b3b3;
    font-weight: 700;
    max-width: 470px;
    margin-top: -25px;

    @media (max-width: 480px) {
      display: block;
    }
  }

  p {
    max-width: 535px;
    margin-bottom: 16px;
    transition: color 0.3s ease-out;

    @media (max-width: 480px) {
      display: none;
    }
  }

  .more {
    display: flex;
    align-items: flex-end;
    font-weight: 700;

    &::after {
      content: '\e91a';
      font: 26px/1 'icomoon';
      margin-left: 17px;
    }
  }

  &.ssb-block-a {
    @media (min-width: 768px) {
      clip-path: polygon(0 69px, 0 100%, 100% calc(100% - 62px), 100% 6px);
      padding-bottom: 67.29%;
    }

    @media (min-width: 1024px) {
      padding-bottom: 51.29%;
    }

    @media (min-width: 1280px) {
      padding-bottom: 43.29%;
    }

    @media (min-width: 1441px) {
      padding-bottom: 37.29%;
    }

    .img {
      @media (min-width: 768px) {
        width: 46.5%;
        //padding-bottom: 84.58%;
      }
    }
  }

  &.ssb-block-b {
    @media (min-width: 768px) {
      clip-path: polygon(0 65px, 0 calc(100% - 39px), 100% 100%, 100% 0);
      padding-bottom: 63.9%;

      .body {
        //padding: 131px 5% 70px;
        padding: #{131px + $ssb-article-overflow} 5% #{70px + $ssb-article-overflow};
      }

      .img {
        width: 48%;
        //padding-bottom: 85.35%;
      }

      img {
        //height: 577px;
      }
    }

    @media (min-width: 1024px) {
      padding-bottom: 48.9%;
    }

    @media (min-width: 1280px) {
      padding-bottom: 40.9%;
    }

    @media (min-width: 1441px) {
      padding-bottom: 30.9%;
    }
  }

  &.ssb-block-c {
    @media (min-width: 768px) {
      clip-path: polygon(0 0, 0 100%, 100% calc(100% - 61px), 100% 40px);
      padding-bottom: 46.74%;

      .body {
        //padding: 79px 38px 44px;
        padding: #{79px + $ssb-article-overflow} 38px #{44px + $ssb-article-overflow};
      }

      .img {
        //padding-bottom: 71.63%;
      }

      img {
        //height: 504px;
      }
    }

    @media (min-width: 1024px) {
      padding-bottom: 35.74%;
    }

    @media (min-width: 1440px) {
      padding-bottom: 26.74%;
    }
  }

  &.ssb-block-d {
    @media (min-width: 768px) {
      clip-path: polygon(0 0, 0 100%, calc(100% - 20px) calc(100% - 8px), calc(100% - 60px) 11px);

      .body {
        //padding: 34px 15% 34px 8%;
        padding: 34px 15% #{34px + $ssb-article-overflow} 8%;
      }

      .img {
        //padding-bottom: 59.57%;
        padding-bottom: #{(279 + $ssb-article-overflow_dig) / 495 * 100%};
      }
    }
  }

  &.ssb-block-e {
    @media (min-width: 768px) {
      margin-left: -26px;
      margin-right: -5px;
      clip-path: polygon(0% 13px, 40px calc(100% - 7px), calc(100% - 14px) calc(100% - 15px), 100% 27px);
    }

    @media (min-width: 1024px) {
      // margin-left: -14px;

      .img {
        //padding-bottom: 57.57%;
        padding-bottom: #{(279 + $ssb-article-overflow_dig) / 495 * 100%};
      }
    }

    .body {
      @media (min-width: 768px) {
        //padding: 58px 9% 44px;
        padding: #{58px + $ssb-article-overflow} 9% 44px;
      }
    }

    &:not(.alt) {
      .body {
        @media (min-width: 768px) and (max-width: 1023px) {
          padding-left: 17%;
          padding-bottom: 88px;
        }
      }
    }
  }

  &.ssb-block-f {
    @media (min-width: 768px) {
      margin-left: 23px;
      clip-path: polygon(16px 29px, 0 calc(100% - 17px), 100% calc(100% - 24px), 100% 41px);

      .img {
        //padding-bottom: 59.57%;
        // padding-bottom: #{(279 + $ssb-article-overflow_dig) / 495 * 100%};
        padding-bottom: calc(55.76% + 16px);
      }
    }

    .body {
      @media (min-width: 768px) {
        //padding: 58px 8% 55px 10%;
        padding: 58px 8% #{55px + $ssb-article-overflow} 10%;
      }
    }
  }

  &.ssb-block-g {
    @media (min-width: 768px) {
      clip-path: polygon(0 26px, 0% calc(100% - 19px), calc(100% - 36px) 100%, 100% 14px);
      margin-right: -3px;

      .body {
        //padding: 85px 8% 55px 6%;
        padding: 85px 8% #{65px + $ssb-article-overflow};
      }

      .img {
        //padding-bottom: 39.71%;
        padding-bottom: #{((280 + $ssb-article-overflow_dig) / 705 * 100%)};
      }
    }
  }

  &.ssb-block-h {
    @media (min-width: 768px) {
      clip-path: polygon(38px 14px, 0 calc(100% - 23px), 100% 100%, 100% 0);
      margin-left: -2px;

      .body {
        //padding: 88px 14% 44px 9%;
        padding: 88px 14% #{54px + $ssb-article-overflow} 9%;
      }

      .img {
        //padding-bottom: 39.71%;
        padding-bottom: #{(280 + $ssb-article-overflow_dig) / 705 * 100%};
      }
    }
  }

  .ssb-col-3 & {
    @media (min-width: 768px) {
      padding-bottom: 224.95%;
    }

    @media (min-width: 1024px) {
      padding-bottom: 195.95%;
    }

    @media (min-width: 1200px) {
      padding-bottom: 172.95%;
    }

    @media (min-width: 1280px) {
      padding-bottom: 165.95%;
    }

    @media (min-width: 1500px) {
      padding-bottom: 139.95%;
    }

    @media (min-width: 1690px) {
      padding-bottom: 125.95%;
    }
  }

  .ssb-col-2 & {
    @media (min-width: 768px) {
      padding-bottom: 141.95%;
    }

    @media (min-width: 1024px) {
      padding-bottom: 128.95%;
    }

    @media (min-width: 1280px) {
      padding-bottom: 106.95%;
    }

    @media (min-width: 1500px) {
      padding-bottom: 91.95%;
    }
  }

  &.ssb-block-a,
  &.ssb-block-d,
  &.ssb-block-f,
  &.ssb-block-h,
  &.ssb-block-b.blue-light {
    @media (max-width: 767px) {
      clip-path: polygon(0 0, 0 100%, 100% calc(100% - 35px), 100% 35px);
    }
  }

  &.ssb-block-b,
  &.ssb-block-c,
  &.ssb-block-e,
  &.ssb-block-g {
    @media (max-width: 767px) {
      clip-path: polygon(0 35px, 0 calc(100% - 35px), 100% 100%, 100% 0);
    }
  }
}

.ssb-col-2,
.ssb-col-3 {
  @media (min-width: 768px) {
    display: flex;
  }

  .col {
    @media (min-width: 768px) {
      width: 33.33%;
      position: relative;
      display: flex;
    }
  }

  .ssb-article {
    @media (min-width: 768px) {
      .ssb-article--inner {
        flex-direction: column;
      }
      flex: 1;

      &.alt {
        .ssb-article--inner {
          flex-direction: column-reverse;
        }

        .img.video {
          @media (min-width: 768px) {
            padding-bottom: 1px;
          }
        }

        .body {
          @media (min-width: 768px) {
            padding-bottom: 40px;
          }
        }
      }

      .img {
        width: auto;
      }

      video {
        max-width: 100%;
        height: auto;
        vertical-align: top;
      }

      img {
        //height: 279px;
      }
    }
  }
}

.ssb-col-3 {
  @media (min-width: 768px) {
    + .line-c {
      margin: -27px 0 -40px;
    }

    .col {
      &:nth-child(2) {
        //padding-bottom: 7px;
      }

      &:nth-child(3) {
        //padding-bottom: 16px;
      }
    }
  }
}

.ssb-col-2 {
  .col {
    @media (min-width: 768px) {
      width: 50%;

      &:nth-child(1) {
        padding-bottom: 23px;
      }
    }
  }

  .ssb-article {
    @media (min-width: 768px) {
      p {
        max-width: 555px;
      }

      .body {
        min-height: 350px;
      }

      &.ssb-block-h {
        .body {
          min-height: 420px;
        }
      }
    }
  }

  .line-v-b {
    @media (min-width: 768px) {
      top: -1px;
      bottom: 9px;
      right: -35px;
      clip-path: polygon(0% 100%, 11px 100%, calc(100% - 10px) 0, 38px 0%);
    }
  }

  + .line-a {
    @media (min-width: 768px) {
      margin: -32px 0 -54px;
    }
  }
}

.line-v-a,
.line-v-b {
  @media (min-width: 768px) {
    position: absolute;
    width: 59px;
    top: -9px;
    bottom: -8px;
    background-color: $white;
    clip-path: polygon(calc(100% - 11px) 100%, 100% 100%, calc(100% - 43px) 2px, 5px 1px);
  }
}

.line-v-a {
  @media (min-width: 768px) {
    left: -55px;
  }
}

.line-v-b {
  @media (min-width: 768px) {
    top: 7px;
    bottom: -4px;
    right: -60px;
    clip-path: polygon(0 100%, 11px 100%, 25px 2px, 14px 2px);
  }
}

.ssb-block-b + .line-a {
  @media (min-width: 768px) {
    margin: -29px 0 -54px;
  }
}

.ssb-quote-wrap--inner {
  @media (min-width: 1600px) {
    max-width: 1440px;
    margin: 0 auto;
  }
}

.ssb-quote-wrap {
  position: relative;
  z-index: 1;

  @media (max-width: 767px) {
    padding-bottom: 50px;
  }

  &:after {
    content: '';
    background: $poppy;
    position: absolute;
    top: -5px;
    left: 14px;
    right: 14px;
    bottom: 0;
    z-index: -1;
    clip-path: polygon(0 35px, 0 100%, 100% 100%, 100% 0);

    @media (min-width: 768px) {
      clip-path: polygon(0 100%, 100% 100%, 100% 45px, 0% 4px);
    }
  }

  &:before {
    content: '';
    background: url('/wp-content/themes/a16z-2015/client/images/ssb/pattern-mobile.svg') no-repeat 0 0 / cover;
    width: 288px;
    height: 212px;
    position: absolute;
    right: 5%;
    bottom: 21px;

    @media (min-width: 480px) {
      width: 485px;
      height: 360px;
      position: absolute;
      right: 3%;
    }

    @media (min-width: 768px) {
      background: url('/wp-content/themes/a16z-2015/client/images/ssb/pattern.svg') no-repeat 0 0 / cover;
      width: 816px;
      height: 250px;
      position: absolute;
      right: 13%;
      bottom: 0;
    }
  }

  .ssb-quote-block {
    position: relative;
    pointer-events: auto;

    @media (min-width: 768px) {
      width: 750px;
    }

    @media (min-width: 1200px) {
      width: 950px;
    }

    @media (min-width: 1600px) {
      //margin: 0 auto;
    }
  }

  .ssb-quote-block--inner {
    transition: transform 1s ease-out;
    transform: scale(0.1);
    transform-origin: 100% 100%;
    will-change: transform;

    @media (max-width: 767px) {
      max-width: 412px;
      margin: 0 auto;
    }

    &.is-appeared {
      transform: none;
    }

    .quote-frame {
      position: absolute;
      background-size: cover;
      background-repeat: no-repeat;
      background-image: url('../../images/ssb/mobile-quote.svg');
      left: -57px;
      top: -17px;
      right: auto;
      width: 521px;
      height: 412px;
      pointer-events: none;

      @media (min-width: 768px) {
        background-image: url('../../images/ssb/quote.svg');
        right: auto;
        left: -185px;
        top: -32px;
        width: 1000px;
        height: 508px;
      }

      @media (min-width: 1200px) {
        width: 1307px;
        height: 665px;
      }
    }
  }

  .slick-dots {
    display: flex;
    position: absolute;
    bottom: 29px;
    z-index: 9;
    left: 36px;

    @media (min-width: 768px) {
      left: auto;
      right: 36px;
      top: 67px;
    }

    li {
      padding: 0 8px;

      &.slick-active {
        button {
          background-color: $black;
        }
      }
    }

    button {
      font-size: 0;
      line-height: 1;
      width: 20px;
      height: 20px;
      background-color: transparent;
      border: 4px solid $black;
      outline: none;
      box-shadow: none;
      display: block;
      border-radius: 50%;
    }
  }
}

.ssb-quote-slider {
  text-align: center;
  position: relative;
  z-index: 1;

  .slick-slide {
    padding: 58px 30px 40px;
    position: relative;

    @media (min-width: 768px) {
      padding: 50px 106px 31px 80px;
    }

    @media (min-width: 1200px) {
      padding: 106px 110px 31px;
    }
  }

  blockquote {
    margin: 0 0 12px;

    q {
      font-size: 18px;
      line-height: 1.34;
      font-weight: 700;
      margin: 0 0 14px;
      display: block;
      color: $black;
      position: relative;

      @media (min-width: 768px) {
        font-size: 24px;
      }

      @media (min-width: 1200px) {
        font-size: 30px;
      }

      &:before,
      &:after {
        font-family: 'Permanent Marker', cursive;
        position: absolute;
        top: -10px;
        font-size: 39px;
        line-height: 1;
        color: $citrus;
        font-weight: 400;
        display: none;

        @media (min-width: 768px) {
          top: 34px;
          font-size: 85px;
          display: block;
        }

        @media (min-width: 1200px) {
          font-size: 124px;
          top: 66px;
        }
      }

      &:before {
        left: 0;

        @media (min-width: 768px) {
          left: -71px;
        }

        @media (min-width: 1200px) {
          left: -83px;
        }
      }

      &:after {
        right: 0;
        top: auto;
        bottom: -10px;

        @media (min-width: 768px) {
          right: -54px;
          top: 34px;
          bottom: auto;
        }

        @media (min-width: 1200px) {
          right: -100px;
          top: 66px;
        }
      }
    }

    cite,
    cite a,
    cite a:hover {
      color: $citrus;
      font-size: 16px;
      display: block;
      font-style: normal;
      font-family: 'Permanent Marker', cursive;
      text-decoration: none;

      @media (min-width: 768px) {
        font-size: 26px;
      }

      @media (min-width: 1200px) {
        font-size: 35px;
      }
    }
  }

  .social-links {
    @extend %listreset;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;

    li {
      padding: 0 6px;

      a {
        width: 38px;
        height: 38px;
        background: $deep_ocean;
        border-radius: 50%;
        display: block;
        overflow: hidden;
        color: $white;
        text-decoration: none;

        > i {
          line-height: inherit;
        }

        .icon-facebook-a {
          font-size: 23px;
        }

        .icon-linkedin-a {
          font-size: 20px;
        }

        .icon-twitter {
          font-size: 24px;
        }
      }
    }
  }
}

