.ssb-intro-animation--container {
  max-width: 1440px;
  margin: 0 auto;

  @media (min-width: 768px) {
    padding-left: 1.25rem;
    padding-right: 1.25rem;
  }
}

.ssb-canvas--wrapper {
  position: relative;

  @media (max-width: 767px) {
    display: none;
  }

  &:before {
    content: "";
    padding-bottom: 57.92%;
    display: block;
    position: relative;
  }
}

#ssb-scene {
  position: absolute;
  display: block;
  width: 100%;
  left: 0;
  top: 0;
  height: 100%;
}

.ssb-frame {
  position: absolute;
  width: 330px;
  height: 100%;
  top: 0;
  will-change: transform;

  @media (max-width: 767px) {
    display: none;
  }

  svg {
    display: block;
    position: relative;
    height: 100%;
  }

  &:before {
    content: "";
    position: absolute;
    height: 100%;
    background: $white;
    width: 100vw;
  }
}

#ssb-frame-right {
  right: 0;
  transform: translate3d(100vw, 0, 0);

  &:before {
    left: 99%;
  }

  svg {
    margin-left: auto;
  }
}

#ssb-frame-left {
  left: 0;
  transform: translate3d(-100vw, 0, 0);

  &:before {
    right: 99%;
  }
}

.page-template-page_template-social-strikes-back-lp {
  #content {
    will-change: transform;
  }
}

[data-parallax] {
  will-change: transform;
  position: absolute;
  pointer-events: none;
}

@keyframes keyframeOrbitSm {
  from {
    transform: rotate(0deg) translateX(10px) rotate(0deg);
  }

  to {
    transform: rotate(360deg) translateX(10px) rotate(-360deg);
  }
}

@keyframes keyframeOrbitMd {
  from {
    transform: rotate(0deg) translateX(20px) rotate(0deg);
  }

  to {
    transform: rotate(360deg) translateX(20px) rotate(-360deg);
  }
}

@keyframes keyframeOrbitLg {
  from {
    transform: rotate(0deg) translateX(30px) rotate(0deg);
  }

  to {
    transform: rotate(360deg) translateX(30px) rotate(-360deg);
  }
}

@keyframes keyframeUpDownSm {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(20px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes keyframeUpDownMd {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(30px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes keyframeUpDownLg {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(40px);
  }

  100% {
    transform: translateY(0);
  }
}

[data-orbit-sm] {
  @media (min-width: 768px) {
    animation-name: keyframeOrbitSm;
    animation-duration: 10.2s;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
  }
}

[data-orbit-md] {
  @media (min-width: 768px) {
    animation-name: keyframeOrbitMd;
    animation-duration: 10.2s;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
  }
}

[data-orbit-lg] {
  @media (min-width: 768px) {
    animation-name: keyframeOrbitLg;
    animation-duration: 10.2s;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
  }
}

[data-orbit-sm="alt"] {
  animation-direction: reverse;
}

[data-orbit-md="alt"] {
  animation-direction: reverse;
}

[data-orbit-lg="alt"] {
  animation-direction: reverse;
}

[data-updown-sm] {
  @media (min-width: 768px) {
    animation-name: keyframeUpDownSm;
    animation-duration: 6s;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    animation-timing-function: linear;

    &[data-updown-delay] {
      animation-delay: 2s;
    }
  }
}

[data-updown-md] {
  @media (min-width: 768px) {
    animation-name: keyframeUpDownMd;
    animation-duration: 6s;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    animation-timing-function: linear;

    &[data-updown-delay] {
      animation-delay: 2s;
    }
  }
}

[data-updown-lg] {
  @media (min-width: 768px) {
    animation-name: keyframeUpDownLg;
    animation-duration: 6s;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
    animation-timing-function: linear;

    &[data-updown-delay] {
      animation-delay: 2s;
    }
  }
}

[data-updown-sm="alt"] {
  animation-direction: reverse;
}

[data-updown-md="alt"] {
  animation-direction: reverse;
}

[data-updown-lg="alt"] {
  animation-direction: reverse;
}
