/*
 * Content Column Layout Styles
 */

.content-column {
	display: flex;
	flex-direction: column;
	width: 100%;

	@include breakpoints(medium) {
		flex-direction: row;
	}
}

.content-column--two-thirds {

	.content-column__column--small,
	.content-column__column--large {
		width: 100%;
	}

	.content-column__column--small {
		margin-bottom: spacer(2.75);

		@include breakpoints(medium) {
			flex: 0 0 auto;
			margin: 0 spacer(2) 0 0;
			width: spacer(17);
		}

		@include breakpoints(large) {
			margin: 0 spacer(3) 0 0;
		}
	}

	.content-column__column--large {
		@include breakpoints(medium) {
			flex: 1 1 auto;
		}
	}
}

.content-column--two-halves {
	@include breakpoints(medium) {
		flex-flow: row wrap;
	}

	.content-column__column--half {
		margin-bottom: spacer(2.75);

		@include breakpoints(medium) {
			flex: 1 1 auto;
			margin: 0 spacer(2) spacer(2) 0;
			width: calc(50% - #{spacer(2)});
		}

		@include breakpoints(large) {
			margin: 0 spacer(4) spacer(4) 0;
			width: calc(50% - #{spacer(4)});
		}
	}

	> *:nth-last-child(-n+2) {
		margin-bottom: 0;
	}
}
