/*
 * Summit Hub page styles
 */

// stylelint-disable selector-max-specificity

body.page-template-page_template-summit-landing {
    background: $white;
}

body.post-type-archive-a16z_demo {
  background: $white;
}

.page-template-page_template-summit-landing,
.post-type-archive-a16z_demo {
  .site-content {
    margin-top: -121px;
    width: 100%;
  }

  .header-new {
    background: transparent;
  }

  .content-new {
    padding-top: 0;

    > .container {
      padding-top: 0;
    }
  }

  .landing-banner-wrap {
    margin-left: -100vw;
    margin-right: -100vw;
  }

  .landing-banner {
    padding: 11em 1.875rem 7rem;
    margin-left: auto;
    margin-right: auto;
    width: 100vw;

    @include breakpoints(none, s-medium) {
      padding-bottom: 5rem;
    }
  }
}

.page-template-page_template-summit-landing .content-new > .container {
  @include breakpoints(none, s-medium) {
    margin-bottom: 0;
  }
}

body.page-template-page_template-summit-sign-up {
    padding: 0;
    margin: 0;
    background: $white;

    > .site {
        max-width: none;
        background: transparent;
    }

    .site-header {
        display: none;
    }

    .site-content {
        margin-bottom: 0;
        padding: 0;
    }
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
    color: $steel;
}

.container {
    max-width: rem(1350);
    margin: 0 auto;
    padding-left: rem(16);
    padding-right: rem(16);

    @include breakpoints(s-medium) {
        padding-left: rem(20);
        padding-right: rem(20);
    }
}

.btn {
    display: inline-block;
    vertical-align: top;
    padding: 12px 8px;
    font-size: 16px;
    line-height: 1.2;
    font-weight: 600;
    transition: all 0.3s linear;
    color: $white;
    text-decoration: none;
    text-align: center;

    @include breakpoints(s-medium) {
        padding: 11px 21px;
    }

    &.btn-orange {
        transition: background 0.3s linear;
        background: $poppy;

        &:hover {
            background: $poppy;
        }
    }

    &.btn-blue {
        background: $bayside;
        border-radius: 2px;
        padding: 10px 12px;

        &:hover {
            background: darken($bayside, 15%);
        }
    }

    i {
        display: inline-block;
        vertical-align: middle;
        margin: -2px 0 0 7px;

        img {
            display: block;
        }
    }
}

.link {
    display: inline-block;
    vertical-align: top;
    color: $poppy;
    font-size: rem(15);
    padding-right: 22px;
    position: relative;
    text-decoration: none;
    transition: all 0.3s linear;

    &::after {
        content: "\e902";
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        font: 13px/1 "icomoon";
        margin-top: -1px;
    }

    &:hover {
        color: darken($poppy, 10%);
    }
}

.page-template-page_template-summit-hub .site-header {
    display: none;
}

.header-summit-hub {
    padding: rem(25 0);

    .container {
        max-width: 1182px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
    }

    .logo {
        width: 280px;
        margin: 0 auto;

        @include breakpoints(s-medium) {
            width: 315px;
            margin: 0;
        }

        a {
            display: block;
            height: 100%;
        }

        img {
            display: block;
        }
    }

    .btn-wrap {
        text-align: center;
        margin-top: rem(20);
        width: 100%;

        @include breakpoints(s-medium) {
            text-align: left;
            margin: 0;
            width: auto;
        }
    }
}

.hero-summit-hub {
    padding: rem(50 0);
    position: relative;
    text-align: center;
    color: $white;

    @include breakpoints(s-medium) {
        padding: rem(100 0);
    }

    @include breakpoints(s-large) {
        padding: rem(158 0 182);
    }

    .container {
        position: relative;
        z-index: 2;
        max-width: 650px;
    }

    .logo-wrap {
        width: 280px;
        margin: rem(0 auto 25);

        @include breakpoints(s-medium) {
            margin-bottom: rem(41);
            width: 375px;
        }
    }

    .hero-content {
        font-size: rem(18);
        line-height: 1.5;

        @include breakpoints(s-medium) {
            font-size: rem(20);
            padding: rem(0 6);
        }

        .text-wrap {
            font-weight: 700;
        }

        ul {
            margin: 0;
            text-align: left;
            padding: 0;
            list-style: none;
            display: inline-block;
            vertical-align: top;
            padding: rem(30 0 0 8);
            font-size: rem(18);
            line-height: 1.5;
            font-weight: 300;

            @include breakpoints(s-medium) {
                font-size: rem(20);
            }

            li {
                padding-left: rem(22);
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 13px;
                    width: 5px;
                    height: 5px;
                    background: $white;
                    border-radius: 50%;
                    overflow: hidden;
                }
            }

            strong {
                font-weight: 700;
            }
        }
    }

    .highlight-txt {
        font-size: rem(24);
        line-height: 1.31;
        font-weight: 700;
        margin-bottom: rem(32);

        @include breakpoints(s-medium) {
            font-size: rem(32);
        }
    }

    .signup-form-wrap {
        margin-top: rem(30);

        p {
            margin-bottom: 42px;
        }
    }
    // stylelint-disable
    .sign-up-form {
        background: $white;
        padding-right: 90px;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
        max-width: 400px;
        margin: 0 auto;
        text-align: left;
        padding: 0 !important;

        input[type="email"] {
            background: none;
            border: none !important;
            outline: none !important;
            padding: 11px 15px !important;
            width: 100% !important;
            font-size: rem(15);
            line-height: 1.2;
        }

        button[type="submit"] {
            transition: background 0.3s linear;
            position: absolute;
            right: 0;
            bottom: 0;
            top: 0;
            width: 90px;
            background: $bayside !important;
            border: none !important;
            outline: none;
            border-radius: 0;
            cursor: pointer;
            color: $white;
            font-size: rem(16);
            font-weight: 700;
            line-height: 1;

            &::before {
                content: "Submit";
                //position: absolute;
                //top: 50%;
                //left: 50%;
                //transform: translate(-50%, -50%);
                //background: url('/wp-content/themes/a16z-2015/client/images/summit-hub/arrow-right.svg') no-repeat 0 0 / 12px 12px;
                //width: 12px;
                //height: 12px;
            }

            img {
                display: none !important;
            }

            &:hover {
                background: darken($bayside, 10%) !important;
                border: none !important;
            }
        }
        .mktoFormCol {
            margin: 0 !important;
        }
        .mktoButtonRow {
            position: absolute !important;
            right: 0;
            height: 100%;
        }
        &.mktoForm .mktoButtonWrap.mktoSimple .mktoButton {
            border: 0 none !important;
        }
    }
    // stylelint-enable
}

.summit-content {
    padding: rem(50 0);

    @include breakpoints(s-large) {
        padding: rem(94 0 148);
    }

    .container {
        max-width: rem(1182);
    }

    .header {
        margin-bottom: rem(50);

        @include breakpoints(s-large) {
            margin-bottom: rem(92);
        }
    }

    h2 {
        text-align: center;
        font-size: rem(32);
        line-height: 1.05;

        @include breakpoints(s-medium) {
            font-size: rem(42);
        }
    }

    .row {
        margin: rem(0 -15);
        display: flex;
        flex-wrap: wrap;

        .post-box {
            padding: rem(0 15 40);
            width: 100%;

            @include breakpoints(s-medium) {
                width: 50%;
                padding: rem(0 15 49);
            }

            @include breakpoints(s-large) {
                width: 33.33%;
            }
        }
    }
}

.post-box {

    .img-wrap {
        display: block;
        text-decoration: none;
        width: 100%;
        padding-top: 56.25%;
        position: relative;
        margin-bottom: rem(25);

        .img {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-size: cover;
            background-position: 50% 50%;
        }

        &:hover .btn-play {
            border-color: $poppy;

            &::after {
                border-color: transparent transparent transparent $poppy;
            }
        }

        .btn-play {
            transition: border 0.3s linear;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 62px;
            height: 62px;
            border-radius: 50%;
            border: 6px solid $white;
            overflow: hidden;
            background: rgba($black, 0.5);

            &::after {
                content: "";
                transition: border 0.3s linear;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 10px 0 10px 18px;
                border-color: transparent transparent transparent $white;
                margin-left: 2px;
            }

            .hide-buttons & {
              display: none;
            }
        }
    }

    h3 {
        font-size: rem(22);
        line-height: 1.14;
        font-weight: 600;
        margin-bottom: rem(3);

      > a {
        text-decoration: none;

        &:hover {
          color: $poppy;
        }
      }
    }

    .by {

        a {
            transition: color 0.3s linear;
            text-decoration: none;
            color: $poppy;
            font-weight: 600;

            &:hover {
                color: $poppy;
            }
        }

        span {
            transition: color 0.3s linear;
            text-decoration: none;
            color: $poppy;
            font-weight: 600;
        }
    }

    .social-links {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding: 13px 0 0;
        font-size: 13px;

        @include breakpoints(767px) {
            font-size: 19px;
            padding-top: 19px;
        }

        li {
            margin-right: 20px;
            margin-bottom: 10px;
        }

        a {
            color: $concrete;
            text-decoration: none;
            transition: color 0.3s linear;

            &:hover {
                color: $poppy;
            }
        }
    }

    .tags {
        padding: 0;
        margin: 20px -15px 0;
        list-style: none;
        font-size: 12px;
        line-height: 1.1;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        color: $concrete;

        li {
            padding: 0 15px;
            position: relative;

            &::after {
                content: "";
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                background: $concrete;
                width: 3px;
                height: 3px;
                border-radius: 50%;
            }

            &:last-child::after {
                content: none;
            }

            a {
                text-decoration: none;
                transition: color 0.3s linear;
                color: $concrete;

                &:hover {
                    color: $poppy;
                }
            }
        }
    }
}

.site-footer {
    max-width: 1182px;
    margin: 0 auto;
    padding-left: rem(16);
    padding-right: rem(16);
    flex-wrap: wrap;

    @include breakpoints(s-medium) {
        padding-left: rem(20);
        padding-right: rem(20);
    }

    .site-footer__utilities-menu {
        @media only screen and (max-width: 1023px) {
            width: 100%;
            margin: rem(30 0 0);
        }

        @media only screen and (max-width: 767px) {
            margin: 0;
        }
    }
}

.landing-banner {
    padding: rem(53 30);
    color: $white;
    margin-bottom: rem(44);

    h1 {
        margin: 0;
        text-align: center;
        color: $white;
    }

    img,
    span {
        display: block;

        @include breakpoints(s-medium) {
            display: inline-block;
            vertical-align: middle;
        }
    }

    img {
        margin: rem(0 auto 30);

        @include breakpoints(s-medium) {
            margin: auto;
        }
    }

    span {
        font-size: rem(40);
        line-height: 1;
        font-weight: 300;

        @include breakpoints(s-medium) {
            margin: rem(0 0 0 20);
            font-size: rem(65);
            letter-spacing: -3.5px;
        }
    }
}

.summit-category {

    .row {
        margin: rem(0 -15);
        display: flex;
        flex-wrap: wrap;

        @include breakpoints(s-medium) {
            margin: rem(0 -20);
        }

        @include breakpoints(s-large) {
            margin: rem(0 -25);
        }
    }

    h2 {
        font-size: rem(24);
        line-height: 1.24;
        border-bottom: 1px solid #a8a8a8;
        padding-bottom: rem(12);
        margin-bottom: rem(30);

        @include breakpoints(s-medium) {
            font-size: rem(30);
            letter-spacing: -0.8px;
        }

        @include breakpoints(m-large) {
            font-size: rem(34);
            margin-bottom: rem(50);
        }
    }

    .post-box {
        padding: rem(0 15 35);
        width: 100%;

        @include breakpoints(s-medium) {
            width: 50%;
            padding: rem(0 20 35);
        }

        @include breakpoints(s-large) {
            width: 33.33%;
            padding: rem(0 25 44);
        }
    }

    h3 a {
      color: $steel;
    }
}

.summit-newsletter {
    margin-top: rem(50);

    @include breakpoints(s-large) {
        margin-top: 0;
    }

    .row {
        display: flex;
        flex-wrap: wrap;
        margin: rem(0 -15);

        @include breakpoints(m-large) {
            margin: rem(0 -25);
        }

        [class*="col-"] {
            display: flex;
            flex-wrap: wrap;
            padding: rem(0 15);

            @include breakpoints(m-large) {
                padding: rem(0 25);
            }
        }

        .col-lg {
            margin-bottom: rem(30);
            width: 100%;

            @include breakpoints(s-large) {
                width: 66.67%;
                margin-bottom: 0;
            }
        }

        .col-sm {
            width: 100%;

            @include breakpoints(s-large) {
                width: 33.33%;
            }
        }
    }

    .event {
        background: #f8f8f8;
        position: relative;
        width: 100%;

        @include breakpoints(s-large) {
            padding: rem(30);
            min-height: 300px;
        }

        .bg-wrap {
            position: relative;
            width: 100%;
            height: 0;
            padding-bottom: 38.96%;

            @include breakpoints(s-large) {
                position: absolute;
                right: 0;
                top: 0;
                bottom: 0;
                padding: 0;
                height: auto;
            }

            .img {
                position: absolute;
                background: no-repeat 0 50% / cover;
                right: 0;
                top: 0;
                bottom: 0;
                left: 0;
            }
        }
    }

    .logo-wrap {
        margin-bottom: rem(20);

        @include breakpoints(s-medium) {
            margin-bottom: rem(55);
        }
    }

    h2 {
        font-size: rem(30);
        line-height: 1.13;
        font-weight: 300;
        color: $poppy;
        margin-bottom: rem(28);

        @include breakpoints(s-medium) {
            font-size: rem(40);
        }
    }

    .speakers-row {
        color: $concrete;

        .title {
            display: block;
            font-size: 14px;
            line-height: 1.14;
            color: $bayside;
            text-transform: uppercase;
            margin-bottom: rem(3);
        }
    }

    .signup-col {
        padding: rem(25 30);
        color: $white;
        line-height: 1.25;
        width: 100%;

        h3 {
            font-size: rem(28);
            line-height: 1.06;
            color: $white;
            margin-bottom: rem(12);

            @include breakpoints(s-medium) {
                padding-right: rem(41);
                font-size: rem(34);
            }
        }
    }

    .signup-form-holder {
        padding-top: rem(19);

        .title {
            display: block;
            font-weight: 600;
            color: $white;
            text-transform: uppercase;
            margin-bottom: rem(21);
        }

        .sign-up-form {
            // stylelint-disable
            background: $white;
            padding-right: 40px;
            position: relative;
            border-radius: 4px;
            overflow: hidden;
            max-width: 400px;
            text-align: left;
            padding: 0 !important;

            input[type="email"] {
                background: none;
                border: none;
                outline: none;
                padding: 11px 15px;
                width: 100%;
                font-size: rem(15);
                line-height: 1.2;
                color: #999;
                height: 40px;
            }

            button[type="submit"] {
                transition: background 0.3s linear;
                position: absolute;
                right: 0;
                bottom: 0;
                top: 0;
                width: 40px;
                background: $marina !important;
                border: none !important;
                outline: none;
                border-radius: 0;
                cursor: pointer;
                color: $white;
                font-size: rem(16);
                font-weight: 700;
                line-height: 1;

                &::before {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    background: url('/wp-content/themes/a16z-2015/client/images/summit-hub/arrow-right.svg') no-repeat 0 0 / 12px 12px;
                    width: 12px;
                    height: 12px;
                }

                &:hover {
                    background: darken($marina, 10%) !important;
                    border: none !important;
                }
                img {
                    display: none !important;
                }
            }
            .mktoFormCol {
                margin: 0 !important;
            }
            .mktoButtonRow {
                position: absolute !important;
                right: 0;
                height: 100%;
            }
            // stylelint-enable
        }
    }
}

.coming-soon-block {
    border-top: 1px solid #a8a8a8;
    padding: rem(33 0 43);

    .row {
        margin: rem(0 -15);
        display: flex;
        flex-wrap: wrap;

        @include breakpoints(s-medium) {
            margin: rem(0 -20);
        }

        @include breakpoints(s-large) {
            margin: rem(0 -25);
        }
    }

    .title-col {
        padding: rem(0 15);
        width: 100%;
        margin-bottom: rem(20);

        @include breakpoints(s-medium) {
            width: 33.33%;
            padding: rem(0 20);
        }

        @include breakpoints(s-large) {
            padding: rem(0 25);
        }

        strong {
            font-size: rem(30);
            line-height: 1.24;
            font-weight: 300;

            @include breakpoints(s-large) {
                font-size: rem(34);
            }
        }
    }

    .list-col {
        padding: rem(13 15 0);
        width: 100%;
        font-size: 1rem(18);
        line-height: 1.22;
        font-weight: 600;

        @include breakpoints(s-medium) {
            width: 66.67%;
            padding: rem(0 20);
        }

        @include breakpoints(s-large) {
            padding: rem(0 25);
        }

        ul {
            padding: 0;
            margin: rem(0 0 41);
            list-style: none;
            font-size: rem(18);
            line-height: 1.11;
            font-weight: 400;

            li {
                padding-left: rem(15);
                position: relative;

                + li {
                    margin-top: rem(15);
                }

                &::before {
                    content: "";
                    background: $steel;
                    width: 4px;
                    height: 4px;
                    border-radius: 50%;
                    position: absolute;
                    left: 0;
                    top: 7px;
                }
            }
        }

        a {
            text-decoration: none;
            color: $poppy;

            &:hover {
                color: $poppy;
            }
        }
    }
}

.download-block {
    border-top: 1px solid #a8a8a8;
    padding: rem(30 0 0);

    @include breakpoints(s-large) {
        padding: rem(72 0 79);
    }

    .title {
        display: block;
        font-size: rem(30);
        font-weight: 300;
        margin-bottom: rem(28);

        @include breakpoints(s-large) {
            font-size: rem(34);
            margin-bottom: rem(38);
        }
    }

    .download-lists {
        padding: 0;
        margin: rem(0 -20);
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        font-size: 18px;
        line-height: 1.22;

        li {
            width: 100%;
            padding: rem(0 20 20);

            @include breakpoints(s-large) {
                width: 50%;
            }

            @include breakpoints(m-large) {
                width: 33.33%;
            }

            a {
                text-decoration: none;
                color: $poppy;
                display: inline-block;
                vertical-align: top;
                padding-right: rem(26);
                position: relative;

                @include breakpoints(none, s-medium) {
                  //padding-right: rem(10);
                }

                &::after {
                    content: "";
                    position: absolute;
                    right: 0;
                    top: 2px;
                    background: url('/wp-content/themes/a16z-2015/client/images/summit-hub/caret-right.png') no-repeat 0 0 / 11px 16px;
                    width: 11px;
                    height: 16px;

                    @include breakpoints(none, s-medium) {
                      margin: 0 0 0 5px;
                      display: inline-block;
                      position: relative;
                      right: auto;
                    }
                }

                &:hover {
                    color: $poppy;
                }
            }
        }
    }
}

.intro-text {
    max-width: 778px;
    margin: rem(0 auto 44);
    font-size: rem(20);
    line-height: 1.45;
    font-weight: 600;
    text-align: center;

    @include breakpoints(s-large) {
        font-size: rem(22);
        margin-bottom: rem(72);
    }

    p {
        margin: 0;

        + p {
            margin-top: rem(16);
        }
    }

   a {
     color: $poppy;

     &:hover {
       color: $poppy;
     }
   }
}
