/*
 * Briefings page styles
 */

.page-template-page_template-briefings {

	.page-header__subnav-list {
		flex-direction: row;
	}

}

.page-content--briefings {
	@include vertical-rhythym(spacer(2), spacer(3.5), $border-light);

	@include breakpoints(medium) {
		@include vertical-rhythym(spacer(4), spacer(4), $border-light);
	}
}
