/*
* Growth Metrics inner page styles
*/

/* stylelint-disable */
/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */
body.page-template-page_template-growth-metric-results {

    .content-new {
        padding: 0;
        max-width: none;

        > .container {
            margin: 0;
            padding: 0;
            max-width: none;
        }
    }

    .header-new {
        @include breakpoints(none, 's-large') {
            position: fixed;
            width: 100%;
            z-index: 99;
        }
    }

    #content {
        @include breakpoints(none, 's-large') {
            padding-top: 134px;
        }

        @include breakpoints(none, 's-medium') {
            padding-top: 90px;
        }
    }

    .site-content__primary,
    .archive-content--author {
        margin-bottom: 0;
    }

    #main {
        margin: 0;

        .container {
            max-width: 1426px;
            padding-left: 15px;
            padding-right: 15px;
            margin: 0 auto;
        }
    }

    .footer-new {
        display: none;
    }

    .site {
        overflow: visible;
    }

    a {
        text-decoration: none;
    }
}

.gm-active,
.gm-drawer-active {
    @include breakpoints(none, s-large) {
        overflow: hidden;
    }
}

.gm-container {
    padding: 0 24px;
    max-width: 1426px;
    margin: 0 auto;
}

.gm-btn {
    min-height: 30px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 110px;
    padding: 4px 15px;
    background: $fog;
    color: $steel;
    border: 1px solid transparent;
    transition: all 0.25s linear;

    &.btn-avocado {
        background: $avocado;
        color: white;

        &:hover {
            border-color: $fog;
        }
    }

    &.btn-avocado-o {
        background: white;
        border-color: $fog;
        color: $fog;

        &:hover {
            border-color: $avocado;
        }
    }
}

.gm-tooltip {
    width: 12px;
    height: 12px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: $concrete;
    color: $fog;
    font-weight: 700;
    overflow: hidden;
    border-radius: 50%;
    font-size: 10px;
    line-height: 1;
    position: relative;
    top: -5px;
    cursor: pointer;
}

.gm-btn-options {
    display: flex;
    gap: 10px;
    margin-bottom: 24px;

    label {
        display: block;
    }

    input[type="radio"] {
        display: none;

        &:checked + .radio-btn {
            background: $avocado;
            color: white;

            &:hover {
                border-color: $fog;
            }
        }
    }

    .radio-btn {
        min-height: 30px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        min-width: 110px;
        padding: 4px 8px;
        transition: all 0.25s linear;
        background: white;
        border: 1px solid $fog;
        color: $fog;
        cursor: pointer;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
        letter-spacing: -0.5px;
        font-size: 16px;

        &:hover {
            border-color: $avocado;
        }
    }
}

.gm-wrapper {
    position: relative;
    //padding-top: 50px;
    line-height: 1.21;

    @include breakpoints(none, s-large) {
        padding-top: 0;
    }
}

.gm-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background: rgba(255, 255, 255, 0.8);
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.25s linear;

    .gm-active & {
        opacity: 1;
        pointer-events: auto;
    }
}

.gm-openclose {
    text-align: center;
    color: white;
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    background: $avocado;
    overflow: hidden;

    @include breakpoints(none, s-large) {
        position: fixed;
        bottom: 0;
        top: auto;
        //max-height: calc(100vh - 134px);

        .gm-active & {
            overflow-y: auto;
        }
    }

    @include breakpoints(none, s-medium) {
        //max-height: calc(100vh - 90px);
    }

    [data-more] {
        padding: 12px 0;
        color: white;
        position: relative;
        display: block;
        font-size: 20px;
        line-height: 1.3;
        font-weight: 700;
        letter-spacing: -0.3px;
        transition: all 0.25s linear;

        @include breakpoints(none, s-large) {
            position: static;
        }

        &::after {
            content: "";
            background-image: url('data:image/svg+xml;base64,');
            background-size: contain;
            background-repeat: no-repeat;
            height: 9px;
            width: 16px;
            display: block;
            position: absolute;
            right: -16px;
            top: 20px;

            @include breakpoints(none, s-large) {
                display: none;
            }
        }

        &.active {
            font-size: 25px;
            line-height: 1.04;
            padding-top: 50px;

            @include breakpoints(none, s-large) {
                font-size: 20px;
                line-height: 1.3;
            }

            &::after {
                background-image: url('data:image/svg+xml;base64,');
                width: 17px;
                height: 17px;

                @include breakpoints(none, s-large) {
                    display: block;
                    top: auto;
                    right: auto;
                    bottom: 25px;
                    left: 50%;
                    transform: translateX(-50%);
                }
            }
        }
    }

    .slide {
        font-size: 16px;
        line-height: 1.218;
        padding-bottom: 62px;
        max-width: 1026px;
        margin: 0 auto;
        padding-top: 2px;
        text-align: left;

        ul, ol {
            padding: 0 0 0 20px;

            li {
                padding-bottom: 10px;
                //list-style: none;

                + li {
                    margin-top: 4px;
                }
            }

            + p {
                margin-top: 4px;
            }
        }

        @include breakpoints(none, s-large) {
            font-size: 14px;
            padding-bottom: 0;
            margin-bottom: 70px;
            overflow-y: auto;
            max-height: calc(100vh - 260px);
        }

        @include breakpoints(none, s-large) {
            max-height: calc(100vh - 230px);
        }

        p:not(:last-child) {
            margin: 0 0 20px;
        }

        a {
            color: #286440
        }
    }
}

.gm-results {
    padding: 0 24px;
    max-width: 1426px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}

.gm-drawer-opener {
    width: 90px;
    height: 29px;
    background: url(data:image/svg+xml;base64,);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    display: block;
    position: absolute;
    z-index: 1;
    pointer-events: auto;

    &:after {
        position: absolute;
        content: "";
        left: 50%;
        top: 50%;
        background-image: url(data:image/svg+xml;base64,);
        width: 20px;
        height: 11px;
        transform: translate3d(-50%, -50%, 0);
        transform-origin: 50% 50%;

        .gm-drawer-active & {
            transform: translate3d(-50%, -50%, 0) rotate(180deg);
        }
    }

    &.active {
        position: absolute;
        bottom: 0;
        background: url('data:image/svg+xml;base64,');
    }

    @include breakpoints(large) {
        display: none;
    }
}

.gm-sidebar-inner {

    position: relative;

    @include breakpoints(m-large) {
        padding: 55px 70px 0 0;
    }

    @include breakpoints(large) {
        padding: 50px 24px 50px 0;
        //padding: 0;
        max-height: calc(100vh - 50px);
        overflow-y: scroll;
        overflow-x: hidden;
    }
}

.growth-metrics-app {
    @include breakpoints(large) {
        &.is-result {
            background: #F6F7F7;

            .gm-content {
                background: #ffffff;
                position: relative;

                &:before {
                    content: "";
                    position: absolute;
                    right: -9999px;
                    left: 0;
                    top: 0;
                    height: 100%;
                    background: #ffffff;
                }
            }
        }
    }
}

.gm-content-inner {
    position: relative;
}

.gm-disclosures {
    color: $concrete;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0;
    text-align: left;

    p {
        margin-bottom: 15px;
    }
}

.gm-sidebar-wrapper {
    width: 350px;
    position: sticky;
    top: 51px;
    //z-index: 1;
    min-height: calc(100vh - 50px);

    @include breakpoints(none, m-large) {
        width: 270px;
    }

    @include breakpoints(none, s-large) {
        width: 100%;
        min-height: 0;
        position: fixed;
        top: 132px;
        left: 0;
        right: 0;
        padding: 0;
        z-index: 99;
        overflow: hidden;
        height: calc(100vh - 132px);
        //padding-bottom: 29px;
        pointer-events: none;

        .gm-active & {
            z-index: 0;
        }

        .gm-drawer-active & {
            pointer-events: auto;
        }
    }

    @include breakpoints(none, s-medium) {
        height: calc(100vh - 92px);
        top: 92px;
    }

    .gm-sidebar-item {
        + .gm-sidebar-item {
            margin-top: 35px;
        }
    }

    .gm-sidebar {
        &::before,
        &::after {
            @include breakpoints(none, s-large) {
                content: "";
                height: 1px;
                box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
                left: 0;
                right: 0;
                position: absolute;
                top: -1;
                z-index: -1;
                pointer-events: none;
            }
        }

        &::after {
            @include breakpoints(none, s-large) {
                content: none;


                .gm-drawer-active & {
                    content: "";
                    top: auto;
                    bottom: 29px;
                }
            }

            @include breakpoints(large) {
                content: "";
                position: absolute;
                left: -9999px;
                right: 0;
                top: 0;
                height: 100%;
                background: #F6F7F7;
                z-index: -1;
            }
        }

        .inner-wrap {
            @include breakpoints(none, s-large) {
                background: #F6F7F7;
                padding: 69px 24px 120px !important;
                //margin-top: -29px !important;
                overflow-y: auto;
                max-height: calc(100vh - 125px);
                transform: translate3d(0, -100%, 0);
                transition: transform 0.25s ease-out;

                .enable-announcement-bar {
                    max-height: calc(100vh - 256px);
                }

                .gm-drawer-active & {
                    transform: translate3d(0, 0, 0);
                }
            }

            @include breakpoints(none, s-medium) {
                max-height: calc(100vh - 92px);
            }

            @include breakpoints(none, small) {
                .enable-announcement-bar {
                    max-height: calc(100vh - 184px);
                }
            }

            @include breakpoints(large) {
                height: auto !important;
                padding: 0 !important;
                margin: 0 !important;
                display: block !important;
            }
        }

        h2 {
            font-size: 18px;
            line-height: 1.2;
            color: $steel;
            text-transform: uppercase;
            letter-spacing: 2.5px;
            font-weight: 800;
            display: block;
            padding-bottom: 8px;
            //padding-right: 40px;
            position: relative;
        }

        .slide {
            padding-top: 17px;
        }

        .header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1px solid $fog;
        }

        &-title {
            display: block;
            margin-bottom: 6px;
            color: $concrete;
            font-size: 14px;
            letter-spacing: -0.005em;
        }

        .quick-links-wrap {
            padding-left: 20px;
            padding-bottom: 10px;

            li {
                font-weight: 400;
            }
        }

        .quick-links {
            margin: 0 0 15px 0;
            padding: 0;
            list-style: none;
            font-size: 18px;
            line-height: 1.222;
            font-weight: 600;
            letter-spacing: -0.005em;

            li {
                color: $concrete;
                margin-bottom: 10px;

                a {
                    color: $concrete;

                    &:hover,
                    &.active {
                        color: $avocado;
                    }
                }
            }

            &.margin-bottom {
                margin-bottom: 30px;
            }
        }

        .tooltip {
            position: relative;
            top: -5px;
            cursor: pointer;

            i {
                width: 12px;
                height: 12px;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                background: $concrete;
                color: $fog;
                font-weight: 700;
                overflow: hidden;
                border-radius: 50%;
                font-size: 10px;
                line-height: 1;
                font-style: normal;
            }

            .tooltip-content {
                position: absolute;
                bottom: 100%;
                //left: 110px;
                transform: translateX(-50%);
                margin-bottom: 15px;
                background: #3C3E43;
                border-radius: 6px;
                //padding: 30px;
                font-weight: 600;
                font-size: 14px;
                line-height: 17px;
                letter-spacing: -0.5px;
                color: $fog;
                //white-space: nowrap;
                text-align: center;
                transition: opacity 0.3s linear;

                //@include breakpoints(none, s-medium) {
                white-space: normal;
                min-width: 200px;
                padding: 15px;
                left: 50px;
                //}

                pointer-events: none;

                &:after {
                    content: "";
                    top: calc(100% - 2px);
                    width: 0;
                    position: absolute;
                    left: 55px;
                    transform: translateX(-50%);
                    height: 0;
                    border-style: solid;
                    border-width: 10px 10px 0 10px;
                    border-color: #3C3E43 transparent transparent transparent;
                }
            }
        }

        .reset-button {
            display: flex;
            align-items: center;
            padding-top: 15px;

            cursor: pointer;
            font-size: 14px;
            letter-spacing: -0.005em;
            line-height: 22px;
            //color: $fog;
            color: #989aa5;
            opacity: 0.6;
            transition: opacity 0.2s ease-in-out;
            //z-index: 101;
            text-decoration: none;

            svg {
                margin-left: 5px;
            }

            &:hover {
                opacity: 1;
            }
        }
    }
}


.gm-content {
    flex: 1;
    padding: 35px 0 50px 98px;

    @include breakpoints(none, m-large) {
        padding: 50px 0 50px 24px;
    }

    @include breakpoints(none, s-large) {
        flex: none;
        width: 100%;
        padding: 71px 0 50px;
        position: relative;
        z-index: 0;
    }

    h2 {
        font-size: 20px;
        font-weight: 700;
        line-height: 52px;
        letter-spacing: -0.5px;
        text-transform: uppercase;

        @include breakpoints(none, s-large) {
            margin-bottom: 30px;
        }
    }

    h3 {
        font-size: 25px;
        line-height: 1.222;
        color: $avocado;
        font-weight: 800;
        margin-bottom: 21px;
        letter-spacing: -0.5px;
    }

    h4 {
        font-size: 24px;
        line-height: 1.083;
        letter-spacing: -0.005em;
        font-weight: 700;
        color: $steel;
        margin-bottom: 14px;
    }

    h5 {
        font-size: 20px;
        line-height: 1.2;
        letter-spacing: -0.01em;
        font-weight: 500;
        margin-bottom: 13px;
    }

    .text-intro {
        p {
            margin-bottom: 27px;
        }
    }

    .header-line {
        width: 100%;
        height: 1px;
        background: $fog;
        margin-bottom: 50px;
    }
}

.gm-articles-wrap {
    .gm-article-opener {
        padding: 0 0 30px;
        color: $concrete;
        position: relative;
        display: block;
        font-size: 30px;
        line-height: 1.04;
        font-weight: 700;
        letter-spacing: -0.5px;
        border-bottom: 1px solid $fog;
        text-transform: uppercase;
        margin-bottom: 30px;

        &::after {
            content: "";
            background-image: url('data:image/svg+xml;base64,');
            background-size: contain;
            background-repeat: no-repeat;
            height: 13px;
            width: 22px;
            display: block;
            position: absolute;
            right: 0;
            top: 3px;
        }

        &.active {

            &::after {
                transform: rotate(180deg);
            }
        }
    }
}

.gm-article {
    margin-bottom: 70px;

    .formula {
        margin: 30px 0;
        display: flex;
        justify-content: center;
        gap: 40px;

        @include breakpoints(none, small) {
            flex-direction: column;
            align-items: center;
            gap: 20px;
        }

        h6 {
            font-size: 20px;
            font-weight: 700;
            text-align: center;
            color: $concrete;
        }

        p {
            font-size: 16px;
            font-weight: 400;
            line-height: 19px;
            letter-spacing: 0;
            text-align: center;
            color: $steel;
        }

        .definition, .calculation {
            background: #FBFBFB;
            padding: 20px 40px;
            border: 1px solid #E5E6E5;
            border-top: 2px solid $avocado;

            flex: 1;
            display: flex;
            flex-direction: column;
            gap: 10px;
            align-items: center;
            width: 100%;
            //max-width: 400px;

            .content {
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                gap: 10px;
            }
        }

        .calculation {
            .row {
                display: flex;
                align-items: center;
                gap: 10px;
                width: 100%;
            }

            .column {
                width: 100%;
                display: flex;
                flex-direction: column;
                gap: 10px;
            }

            .bracket {
                color: $fog;
                font-size: 56px;
                font-weight: lighter;
            }

            .line {
                width: 100%;
                height: 2px;
                background-color: $fog;
            }

            .footnotes {
                display: flex;
                flex-direction: column;

                span {
                    font-size: 12px;
                    font-weight: 400;
                    text-align: center;
                    color: $concrete;
                }
            }

            .beyond-the-brackets,
            .operation {
                line-height: 0;
                white-space: nowrap;
            }
        }
    }
}

.gm-info-box {
    margin-bottom: 20px;

    h4 {
        font-size: 20px;
        font-weight: 700;
        line-height: 26px;
        letter-spacing: -0.005em;
        color: $concrete;
    }

    p {
        margin: 0;
        font-size: 16px;
        font-weight: 400;
        line-height: 19px;
        letter-spacing: 0;
        color: $steel;

        & + p {
            margin-top: 1em;
        }
    }
}

.gm-additional-info {
    padding-top: 20px;

    a[data-more] {
        font-size: 14px;
        font-weight: 800;
        line-height: 14px;
        letter-spacing: 0;

        color: white;
        padding: 11px 20px;
        background-color: $bayside;
        transition: border-color 0.3s ease-out, background-color 0.3s ease-out, color 0.3s ease-out;

        @include breakpoints(none, small) {
            width: 100%;
            display: inline-block;
            text-align: center;
        }

        &:hover {
            background-color: #3FB4ED;
        }

        &.active {
            color: $fog;
            background-color: $white;
            border: 1px solid $fog;

            &:focus {
                color: $fog;
                border-color: $fog;
            }

            &:hover {
                color: #5BC3F5;
                border-color: #5BC3F5;
            }
        }
    }

    .slide {
        padding-bottom: 40px;
        margin-top: -14px;
    }

    .info-list {
        margin: 0 0 20px;
        padding: 0;
        list-style: none;

        .title {
            letter-spacing: -0.3px;
            font-size: 16px;
            line-height: 1.625;
            font-weight: 700;
            display: block;
            margin-bottom: 4px;
            color: $poppy;
        }

        li {
            margin-bottom: 12px;
            //padding-right: 50px;
            border-bottom: 1px solid $fog;
            padding-bottom: 15px;

            //@include breakpoints(none, small) {
            //    padding-right: 0;
            //}

            p {
                margin: 0;

                & + p {
                    margin-top: 1em;
                }
            }
        }

        a {
            color: #5bc3f5;
        }
    }

}

.gm-revenue-scale {
    display: flex;
    justify-content: space-between;
    list-style: none;
    gap: 10px;
    margin: 0;
    padding: 0;
    position: relative;
    padding-top: 23px;
    margin-top: 20px;

    &::after {
        content: "";
        height: 2px;
        background: #E5E6E5;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;

    }

    input[type="radio"] {
        display: none;

        &:checked + .option {
            color: $avocado;

            &::before {
                background: white;
                border-color: $avocado;
            }
        }
    }

    .option {
        display: inline-block;
        vertical-align: top;
        position: relative;
        cursor: pointer;
        color: $concrete;
        font-size: 14px;
        line-height: 1.142;
        transition: color 0.25s linear;

        &:hover {
            color: $avocado;

            &::before {
                background: white;
                border-color: $avocado;
            }
        }

        &::before {
            content: "";
            width: 15px;
            height: 15px;
            border-radius: 50%;
            border: 1px solid transparent;
            background: #E5E6E5;
            z-index: 1;
            position: absolute;
            top: -29px;
            left: 50%;
            transform: translateX(-50%);
            transition: background 0.25s linear, border-color 0.25s linear;
        }
    }

    li {
        width: 33.333%;
        text-align: center;

        &:first-child {
            text-align: left;

            .option::before {
                left: 0;
                transform: none;
            }
        }

        &:last-child {
            text-align: right;

            .option::before {
                right: 0;
                left: auto;
                transform: none;
            }
        }
    }
}

.gm-bar-chart {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    margin-bottom: 45px;
    height: 250px;
    //clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    position: relative;

    @include breakpoints(none, s-large) {
        max-width: 500px;
        margin: 0 auto 50px;
        height: 180px;
    }

    .bar {
        width: 158px;
        position: relative;
        text-align: center;
        color: white;
        transition: all 0.5s ease-in-out;

        @include breakpoints(none, s-large) {
            width: 25%;
        }

        &.bar-sm {
            .data {
                top: auto;
                bottom: 100%;
                margin-bottom: 10px;
            }
        }

        &.bar-xs {
            .data {
                top: auto;
                bottom: 100%;
                margin-bottom: 50px;

                @include breakpoints(none, s-large) {
                    margin-bottom: 43px;

                    &.push-back {
                        margin-bottom: 60px;
                    }
                }
            }

            .info {
                bottom: 100%;
                margin-bottom: 10px;
            }
        }

        &.bar-negative {
            .data {
                bottom: 18px;
                top: auto;
            }

            .info {
                margin-top: 10px;
                top: 10px;
                bottom: auto;
            }

            &.bar-sm {
                .data {
                    top: 100%;
                    bottom: auto;
                    margin-top: 10px;
                }
            }

            &.bar-xs {
                .data {
                    top: 100%;
                    bottom: auto;
                    margin-top: 50px;

                    @include breakpoints(none, s-large) {
                        margin-top: 43px;

                        &.push-back {
                            margin-top: 60px;
                        }
                    }
                }

                .info {
                    top: 100%;
                    margin-top: 10px;
                }
            }
        }

        &.deep-ocean {
            background: $deep_ocean;

            &.bar-sm {
                .data {
                    color: $deep_ocean;
                }
            }

            &.bar-xs {
                .data,
                .info {
                    color: $deep_ocean;
                }
            }
        }

        &.marina {
            background: $marina;

            &.bar-sm {
                .data {
                    color: $marina;
                }
            }

            &.bar-xs {
                .data,
                .info {
                    color: $marina;
                }
            }
        }

        &.bayside {
            background: $bayside;

            &.bar-sm {
                .data {
                    color: $bayside;
                }
            }

            &.bar-xs {
                .data,
                .info {
                    color: $bayside;
                }
            }
        }

        &.avocado {
            background: $avocado;

            &.bar-sm {
                .data {
                    color: $avocado;
                }
            }

            &.bar-xs {
                .data,
                .info {
                    color: $avocado;
                }
            }
        }

        .data {
            font-size: 30px;
            font-weight: 700;
            line-height: 1;
            letter-spacing: -0.5px;
            position: absolute;
            left: 0;
            right: 0;
            top: 30px;
            padding: 0 20px;
            transition: all 0.5s 0.10s ease-in-out;

            @include breakpoints(none, s-large) {
                font-size: 22px;
                top: 18px;
                padding: 0 10px;
            }

            &.squeeze {
                font-size: 26px;

                @include breakpoints(none, s-large) {
                    font-size: 16px;
                }
            }
        }

        .info {
            font-size: 14px;
            line-height: 16px;
            letter-spacing: -0.5px;
            font-weight: 500;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 20px;
            padding: 0 30px;
            transition: all 0.5s 0.15s ease-in-out;

            @include breakpoints(none, s-large) {
                padding: 0 8px;
                font-size: 13px;
                bottom: 14px;
            }

            //@include breakpoints(none, 480px) {
            //    padding: 0 8px;
            //}
        }
    }

    &.is-hidden {
        .bar {
            height: 0;

            .data,
            .info {
                opacity: 0;
            }
        }
    }
}

.gm-inline-chart-wrapper {
    display: flex;
    justify-content: center;
    padding: 33px 20px;
    width: 100%;

    .gm-inline-chart {
        display: flex;
        justify-content: center;
        background: linear-gradient(to left, #93CA15 31.51%, #07A6A9 34.52%, #00A1B2 67%, #0057BF 70.73%);
        margin: 86px auto 69px;
        position: relative;
        width: 100%;
        transition: all 0.5s ease-in-out;

        @include breakpoints(none, s-large) {
            margin-top: 70px;
        }

        &::before,
        &::after {
            content: "";
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 41px 36px 41px 0;
            border-color: transparent #0056bf transparent transparent;
            position: absolute;
            left: -25px;
            top: 50%;
            transform: translateY(-50%);

            @include breakpoints(none, s-large) {
                border-width: 28px 26px 28px 0;
            }
        }

        &::after {
            border-width: 41px 0 41px 36px;
            border-color: transparent transparent transparent #94ca15;
            left: auto;
            right: -25px;

            @include breakpoints(none, s-large) {
                border-width: 28px 0 28px 26px;
            }
        }

        .bar {
            width: 33.333%;
            height: 44px;
            position: relative;
            text-align: center;
            color: white;
            transition: all 0.5s ease-in-out;

            @include breakpoints(none, s-large) {
                height: 30px;
            }

            &.deep-ocean {
                .data {
                    color: $deep_ocean;
                }
            }

            &.marina {
                .data {
                    color: $marina;
                }
            }

            &.avocado {
                .data {
                    color: $avocado;
                }
            }

            .data {
                font-size: 30px;
                font-weight: 700;
                line-height: 1;
                letter-spacing: -0.5px;
                position: absolute;
                left: 0;
                right: 0;
                top: -40px;

                @include breakpoints(none, s-large) {
                    font-size: 20px;
                    top: -27px;
                }

                &.squeeze {
                    font-size: 26px;

                    @include breakpoints(none, s-large) {
                        font-size: 16px;
                    }
                }
            }

            .info {
                font-size: 20px;
                letter-spacing: -0.3px;
                font-weight: 400;
                line-height: 44px;
                opacity: 0.8;

                @include breakpoints(none, s-large) {
                    line-height: 30px;
                    font-size: 13px;
                }
            }
        }

        &.is-hidden {
            width: 0;
            opacity: 0;

            .bar {
                .data,
                .info {
                    opacity: 0;
                }
            }
        }
    }
}

.gm-related-reading {

    .related-reading-row {
        margin: 0 -13px;
        display: flex;
    }

    .col {
        padding: 13px;
        width: 33.333%;

        @include breakpoints(none, s-medium) {
            width: 100%;
        }
    }

    + {
        h4,
        .h4 {
            margin-bottom: 30px;
        }
    }

    .title {
        display: block;
        font-size: 26px;
        font-weight: 500;
        color: gray;
        letter-spacing: -1px;
        margin-bottom: 17px;
        line-height: 1.166;
    }

    .related-item {
        line-height: 1;
        font-weight: 600;
        display: flex;
        position: relative;
        color: $steel;
        border: 1px solid $fog;
        transition: border-color 0.25s linear;
        min-height: 106px;
        letter-spacing: -0.5px;
        padding: 19px 26px 22px;
        margin-bottom: 0;
        height: 100%;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        background: none;

        @include breakpoints(none, s-medium) {
            display: block;
            height: auto;
        }

        &:hover {
            border-color: $avocado;
        }

        .description {
            flex-grow: 1;
            padding-bottom: 36px;
        }

        h6 {
            font-size: 18px;
            line-height: 22px;
            letter-spacing: -0.5px;
            margin-bottom: 14px;
        }

        p {
            &:last-of-type {
                margin-bottom: 0;
            }
        }

        .action {
            color: $bayside;
            font-weight: 800;
            font-size: 12px;
            text-transform: uppercase;
            letter-spacing: 0.2em;
            position: static;
            padding: 0;

            @include breakpoints(none, s-medium) {
                padding: 30px 0 0;
                display: block;
            }
        }

        .subtitle {
            display: block;
            margin-bottom: 5px;
            color: $concrete;
            letter-spacing: -0.005em;

            @include breakpoints(none, s-medium) {
                margin-bottom: 14px;
            }
        }
    }
}
