/*
 * M100 Table Styles
 */

/* stylelint-disable */
/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */

.wp-content {

	.table-wrap {
		margin: 0;
	}

	.table-head {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;
		margin-bottom: rem(30);
		font-size: rem(14);
		line-height: 1.571;
		position: relative;
		z-index: 2;

		* {
			margin: 0;
		}

		.show {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.title {
				font-weight: 400;
				margin-right: rem(15);
			}
		}

		.filter {
			position: relative;
			text-align: left;
		}

		.opener {
			display: block;
			width: 191px;
			border: 1px solid $fog;
			text-decoration: none;
			padding: rem(5 42 5 16);
			position: relative;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;

			&:hover {
				color: $steel;
			}

			&:after {
				content: "\e91c";
				font: 7px/1 "icomoon";
				position: absolute;
				right: 16px;
				top: 50%;
				transform: translateY(-50%);
				transition: transform 0.3s linear;
			}
		}

		.filter-drop {
			border: 1px solid $fog;
			padding: rem(10);
			position: absolute;
			top: 100%;
			left: 0;
			right: 0;
			background: $white;
			margin-top: -2px;
			opacity: 0;
			visibility: hidden;
			transition: opacity 0.3s linear, visibility 0.3s linear;
		}

    .table-filter {
      &.active  {
        .filter-drop {
          opacity: 1;
          visibility: visible;
        }

        .opener {
          &:after {
            transform: translateY(-50%) rotate(180deg);
          }
        }
      }
    }
	}

	.filter-search {
		margin-bottom: rem(7);
    display: none;

		.input-wrap {
			border: 1px solid $fog;
			height: 29px;
			padding: rem(2 27);
			position: relative;

			label {
				position: absolute;
				left: 8px;
				top: 50%;
				transform: translateY(-50%);
				width: 12px;
				height: 12px;

				&:before {
					content: "\e91d";
					font: 12px/1 "icomoon";
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					color: $concrete;
					transition: color 0.3s linear;
				}
			}

			input[type="search"] {
				border: none;
				outline: none;
				width: 100%;
				height: 100%;
				color: $concrete;

				&.active,
				&:focus {

					+ label:before {
						color: $poppy;
					}

					~ .close {
						opacity: 1;
						visibility: visible;
					}
				}
			}

			input[type="search"]::-webkit-input-placeholder,
			input[type="search"]::-moz-placeholder,
			input[type="search"]:-ms-input-placeholder,
			input[type="search"]:-moz-placeholder {
				color: $concrete;
			}

			input[type="search"]::-webkit-search-decoration,
			input[type="search"]::-webkit-search-cancel-button,
			input[type="search"]::-webkit-search-results-button,
			input[type="search"]::-webkit-search-results-decoration {
				display: none;
			}

			.close {
				position: absolute;
				right: 10px;
				top: 50%;
				transform: translateY(-50%);
				width: 8px;
				height: 8px;
				text-decoration: none;
				opacity: 0;
				visibility: hidden;
				transition: opacity 0.3s linear, visibility 0.3s linear;

				&:before {
					content: "\e91e";
					font: 8px/1 "icomoon";
					top: 50%;
					left: 50%;
					position: absolute;
					transform: translate(-50%, -50%);
					color: $steel;
					transition: color 0.3s linear;
				}

				&:hover {
					color: $poppy;
				}
			}
		}
	}

	.filter-checklist-wrap {
		height: 260px;
		margin: 0 -7px;
		padding-left: 7px;

		.mCSB_scrollTools {
			width: 4px;

			 .mCSB_dragger {
			 	background: #e5e6e5;
			 	border-radius: 10px;
			 }
		}
	}

	.filter-checklist {
		margin: 0;
		padding: 0;
		list-style: none;
		color: $steel;

		li {

			+ li {
				margin-top: rem(3);
			}

			&.disabled {
				opacity: 0.3;
				pointer-events: none;
			}
		}

		label {
			padding-left: rem(28);
			line-height: 1.714;
		}

		.fake-input {
			top: 2px;

			&:before {
				content: "\e91b";
				border: none;
				width: auto;
				height: auto;
				font: 8px/1 "icomoon";
				color: $steel;
				transform: translate(-50%, -50%);
				margin: 0;
			}
		}

		.fake-label {
			line-height: 23px;
		}
	}

	.btn-wrap {
		margin-top: rem(13);

		.btn {
			font-size: rem(14);
			line-height: 1;
			font-weight: 500;
			padding: rem(8 12);
			display: block;
			width: 100%;

			&:hover,
			&:focus {
				color: $white;
			}
		}
	}

	.clear-filter {
		display: flex;
		justify-content: flex-end;
		margin-top: rem(4);

		a {
			text-decoration: none;
			color: $bayside;
			transition: color 0.3s linear;

			&:hover {
				color: $poppy;
			}
		}
	}

	.m100-table-body-wrap {
		@media only screen and (max-width: 1023px) {
			height: auto;
		}

		* {
			margin: 0;
		}

    @media only screen and (min-width: 1024px) {
      &[aria-expanded] {
        overflow: hidden;
        margin-bottom: 0;
        max-height: 720px;
      }

      &[aria-expanded="true"] {
        max-height: none;
      }
    }

		.mCSB_inside {

			> .mCSB_container {
				margin-right: 0;
			}
		}

		.mCSB_scrollTools {
			width: 6px;

			 .mCSB_dragger {
			 	background: #e5e6e5;
			 	border-radius: 10px;
			 }
		}
	}
}

.m100-table-wrapper {
  margin-bottom: rem(32);
}

.wp-content [class*="m100-table-"],
[class*="m100-table-"], {
	border-collapse: collapse;
	border-spacing: 0;
	text-align: center;
	font-size: rem(14);
	line-height: 1.42;
	color: $steel;
	min-width: 1px;
	width: 100%;

	@media only screen and (max-width: 1023px) {
		display: block;
	}

	thead {

		@media only screen and (max-width: 1023px) {
			display: block;
		}
	}

	tr {

		@media only screen and (max-width: 1023px) {
			display: flex !important;
			align-items: center;
		}
	}

	tbody {

		@media only screen and (max-width: 1023px) {
			display: block;
		}

		tr {

			@media only screen and (max-width: 1023px) {
				min-height: 95px;
			}

			&:nth-child(even) {

				td {
					background: $gray9;
				}
			}
		}

		.slick-slide {

			> div:nth-child(even) {

				@media only screen and (max-width: 1023px) {
					background: $gray9;
				}
			}
		}
	}

	td,
	th {
		width: 58px/864px * 100%;
		padding: 9px 10px;
		text-align: center;

		@media only screen and (max-width: 1023px) {
			width: 30%;
		}

		+ td,
		+ th {
			width: 155px/864px * 100%;

			@media only screen and (max-width: 1023px) {
				width: 40%;
			}

			+ td,
			+ th {
				width: 191px/864px * 100%;

				@media only screen and (max-width: 1023px) {
					display: none;
				}

				+ td,
				+ th {
					width: 178px/864px * 100%;

					@media only screen and (max-width: 1023px) {
						width: 30%;
						display: block;
					}

					+ td,
					+ th {
						width: 112px/864px * 100%;

						@media only screen and (max-width: 1023px) {
							display: none;
						}

						+ td,
						+ th {
							width: 170px/864px * 100%;

							@media only screen and (max-width: 1023px) {
								display: none;
							}
						}
					}
				}
			}
		}
	}

	th {
		font-weight: 700;
		border-bottom: 2px solid #e5e6e5;
	}

	td {
		border: none;
		padding-top: 10px;
		padding-bottom: 10px;
		height: 72px;

		@media only screen and (max-width: 1023px) {
			padding-top: 5px;
			padding-bottom: 5px;
			height: auto;
		}
	}

	img {
		display: inline-block;
		vertical-align: middle;
		margin: 0;
	}

  .badge-new {
    padding: 3px 6px;
    color: $deep_ocean;
    font-weight: 700;
    text-transform: uppercase;
  }

	.down,
	.up {
		display: inline-block;
		vertical-align: top;
		color: $pine;
		position: relative;
		padding-left: 15px;

		&:before {
			content: "";
			background: url('/wp-content/themes/a16z-2015/client/images/m100/arrow-up.svg') no-repeat 0 0 / 8px 12px;
			width: 8px;
			height: 12px;
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
		}
	}

	.down {
		color: $cable_car;

		&:before {
			background-image: url('/wp-content/themes/a16z-2015/client/images/m100/arrow-down.svg');
		}
	}

	.website-link {
		text-decoration: none;
		color: $bayside;
		transition: color 0.3s linear;

		&:hover {
			color: $poppy;
		}
	}

	.yoy-change {
		display: inline-block;
		vertical-align: top;
		margin: 0 0 0 12px;

		@media only screen and (min-width: 1024px) {
			display: none !important;
		}
	}

	.website {
		margin: 10px 0 0;

		@media only screen and (min-width: 1024px) {
			display: none !important;
		}
	}
}

.wp-content .nav-table,
.nav-table {
	margin-bottom: 2rem;
	margin-top: rem(40);
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0 20px;
	position: relative;

	@media only screen and (min-width: 1024px) {
		display: none;
	}

	* {
		margin: 0;
	}

	.slick-arrow {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 0;
		display: block;
		padding: 0;
		background: none;
		border: none;
		font-size: 0;
		line-height: 0;
		width: 14px;
		height: 14px;
		outline: none;

		&:before {
			content: "";
			font: 13px/1 "icomoon";
			color: $steel;
			transition: color 0.3s linear;
		}

		&:hover:before {
			color: $poppy;
		}

		&.slick-prev:before {
			content: "\e91f";
		}

		&.slick-next {
			left: auto;
			right: 0;

			&:before {
				content: "\e920";
			}
		}
	}

	.slick-dots {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin: -7px;
		padding: 0;
		list-style: none;

		@media only screen and (max-width: 374px) {
			margin: -3px;
		}

		li {
			margin: 0;
			padding: 7px;

			@media only screen and (max-width: 374px) {
				padding: 3px;
			}

			&.slick-active button,
			button:hover {
				background: $poppy;
			}
		}

		button {
			display: block;
			width: 12px;
			height: 12px;
			line-height: 1;
			border-radius: 50%;
			background: $fog;
			padding: 0;
			border: none;
			font-size: 0;
			line-height: 0;
			transition: background 0.3s linear;
			outline: none;
		}
	}
}

.table-expand {
  appearance: none;
  background: none;
  border: $border-light;
  cursor: pointer;
  display: block;
  font-size: rem(14);
  margin-top: spacer(1);
  padding: rem(12);
  text-transform: capitalize;
  width: 100%;
  outline: none;

  @media only screen and (max-width: 1023px) {
    display: none;
  }

  [aria-expanded="true"] + & {
    span {
      &:nth-child(2) {
        display: none;
      }
    }
  }

  [aria-expanded="false"] + & {
    span {
      &:nth-child(1) {
        display: none;
      }
    }
  }

  &:hover,
  &:focus {
    border-color: $poppy;
    color: $poppy;
  }
}
