/* stylelint-disable selector-max-specificity, value-list-comma-space-after */

.page-template-page_template-crypto-quarterly,
.page-template-page_template-crypto-quarterly-static {
    @extend .page-template-page_template-crypto-startup-school-demo;

    #footer {
        background: #F2F2F2;
        margin-top: 0;
    }

    .container {
        margin-bottom: 0;
    }

    .content-new {
        > .container {
            background: linear-gradient(180deg, rgba(89,71,152,1) 0%, rgba(86,75,153,1) 22%, rgba(3,173,189,1) 100%);
        }
    }
}

%bgpattern {
    position: relative;
    z-index: 1;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        pointer-events: none;
        background: url('#{$image-path}/bg-pattern1.png') no-repeat 100% 0 / auto 100%;
        z-index: -1;
    }
}

@mixin bgpattern($opacity:0.2) {
    @extend %bgpattern;

    &::after {
        opacity: $opacity;
    }
}

.iframe-responsive {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56.25%;

  iframe {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
  }
}

.cq {
    &-wrapper {
        min-height: 100vh;
        background-size: cover;
        background-position: 50% 0;
        background-repeat: no-repeat;
        background-image: url('#{$image-path}/bg-crypto-page.png');
        padding-top: 192px;
        color: #40474f;

        @include breakpoints(none, s-large) {
            background: linear-gradient(180deg, rgba(89,71,152,1) 0%, rgba(86,75,153,1) 22%, rgba(3,173,189,1) 100%);
            padding-top: 100px;
        }

        .content-wysiwyg {
            a {
                color: #40474f;
                text-decoration: none;
                background-image: linear-gradient(transparent 2px, $poppy 2px, $poppy 14px, transparent 4px),
                linear-gradient(transparent 2px, #00ADBA 2px, #00ADBA 14px, transparent 4px);
                background-size: 0% 18px, 100% 18px;
                background-position: 0 bottom, 0 bottom;
                transition: background-size 0.3s ease-in-out;
                background-repeat: no-repeat;
                padding-bottom: 4px;
                border-bottom: 6px solid transparent;

                &:hover {
                    background-size: 100% 18px;
                }
            }

            h1 {
                margin-bottom: rem(20);
            }

            h2,
            h3,
            h4,
            h5 {
                margin-bottom: rem(6);

                @include breakpoints(medium) {
                    margin-bottom: rem(20);
                }
            }

            figure {
                max-width: 50%;
                margin-bottom: 1em;

                @include breakpoints(none, s-large) {
                    max-width: 100%;
                }

                img {
                    height: auto;
                    width: 100%;
                }
            }

            .wp-caption {
                height: auto;
            }

            // Alignment
            .alignleft {
                display: inline-block;
                float: left;
                margin-right: rem(32);
            }

            .alignright {
                display: inline-block;
                float: right;
                margin-left: rem(32);
            }

            .aligncenter {
                clear: both;
                display: block;
                margin-left: auto;
                margin-right: auto;
                max-width: 100%;
            }

            .has-trailing-dot:not(.has-image-only) {
                &::after {
                    content: '';
                    display: inline-block;
                    width: 12px;
                    height: 12px;
                    background: #00adba;
                    margin-left: 10px;
                }
            }
        }

        .wp-caption-text {
            color: #868b90;
            font-size: 16px;
            font-weight: 400;
            line-height: 25px;
            margin-top: rem(10);

            @include breakpoints(medium) {
                font-size: 18px;
            }

            @include breakpoints(large) {
                font-size: 20px;
            }

            @include breakpoints(none, s-medium) {
                text-align: left;
                margin-right: auto;
            }
        }

        a {
            text-decoration: none;
        }

        img {
            vertical-align: top;
        }

        h3 {
            color: #00adba;
            margin-bottom: 20px;
            font-size: 20px;

            @include breakpoints(none, s-medium) {
                margin-bottom: 10px;
            }
        }

        .text-left {
            text-align: left;
        }

        .text-center {
            text-align: center;
        }

        .text-right {
            text-align: right;
        }

        .link-arrow {
            color: #3f464e;
            // stylelint-disable-next-line declaration-no-important
            padding: 0 20px 0 5px !important;
            font-size: 16px;
            font-weight: 700;
            text-transform: uppercase;
            text-decoration: none;
            position: relative;

            &::after {
                content: '';
                position: absolute;
                top: 8px;
                right: 4px;
                width: 0;
                height: 0;
                border-style: solid;
                border-color: #231f20 transparent transparent transparent;
                border-width: 8px 4.5px 0 4.5px;
            }
        }

        .link-line {
            //background-image: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(84,200,209,0) 65%,rgba(81,199,208,1) 65%,rgba(0,173,186,1) 100%);
            text-decoration: none;
            background-image: linear-gradient(transparent 2px, $poppy 2px, $poppy 12px, transparent 4px),
            linear-gradient(transparent 2px, #00ADBA 2px, #00ADBA 12px, transparent 4px);
            background-size: 0% 10px, 100% 10px;
            background-position: 0 bottom, 0 bottom;
            transition: background-size 0.3s ease-in-out;
            background-repeat: no-repeat;
            padding-bottom: 4px;
            border-bottom: 6px solid transparent;

            &:hover {
                background-size: 100% 10px;
            }
        }

        .link-video {
            margin-left: 40px;

            &::before {
                content: '';
                width: 30px;
                height: 30px;
                border-radius: 50%;
                border: 4px solid #656161;
                background: $white;
                position: absolute;
                left: -50px;
                top: -4px;
            }

            &::after {
                content: '';
                position: absolute;
                top: 6px;
                left: -39px;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 5px 0 5px 11px;
                border-color: transparent transparent transparent #00aeba;
            }
        }

        .link-u {
            color: $white;
            font-weight: 700;
            letter-spacing: 0.05em;
            padding: 0 5px;
            font-size: 18px;

            @include breakpoints(none, s-medium) {
                font-size: 16px;
            }
        }
    }

    &-container {
        max-width: 1650px;
        padding: 0 100px;
        margin: 0 auto;

        @include breakpoints(none, s-large) {
            padding: 0 50px;
        }

        @include breakpoints(none, s-medium) {
            padding: 0 20px;
        }
    }

    &-txt-bg {
        position: relative;
        z-index: 1;
        display: inline-block;
        vertical-align: top;
        letter-spacing: 0.03rem;
        padding: 5px 20px 5px 0;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: -1;
        }
    }

    &-dot {
        width: 7px;
        height: 7px;
        background: #00adba;
        display: inline-block;
        vertical-align: middle;
        margin: 0 10px;

        @include breakpoints(none, s-medium) {
            margin-top: -1px;
        }

        &.lg {
            width: 15px;
            height: 15px;
        }
    }

    &-typo {
        margin: 0 0 46px;

        .img {
            margin-bottom: 30px;
        }

        .caption {
            text-align: right;
            margin-right: -33px;

            @include breakpoints(none, s-medium) {
                margin-right: 0;
            }

            > .cq-txt-bg {
                padding: 5px 0 5px 35px;
                color: $white;
                font-size: 40px;
                font-weight: 700;
                letter-spacing: 0.9em;
                text-transform: uppercase;

                @include breakpoints(none, s-medium) {
                    font-size: 16px;
                }

                &::after {
                    right: -9999px;
                    background: #00adba;
                }
            }
        }
    }

    &-content {
        position: relative;
        font-size: 20px;
        line-height: 1.2;
        padding-bottom: 80px;

        .btn {
            width: 100%;
            display: block;
            color: $white;
            background: #5c499d;
            padding: 6px 20px;
            box-shadow: 10px 10px 0 rgba(#130c0d, 0.3);
            text-transform: uppercase;
            font-size: 24px;
            font-weight: 700;
            text-align: center;
        }
    }

    &-post {
        padding: 0 56px 33px;

        @include breakpoints(none, s-medium) {
            padding: 0 20px 30px;
        }

        &.alt {
            margin-bottom: 31px;
        }
    }

    &-volume {
        margin-bottom: 31px;
        box-shadow: 10px 10px 0 rgba(#130c0d, 0.3);
    }

    &-post,
    &-volume {
        line-height: 1.429;
        position: relative;
        background: $white;
        font-size: rem(20);

        @include breakpoints(medium) {
            font-size: rem(24);
        }

        .cq-date {
            padding: 5px 20px 5px 0;
            color: $white;
            position: relative;
            font-size: 18px;
            text-transform: uppercase;
            margin-left: -56px;
            margin-bottom: 11px;

            @include breakpoints(none, s-medium) {
                margin-left: -18px;
                font-size: 16px;
            }

            &::after {
                left: -45px;
                background: #0c71b8;
            }
        }

        .toc {
            font-size: 20px;
            line-height: 1.2;
            color: #868b90;
            margin-bottom: 0;
            border-left: 0;
            padding: 0;
            display: block;

            @include breakpoints(none, s-large) {
                font-size: 18px;
            }

            @include breakpoints(none, s-medium) {
                font-size: 16px;
            }

            a {
                color: #868b90;

                &:hover {
                    color: $poppy;
                }
            }

            .title {
                font-size: 20px;
                font-weight: 700;
                margin-right: 5px;
                color: #00adba;
                display: inline-block;
                border: 0 none;
                margin-bottom: 0;
                padding-bottom: 0;

                @include breakpoints(none, s-medium) {
                    font-size: 16px;
                }
            }
        }

        &-hero {
            height: 504px;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            margin: 0 -56px;
            position: relative;

            @include breakpoints(none, s-medium) {
                height: 300px;
            }

            &::before {
                content: '';
                height: 30px;
                background: #231f20;
                position: absolute;
                top: 0;
                left: -46px;
                right: -46px;
                box-shadow: 10px 10px 0 rgba(#231f20, 0.3);
            }

            + .cq-post-heading {
                margin-top: -140px;
            }
        }

        &-heading {
            margin-bottom: 15px;

            .category-title {
                font-size: 30px;
                letter-spacing: 0.05em;
                text-transform: uppercase;
                margin: 0;
                display: block;
                font-weight: 700;

                @include breakpoints(none, s-large) {
                    font-size: 24px;
                }

                @include breakpoints(none, s-medium) {
                    font-size: 20px;
                }

                .cq-txt-bg {
                    padding: 5px 30px 3px 0;
                    color: $white;

                    &::after {
                        left: -56px;
                        background: #00adba;
                    }
                }
            }

            &.t-border {
                padding-top: 81px;
                border-top: 2px solid #cacbce;

                @include breakpoints(none, s-medium) {
                    padding-top: 40px;
                }
            }
        }

        &-headline {
            margin-bottom: 28px;
            line-height: 1.213;
            letter-spacing: 0.02em;
            position: relative;

            @include breakpoints(none, s-medium) {
                margin-bottom: 20px;
            }

            &.sm {
                .inner {
                    font-size: 35px;
                }
            }

            br {
                @include breakpoints(none, 1399px) {
                    display: none;
                }
            }

            .inner {
                background: #130c0d;
                color: $white;
                font-size: 47px;
                font-weight: 400;
                position: relative;
                z-index: 1;
                padding: 5px 0 5px 73px;
                margin-left: -73px;
                box-shadow: 30px 0 0 #130c0d, -30px 0 0 #130c0d;
                box-decoration-break: clone;

                @include breakpoints(none, s-large) {
                    font-size: 32px;
                    box-shadow: 15px 0 0 #130c0d, -15px 0 0 #130c0d;
                }

                @include breakpoints(none, s-medium) {
                    font-size: 24px;
                }
            }

            .inner-text {
                position: relative;
                z-index: 3;
            }
        }

        &-author {
            display: block;
            color: #a0a3a7;
            font-size: 25px;

            @include breakpoints(none, s-large) {
                font-size: 20px;
            }

            @include breakpoints(none, s-medium) {
                font-size: 16px;
            }

            .cq-dot {
                margin: 0 3px;
            }
        }

        &-foot {
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-top: 1px solid #cacbce;
            padding-top: 20px;
        }

        .first-letter {
            font-size: 205px;
            float: left;
            line-height: 0.9;
            color: #00adba;
            margin-right: 32px;
            font-family: $lust;

            @include breakpoints(none, s-large) {
                font-size: 100px;
                margin-right: 15px;
            }
        }

        .cq-img {
            margin-top: 10px;
            width: 100%;
            max-width: 100%;
            margin-bottom: 25px;

            @include breakpoints(medium) {
                max-width: 50%;
                margin-bottom: 40px;
            }

            &.item-center {
                max-width: 100%;
                width: auto;
            }

            &.item-size-large {
                @include breakpoints(medium) {
                    max-width: 50%;
                }

                @include breakpoints(m-large) {
                    max-width: 63%;
                }
            }

            &.item-size-small {
                @include breakpoints(medium) {
                    max-width: 40%;
                }
            }

            img {
                width: 100%;
            }
        }

        .cq-video {
            max-width: 648px;
            width: 100%;

            @include breakpoints(none, s-medium) {
                max-width: 100%;
            }
        }

        p {
            margin-bottom: 41px;

            @include breakpoints(none, s-medium) {
                margin-bottom: 20px;
            }
        }

        .item-center {
            text-align: center;

            .wp-caption-text {
                text-align: left;
            }
        }

        .item-left {
            float: left;
            margin-right: rem(32);
            margin-bottom: rem(25);

            @include breakpoints(none, s-medium) {
                float: none;
                margin-right: 0;
            }
        }

        .item-right {
            float: right;
            margin-left: rem(32);
            margin-bottom: rem(25);

            @include breakpoints(none, s-medium) {
                float: none;
                margin-left: 0;
            }
        }

        .txt-row {
            padding-bottom: 40px;
            border-bottom: 2px solid #cacbce;
            margin-bottom: 40px;

            @include breakpoints(none, s-medium) {
                padding-bottom: 20px;
                margin-bottom: 20px;
            }

            &.no-border {
                border-bottom: none;
                padding-bottom: 0;
            }
        }
    }

    &-widget {
        background-color: #150e0f;
        padding: 30px 40px 45px 40px;
        width: 424px;
        color: $white;
        font-size: 22px;
        line-height: 1.364;
        margin-bottom: 30px;
        position: relative;

        @include bgpattern(1);

        @include breakpoints(none, s-large) {
            font-size: 20px;
            width: 350px;
        }

        @include breakpoints(none, s-medium) {
            font-size: 18px;
            width: auto;
        }

        &::after {
            background-image: url('#{$image-path}/bg-pattern2.png');
        }

        .cq-widget-title {
            display: inline-block;
            position: relative;
            font-size: 28px;
            font-weight: 700;
            padding: 7px 25px 5px 0;
            margin: 0 0 23px;
            z-index: 1;
            letter-spacing: 0.02em;
            color: $white;

            @include breakpoints(none, s-large) {
                font-size: 24px;
            }

            @include breakpoints(none, s-medium) {
                font-size: 20px;
            }

            &::after {
                content: '';
                position: absolute;
                top: 0;
                left: -66px;
                bottom: 0;
                right: 0;
                background: #5c499d;
                z-index: -1;
            }
        }

        a {
            color: $white;
        }

        .link-u {
            position: absolute;
            right: 40px;
            top: 48px;

            @include breakpoints(none, s-medium) {
                top: 37px;
            }
        }

        .txt-wrap {
            max-width: 290px;

            p {
                margin-bottom: 38px;

                @include breakpoints(none, s-medium) {
                    margin-bottom: 25px;
                }
            }
        }

        .link {
            color: $white;
            text-transform: uppercase;
            position: relative;
            z-index: 1;
            text-decoration: none;
            padding: 0 7px;
            margin: 0 -7px;
        }

        .news-list {
            @extend %listreset;

            li {
                padding-bottom: 23px;
                margin-bottom: 25px;
                position: relative;

                &::after {
                    content: '';
                    right: 0;
                    left: -67px;
                    bottom: 0;
                    border-bottom: 2px solid #b5b3b3;
                    position: absolute;
                }

                &:last-child {
                    padding: 0;
                    margin: 0;

                    &::after {
                        display: none;
                    }
                }

                .title {
                    display: block;
                    margin-bottom: 5px;
                }

                p {
                    display: inline;
                }
            }
        }

        .podcast-list {
            @extend %listreset;

            li {
                padding-bottom: 23px;
                margin-bottom: 25px;
                position: relative;

                &::after {
                    content: '';
                    right: 0;
                    left: -67px;
                    bottom: 0;
                    border-bottom: 2px solid #b5b3b3;
                    position: absolute;
                }

                &:last-child {
                    padding: 0;
                    margin: 0;

                    &::after {
                        display: none;
                    }
                }

                .title {
                    display: block;
                    margin-bottom: 5px;
                }

                p {
                    display: inline;
                }
            }

            .link-hold {
                margin-top: 38px;

                a {
                    text-transform: uppercase;
                    display: inline-block;
                    position: relative;
                    color: $white;
                    font-size: 16px;
                    padding: 0 5px;
                }
            }
        }

        &.left {
            float: left;
            margin-right: 58px;
            margin-left: -99px;
            margin-top: 10px;

            @include breakpoints(none, s-large) {
                margin-left: -75px;
            }

            @include breakpoints(none, s-medium) {
                margin-left: -40px;
                margin-right: -40px;
                float: none;
            }
        }

        &.right {
            float: right;
            margin-left: 58px;
            margin-right: -99px;
            margin-top: 10px;

            @include breakpoints(none, s-large) {
                margin-right: -75px;
            }

            @include breakpoints(none, s-medium) {
                margin-right: -40px;
                margin-left: -40px;
                float: none;
            }
        }
    }

    &-newsletter {
        width: 590px;
        background: #00adba;
        padding: 29px 34px 40px 46px;
        color: $white;
        font-size: 22px;
        line-height: 1.37;

        @include bgpattern;

        @include breakpoints(none, s-large) {
            width: 380px;
        }

        @include breakpoints(none, s-medium) {
            font-size: 18px;
            width: auto;
            margin-bottom: 30px;
        }

        .cq-newsletter-title {
            display: inline-block;
            position: relative;
            text-transform: uppercase;
            font-size: 28px;
            font-weight: 700;
            padding: 7px 25px 5px 0;
            margin: 0 0 23px;
            z-index: 1;
            letter-spacing: 0.02em;
            color: $white;

            @include breakpoints(none, s-large) {
                font-size: 24px;
            }

            @include breakpoints(none, s-medium) {
                font-size: 20px;
            }

            &::after {
                content: '';
                position: absolute;
                top: 0;
                left: -66px;
                bottom: 0;
                right: 0;
                background: #5c499d;
                z-index: -1;
            }
        }

        p {
            margin-bottom: 5px;

            &:last-of-type {
                margin-bottom: 26px;
            }
        }

        .input-form {
            height: 54px;
            position: relative;
            margin-top: 24px;

            .input-addon {
                max-width: 100%;
                margin: 0;

                .mktoButtonRow {
                    width: 130px;

                    @include breakpoints(none, s-medium) {
                        width: 95px;
                    }

                    .mktoButtonWrap {
                        button.mktoButton {
                            width: 130px;
                            height: 44px;
                            // stylelint-disable-next-line declaration-no-important
                            background: $black !important;
                            // stylelint-disable-next-line declaration-no-important
                            border: 1px $black !important;

                            @include breakpoints(none, s-medium) {
                                width: 95px;
                            }
                        }
                    }
                }
            }
        }

        &.left {
            float: left;
            margin-right: 40px;
            margin-left: -99px;

            @include breakpoints(none, s-medium) {
                margin-left: -44px;
                margin-right: -44px;
                float: none;
            }
        }

        &.right {
            float: right;
            margin-left: 40px;
            margin-bottom: 30px;
            margin-right: -99px;

            @include breakpoints(none, s-medium) {
                margin-left: -44px;
                margin-right: -44px;
                float: none;
            }
        }
    }

    &-features {
        background: #0c71b8;
        padding: 53px 10% 26px;
        font-size: 22px;
        line-height: 1.26;
        color: $white;
        text-align: center;
        margin: 0 -99px 50px;
        box-shadow: 0 0 18px rgba(#000, 0.5);

        @include breakpoints(none, s-medium) {
            font-size: 16px;
            margin: 0 -40px 30px;
        }

        h3 {
            font-size: 30px;
            line-height: 1.2;
            margin-bottom: 43px;
            color: $white;

            @include breakpoints(none, s-medium) {
                font-size: 24px;
            }
        }

        &-list {
            display: flex;
            flex-wrap: wrap;

            .col {
                width: 25%;
                padding: 0 10px;

                @include breakpoints(none, s-large) {
                    width: 50%;
                    margin-bottom: 20px;
                    padding: 0 20px;
                }

                @include breakpoints(none, s-medium) {
                    width: 100%;
                    padding: 0 10px;
                }
            }

            .img {
                margin-bottom: 17px;

                @include breakpoints(none, s-medium) {
                    margin-bottom: 10px;
                }

                img {
                    vertical-align: top;
                }
            }

            .fl-title {
                display: block;
                font-size: 26px;

                @include breakpoints(none, s-medium) {
                    font-size: 20px;
                }
            }
        }
    }

    .cq-features-white {
        background: $white;
        padding: rem(20) 0 rem(25);
        line-height: 1.26;
        margin-bottom: rem(30);
        margin-top: rem(20);
        border-top: 1px solid #cacbce;
        border-bottom: 1px solid #cacbce;
        font-size: rem(18);

        @include breakpoints(medium) {
            margin-bottom: rem(50);
            margin-top: rem(40);
            padding: rem(30) 0 rem(35);
            font-size: rem(20);
        }

        @include breakpoints(large) {
            margin-bottom: rem(70);
            margin-top: rem(60);
            padding: rem(50) 0 rem(55);
            font-size: rem(22);
        }

        h2 {
            font-size: rem(38);
            line-height: 1.2;
            margin-bottom: 20px;
            color: $grapevine;

            @include breakpoints(none, s-medium) {
                font-size: rem(32);
            }
        }

        h3 {
            font-size: rem(18);
            line-height: 1.26;
            margin-bottom: rem(20);
            color: #40474f;
            font-weight: 400;

            @include breakpoints(medium) {
                margin-bottom: rem(40);
                font-size: rem(22);
            }
        }

        .cq-features-list {
            @include breakpoints(medium) {
                text-align: center;
                display: flex;
                flex-wrap: wrap;
            }

            .col {
                margin-bottom: 0;

                + .col {
                    margin-top: rem(40);

                    @include breakpoints(medium) {
                        margin-top: 0;
                    }
                }

                @include breakpoints(medium) {
                    width: 50%;
                    padding: 0 20px;
                    margin-bottom: rem(20);
                }

                @include breakpoints(large) {
                    width: 25%;
                    margin-bottom: 0;
                }

                @include breakpoints(m-large) {
                    padding: 0 30px;
                }
            }

            .img {
                margin-bottom: 10px;

                @include breakpoints(medium) {
                    margin-bottom: 17px;
                }

                img {
                    vertical-align: top;
                }
            }

            .fl-title {
                display: block;
                font-size: rem(24);
                color: #5bc2e7;
                margin-bottom: rem(10);
                font-weight: 700;

                @include breakpoints(medium) {
                    font-size: rem(28);
                }
            }

            p {
                margin-bottom: 0;
                line-height: 1.2;
                font-size: rem(16);

                @include breakpoints(medium) {
                    font-size: rem(18);
                }

                @include breakpoints(large) {
                    font-size: rem(20);
                }
            }
        }
    }

    &-share-wrapper {
        margin: 0 -3px;
        display: flex;

        .cq-share--button {
            flex: 0 0 24px;
            display: block;
            height: 24px;
            width: 24px;
            border-radius: 2px;
            overflow: hidden;
            transition: opacity 0.3s ease-out;
            margin: 0 3px;

            &:hover {
                opacity: 0.8;
            }

            &.share-fb {
                background: url('#{$image-path}/ico-sm-fb.png') no-repeat 50% 50% / 24px 24px;
            }

            &.share-pdf {
                background: url('#{$image-path}/ico-sm-pdf.png') no-repeat 50% 50% / 24px 24px;
            }

            &.share-linkedin {
                background: url('#{$image-path}/ico-sm-in.png') no-repeat 50% 50% / 24px 24px;
            }

            &.share-twitter {
                background: url('#{$image-path}/ico-sm-tw.png') no-repeat 50% 50% / 24px 24px;
            }
        }
    }
}

.cq-volume {
    .cq-post-content--short {
        display: none;
    }

    [data-toggle-cq-article] {
        span {
            &:nth-child(1) {
                display: none;
            }

            &:nth-child(2) {
                display: block;
            }
        }
    }

    &[aria-expanded="false"] {
        .cq-post-content--short {
            display: block;
        }

        .cq-post-content--full {
            display: none;
        }

        .cq-article-post:not(.cq-article-post-1) {
            display: none;
        }
    }

    &[aria-expanded="true"] {
        [data-toggle-cq-article] {
            span {
                &:nth-child(1) {
                    display: block;
                }

                &:nth-child(2) {
                    display: none;
                }
            }

            &::after {
                border-width: 0 4.5px 8px 4.5px;
                border-color: transparent transparent #231f20 transparent;
            }
        }
    }

    .share {
        margin-left: auto;
    }
}

.cq-article-post {
    + .cq-article-post {
        margin-top: rem(10);

        @include breakpoints(medium) {
            margin-top: rem(40);
        }

        @include breakpoints(large) {
            margin-top: rem(60);
        }
    }

    blockquote {
        &.cq-testimonial {
            background: #0c71b8;
            padding: 53px 10% 43px;
            color: $white;
            text-align: center;
            margin: 70px -99px 60px;

            @include bgpattern(0.05);

            @include breakpoints(none, s-large) {
                margin: 60px -75px 50px;
            }

            @include breakpoints(none, s-medium) {
                margin: 60px -40px 50px;
            }

            &::after {
                top: -120px;
            }

            q {
                display: block;
                font: 46px/1.32 $lust;
                margin-bottom: 25px;

                @include breakpoints(none, s-large) {
                    font-size: 30px;
                }

                @include breakpoints(none, s-medium) {
                    font-size: 24px;
                }
            }

            cite {
                display: block;
                font-style: normal;
                font-size: 30px;
                line-height: 1.1;
                font-weight: 300;

                @include breakpoints(none, s-large) {
                    font-size: 24px;
                }

                @include breakpoints(none, s-medium) {
                    font-size: 18px;
                }

                img {
                    border-radius: 50%;
                    margin-bottom: 21px;
                    max-width: 80px;
                }

                > strong,
                > span {
                    display: block;
                }

                > strong {
                    font-weight: 600;
                }
            }
        }
    }
}
