/*
 * Portfolio page grid & layout styles 
 */

// container reset
.page-template-page_template-demand-hub {
  @extend .page-template-page_template-crypto-startup-school-demo;

  .header-new {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: none;
  }

  h2 {
    font-weight: 700;
    font-size: 26px;
    line-height: 1.24;

    @include breakpoints(767px) {
      font-size: 36px;
    }
  }

  /* stylelint-disable selector-max-specificity */
  .content-new > .container,
  .footer-new { margin: 0 auto; }
}

.dh-banner,
.dh-newsletter .container {
  display: flex;
  align-items: center;

  &::after {
    content: '';
    width: 0;
  }
}

.dh-banner {
  @extend .crypto-banner;
  padding: 110px 0 80px;
  
  &::after {
    height: 70px;

    @include breakpoints(767px) {
      height: 194px;
    }
  }

  img + h1 {
    margin-top: rem(20);
  }

  h1 {
    color: rgb(64,72,79);
    font-size: 24px;

    @include breakpoints(767px) {
      font-size: 44px;
    }

    strong {
      font-weight: 700;
    }
  }

  h1 + .intro {
    padding-top: rem(10);
    color: rgb(64,72,79);
    font-size: 20px;

    @include breakpoints(767px) {
      font-size: 28px;
    }
  }
}

.dh-intro {
  background: #f5f4f4;
  text-align: center;
  padding: 52px 0 21px;

  @include breakpoints(767px) {
    padding: 75px 0 54px;
  }

  .container {
    max-width: 993px;
  }

  h2 {
    margin-bottom: 15px;

    @include breakpoints(767px) {
      margin-bottom: 29px;
    }
  }

  p {
    margin-bottom: 26px;
  }

  a {
    text-decoration: none;
    color: $bayside;

    &:hover {
      text-decoration: none;
      border-bottom: 1px solid $bayside;
    }
  }
}

.dh-course,
.modal {
  .crypto-article {
    counter-reset: item;

    &.crypto-article--image {
      h3 {
        &::before {
          content: none;
        }
      }

      .author {
        color: #ed8b00;
        margin-bottom: 10px;
      }
    }

    > li {
      margin-bottom: 18px;

      @include breakpoints(767px) {
        margin-bottom: 35px;
      }
    }

    .video {
      margin-bottom: 19px;
    }

    .video-placeholder {
      background: #E7E6E5;

      &::after {
        width: 74px;
        height: 53px;
        background-image: url("data:image/svg+xml,%3Csvg width='74' height='53' viewBox='0 0 74 53' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M74 3.975C74 2.89844 73.3393 1.94609 72.3895 1.53203C72.0592 1.40781 71.6875 1.325 71.3571 1.325C70.6551 1.325 69.9944 1.57344 69.4989 2.11171L52.8571 18.757V11.925C52.8571 5.34141 47.5301 0 40.9643 0H11.8929C5.32701 0 0 5.34141 0 11.925V41.075C0 47.6586 5.32701 53 11.8929 53H40.9643C47.5301 53 52.8571 47.6586 52.8571 41.075V34.2016L69.4989 50.8883C69.9944 51.4266 70.6551 51.675 71.3571 51.675C71.6875 51.675 72.0592 51.5922 72.3895 51.468C73.3393 51.0539 74 50.1016 74 49.025V3.975Z' fill='%23D0D0CE'/%3E%3C/svg%3E%0A");
      }

      &.is-coming-soon {
        &::before {
          content: 'Coming Soon';
          color: $fog;
          position: absolute;
          bottom: 3px;
          width: 100%;
          text-align: center;
          font-size: 24px;
        }
      }

      &.is-coming-soon-placeholder {
        &::after {
          width: 100%;
          height: 100%;
          background: url('/wp-content/themes/a16z-2015/client/images/demand-hub/css-comingsoon-overlay.png') no-repeat 50% 50% / contain;
        }
      }
    }

    h3 {
      &::before,
      span {
        color: $poppy;
        float: left;
        margin-right: 16px;
      }

      &::before {
        content: counter(item);
        counter-increment: item;
      }

      &.is-coming-soon {
        &::before {
          content: none;
        }
      }
    }

    .social-links {
      padding: 13px 0 0;
      font-size: 13px;

      @include breakpoints(767px) {
        font-size: 19px;
        padding-top: 19px;
      }

      .download {
        flex: 1;
        text-align: right;
        margin-right: 0;
      }

      .icon-download {
        color: $bayside;
      }
    }

    .bio {
      max-width: 740px;
      margin: 0 auto;
      background: $white;
      padding: rem(30);
      position: relative;
      z-index: 9999;

      @include breakpoints(s-medium) {
        padding: rem(50);
      }

      .close {
        transition: all 0.3s linear;
        position: absolute;
        right: 20px;
        top: 20px;
        text-decoration: none;
        color: $gray2;
        font-size: 12px;

        &:hover {
          color: $poppy;
        }
      }

      .top-row {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin-bottom: rem(20);

        @include breakpoints(s-medium) {
          margin-bottom: rem(48);
        }

        .img-wrap {
          width: 200px;
          margin-bottom: rem(20);

          @include breakpoints(s-medium) {
            margin-bottom: 0;
          }
        }

        .cont-wrap {
          width: 100%;
          font-size: rem(18);
          line-height: 1.67;

          @include breakpoints(s-medium) {
            width: calc(100% - 200px);
            padding-left: rem(50);
          }

          strong {
            display: block;
            font-weight: 700;
            color: $poppy;
            margin-bottom: rem(5);
            font-size: rem(20);
            line-height: 1.27;

            @include breakpoints(s-medium) {
              font-size: rem(22);
            }
          }
        }
      }

      .bio-content {
        font-size: 18px;
        line-height: 1.56;

        strong {
          display: block;
          font-weight: 700;
          margin-bottom: rem(16);

          @include breakpoints(s-medium) {
            margin-bottom: rem(28);
          }
        }

        p {
          margin: 0;

          + p {
            margin-top: 20px;
          }
        }
      }
    }

  }
}

