.input-addon {
   display: flex;
   max-width: 375px;
   margin: 0 auto;
   height: 44px;
   // stylelint-disable-next-line declaration-no-important
   padding: 0 !important;

   .mktoFormRow {
     flex: 1;
     width: 100%;
     // stylelint-disable-next-line declaration-no-important
     display: none !important;

     &:first-of-type {
       // stylelint-disable-next-line declaration-no-important
       display: inline-block !important;
     }

     .mktoFieldDescriptor {
       // stylelint-disable-next-line declaration-no-important
       margin: 0 !important;
       height: 100%;
     }

     .mktoFieldWrap {
       height: 100%;

       .mktoEmailField {
         flex: 1;
         width: 100%;
         background: $white;
         padding: 6px 15px;
         border: 1px solid $white;
         outline: none;
         font-size: 16px;
         font-family: font-family(sans-serif);
         border-radius: 0;
         // stylelint-disable-next-line declaration-no-important
         height: 100% !important;
       }
     }
   }

   &.mktoForm {
     .mktoGutter,
     label {
       // stylelint-disable-next-line declaration-no-important
       display: none !important;
     }
   }

   .mktoButtonRow {
     width: 95px;
     height: 44px;

     .mktoButtonWrap {
       button.mktoButton {
         width: 95px;
         height: 44px;
         // stylelint-disable-next-line declaration-no-important
         background: $poppy !important;
         // stylelint-disable-next-line declaration-no-important
         border: 1px $poppy !important;
         outline: none;
         padding: 0;
         display: flex;
         justify-content: center;
         align-items: center;
         color: $white;
         // stylelint-disable-next-line declaration-no-important
         font: 0.8rem / 1 font-family(sans-serif) !important;

         &:hover {
           // stylelint-disable-next-line declaration-no-important
           background: $poppy !important;
           // stylelint-disable-next-line declaration-no-important
           border: 1px $poppy !important;
         }

         &::after {
           content: 'Sign Up';
           // stylelint-disable-next-line declaration-no-important
           font: 1rem / 1 font-family(sans-serif) !important;
         }

         &[disabled] {
           &::after {
             content: none;
           }
         }

         > img {
           display: none;
         }

         .subscribe-input__btn {
           display: none;
           height: 20px;
           max-width: 16px;
           width: 16px;
         }
       }
     }
   }

   input:not([type="submit"]) {
     flex: 1;
     width: 100%;
     background: $white;
     padding: 6px 15px;
     border: 1px solid $white;
     outline: none;
     font-size: 16px;
   }

   button {
     width: 44px;
     height: 44px;
     background: $poppy;
     border: none;
     outline: none;
     padding: 0;
     font-size: 24px;
     display: flex;
     justify-content: center;
     align-items: center;
     color: $white;
   }
 }

.dh-course-reader {
  color: $steel;

  .head {
    padding-top: 52px;

    @include breakpoints(767px) {
      padding-top: 75px;
    }

    h2 {
      margin-bottom: 22px;

      @include breakpoints(767px) {
        margin-bottom: 36px;
      }
    }
  }

  .nice-select {
    position: relative;
    z-index: 1;
    margin-bottom: 30px;

    @include breakpoints(1023px) {
      margin-bottom: 0;
    }

    a[data-select] {
      @include breakpoints(1023px) {
        display: none;
      }
    }
  }

  .tab-nav {
    @include breakpoints(1023px) {
      display: flex;
      font-weight: 600;
      line-height: 1.25;
      height: 65px;
      font-size: 20px;
      transform: none;
      opacity: 1;
      visibility: visible;
      margin-top: 0;
      background: none;
      position: static;
      border: none;
      padding: 0;

      li {
        display: flex;
        flex: 0 0 12%;

        &:first-child {
          a::after { content: none; }
        }

        &.active a,
        a:hover {
          color: $poppy;
          background-color: #f5f4f4;

          &::after { content: none; }
        }

        a {
          width: 100%;
          display: flex;
          align-items: center;
          padding: 4px 43px;
          color: $steel;
          text-decoration: none;
          transition: all 0.25s ease-out;
          position: relative;

          &::after {
            position: absolute;
            top: 10px;
            left: -1px;
            height: 40px;
            z-index: -1;
            border-left: 1px solid #d1d1d1;
            content: '';
          }
        }
      }
    }

    @include breakpoints(1023px) {
      li {
        a {
          padding: 4px 25px;
          font-size: 14px;
        }
      }
    }

    @include breakpoints(1100px) {
      li {
        a {
          padding: 4px 30px;
          font-size: 14px;
        }
      }
    }

    @include breakpoints(1200px) {
      li {
        a {
          padding: 4px 30px;
          font-size: 16px;
        }
      }
    }

    @include breakpoints(1400px) {
      li {
        a {
          padding: 4px 35px;
          font-size: 18px;
        }
      }
    }

    @include breakpoints(1750px) {
      li {
        a {
          padding: 4px 40px;
          font-size: 20px;
        }
      }
    }
  }

  .intro {
    margin-bottom: 36px;

    @include breakpoints(767px) {
      margin-bottom: 50px;
    }
  }

  .tab-content {
    padding: 36px 0;
    background: #f5f4f4;

    @include breakpoints(767px) {
      padding: 65px 0;
    }

    h3 {
      font-size: 18px;
      line-height: 1.24;
      color: $poppy;
      margin-bottom: 5px;

      @include breakpoints(767px) {
        font-size: 28px;
      }
    }
  }

  .media-block + .media-block {
    padding-top: 56px;
  }

  .list {
    > li {
      padding: 25px 0 19px;
      border-bottom: 1px solid #d1d1d1;

      @include breakpoints(767px) {
        padding: 30px 0 27px;
      }
    }
  }

  .item {
    display: flex;
    text-decoration: none;

    .icon-file,
    span,
    em,
    h4 {
      transition: color 0.25s ease-out;
    }

    .icon-file {
      font-size: 35px;
      color: $fog;
      margin-right: 20px;

      @include breakpoints(767px) {
        width: 44px;
        margin-right: 30px;
        font-size: 56px;
      }
    }

    h4 {
      font-weight: 600;
      font-size: 16px;
      line-height: 1.15;
      margin-bottom: 5px;

      @include breakpoints(767px) {
        font-size: 22px;
        margin-bottom: 8px;
      }
    }

    p {
      span {
        color: $concrete;
      }

      em {
        color: $steel;
        font-style: normal;
      }
    }

    &:hover {
      .icon-file,
      span,
      em,
      h4 {
        /* stylelint-disable-next-line declaration-no-important */
        color: $poppy !important;
      }
    }
  }
}

.dh-instructors {
  padding: 11px 0 38px;
  background: #f5f4f4;

  @include breakpoints(767px) {
    padding-bottom: 66px;
  }

  h2 {
    text-align: center;
    margin-bottom: 37px;
  }

  .instructor-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -7px;

    > li {
      padding: 0 7px 14px;
      width: 50%;

      @include breakpoints(767px) {
        width: 33.33%;
      }

      @include breakpoints(1023px) {
        width: 25%;
      }

      @include breakpoints(1199px) {
        width: 20%;
      }
    }
  }

  .instructor {
    display: block;
    text-align: center;
    padding-top: 100%;

    @extend %objectfit;

    &:hover {
      .caption {
        visibility: visible;
        opacity: 1;
      }
    }

    .name,
    .caption {
      position: absolute;
      left: 0;
      right: 0;
    }

    .name {
      bottom: 20px;
      color: $poppy;

      @include breakpoints(767px) {
        font-size: 20px;
      }
    }

    .caption {
      opacity: 0;
      visibility: hidden;
      bottom: 0;
      top: 0;
      background-color: rgba($steel, 0.9);
      font-weight: 300;
      font-size: 14px;
      line-height: 1.6;
      color: $white;
      transition: all 0.25s ease-out;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px;

      @include breakpoints(767px) {
        font-size: 18px;
      }
    }
  }
}

.dh-faq {
  padding: 52px 0 34px;

  @include breakpoints(767px) {
    padding: 75px 0 96px;
  }

  h2 {
    text-align: center;
    margin-bottom: 24px;

    @include breakpoints(767px) {
      font-size: 36px;
      margin-bottom: 54px;
    }
  }

  .accordion {
    font-weight: 300;

    @include breakpoints(767px) {
      margin-right: 19px;
    }

    > li {
      padding-left: 0;
      margin-bottom: 13px;

      @include breakpoints(767px) {
        margin-bottom: 35px;
      }

      &::before {
        display: none;
      }

      > a {
        display: block;
        text-decoration: none;
        padding: 7px 40px 6px 0;
        font-weight: 500;
        font-size: 18px;
        line-height: 1.5;
        position: relative;
        color: $steel;
        transition: color 0.25s ease-out;

        @include breakpoints(767px) {
          font-weight: 700;
        }

        &::after {
          display: block;
          content: '\e90d';
          font: 10px/1 'icomoon';
          position: absolute;
          top: 14px;
          right: 0;
          transition: transform 0.25s ease-out;
        }

        &:hover {
          color: $poppy;
        }

        &.active {
          &::after {
            transform: rotate(180deg);
          }
        } 
      }

      > div {
        padding: 18px 22px 7px 0;
      }
    }
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  background-color: rgba($steel, 0.9);
  overflow-y: auto;
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s ease-out;

  .modal-active & {
    &.show {
      opacity: 1;
      visibility: visible;
    }
  }

  .modal-inner {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 60px 20px;

    &::after {
      content: '';
      width: 0;
      height: calc(100vh - 120px);
    }
  }

  .modal-box {
    background: $white;
    max-width: 1084px;
    margin: 0 auto;
    width: 100%;
    padding: 30px;
    position: relative;

    @include breakpoints(none, s-medium) {
      padding: 20px;
    }

    .close {
      cursor: pointer;
      position: absolute;
      top: -40px;
      right: -40px;
      font-size: 20px;
      color: rgba($white,0.5);
      transition: color 0.25s ease-out;

      @media (max-width: 1200px) {
        right: 0;
      }

      &:hover {
        color: $white;
      }
    }
  }

  .crypto-article {
    display: block;
    margin: 0;

    .video::after {
      @include breakpoints(large) {
        width: 121px;
        height: 121px;
      }
    }

    h3 {
      &::before { content: none; }
    }

    .social-links {
      margin-bottom: -17px;
    }
  }
}

.dh-instructor-modal {
  .modal-box {
    max-width: 740px;

    p {
      color: $steel;
    }
  }
}

.dh-video-modal {
  .video-container {
    position: relative;
    height: 0;
    padding-bottom: 56.25%;
    margin-bottom: 15px;

    iframe {
      position: absolute;
      width: 100%;
      height: 100%;
      display: block;
      left: 0;
      top: 0;
    }
  }
}

.bw-thumb {
  filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale");
  filter: gray;
  -webkit-filter: grayscale(100%);
}
