/*
 * Page testimonial quotes & post pullquote styles.
 */
/* Exception to select pseudo element after last paragraph
	element of .pullquote wrapper throughout this stylesheet. */
/* stylelint-disable selector-max-specificity */
.quote__list li {
  margin-bottom: rem(60); }

.quote,
blockquote:not(.pullquote),
.pullquote {
  line-height: calc(5 / 3);
  margin-bottom: rem(20);
  position: relative;
  z-index: 1; }

.pullquote {
  font-size: rem(32);
  line-height: calc(4 / 3);
  padding: 0; }
  .pullquote > * {
    font-style: normal; }

.quote__mark--left,
.quote__mark--right,
.pullquote::before,
.pullquote > p:last-child::after {
  color: #d1d1d1;
  font: 1rem/1.667 'proxima-nova', Helvetica, Arial, sans-serif;
  font-size: rem(100);
  line-height: 0;
  opacity: 0.6;
  position: absolute;
  z-index: -1; }

.quote__mark--left,
.quote__mark--right {
  display: inline-block;
  height: 0;
  width: 0; }

.pullquote::before,
.pullquote > p:last-child::after {
  display: inline-block;
  height: 0;
  width: 0; }

.quote__mark--left,
.pullquote::before {
  left: rem(-23);
  top: rem(23); }

.quote__mark--right,
.pullquote > p:last-child::after {
