/*
 * TxO page styles
 */

/* stylelint-disable selector-max-specificity */

.page-template-page_template-txo,
.page-template-page_template-txo-landing,
.page-template-page_template-txo-landing-v2 {
	@extend .page-template-page_template-crypto-startup-school-demo;

	.content-new {
		max-width: none;
		padding: 0;
		margin: 0;

		> .container {
			margin: 0 auto;
			max-width: none;
			padding: 0;
		}
	}

	.header-new {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		background: none;

		.bg-header {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			background: none;
		}
	}

	.container {
		max-width: 1220px;
		padding: 0 20px;
		margin: 0 auto;
	}

	.bio-lp-newsletter {
		padding: 112px 0 86px;

		@include breakpoints(none, s-medium) {
			padding: 31px 0 0;
		}
	}

	.bio-lp-newsletter__container {
		@include breakpoints(none, s-medium) {
			padding: 0 20px;
		}
	}

	.bio-lp-newsletter__text {
		margin-bottom: 34px;

		@include breakpoints(none, s-medium) {
			font-size: 18px;
			line-height: 1.222;
			margin-bottom: 26px;
		}
	}

	.bio-lp-newsletter__title,
	.bio-lp-newsletter__text,
	.footer-new,
	.footer-nav ul li a,
	.footer-new .social-networks li a {
		color: $white;
	}

	.bio-lp-newsletter__title {
		@include breakpoints(none, s-medium) {
			font-size: 24px;
			line-height: 1.083;
			margin-bottom: 17px;
		}
	}

	.footer-nav ul li a:hover,
	.footer-new .social-networks li a:hover {
		color: $poppy;
	}

	.footer-nav ul li + li::before {
		background: $white;
	}

	#footer {
	    background: $grapevine;
		margin-top: 0;
		padding: 32px 0 50px;
	}

	.site-footer__social-media-menu a[href*="simplecast.com"],
	.site-footer__social-media-menu a[href*="twitter.com"] {
		background: $white;
		transition: background 0.3s linear;

		&:hover {
			background: $poppy;
		}
	}

	.site-content__primary {
		margin-bottom: 0;
	}

	.input-addon .mktoFormRow .mktoFieldWrap .mktoEmailField {
		color: $steel;

		&::placeholder {
			color: #a4a6a8;
			opacity: 1;
	    }
	}

	.input-addon .mktoButtonRow .mktoButtonWrap button.mktoButton {
		// stylelint-disable-next-line declaration-no-important
		background: $purple2 !important;
		transition: background 0.3s linear;
	}

	.input-addon .mktoButtonRow .mktoButtonWrap button.mktoButton:hover {
		// stylelint-disable-next-line declaration-no-important
		background: darken($purple2, 10%) !important;
	}

	.nav-opener span,
	.nav-opener::before,
	.nav-opener::after {
		// background: $white;
	}

	.header-new .header-holder .search .icon-search:hover {
		// color: $white;
	}
}

.txo-hero {
	padding: 150px 0 104px;
	background: $grapevine url('/wp-content/themes/a16z-2015/client/images/txo/pattern-hero.png') repeat-x 0 0 / cover;
	color: $white;
	font-size: 24px;
	line-height: 1.333;
  text-align: center;

	@include breakpoints(none, s-medium) {
		background-image: url('/wp-content/themes/a16z-2015/client/images/txo/pattern-hero-mob.png');
		font-size: 18px;
		line-height: 1.222;
		padding: 86px 0 40px;
	}

  .navbar-color-dark & {
    color: $steel;
  }

	.container {
		max-width: 850px;
	}

	h1 {
		font-size: 44px;
		line-height: 1.0909;
		color: $white;
		margin-bottom: rem(18);

    .navbar-color-dark & {
      color: $steel;
    }

		@include breakpoints(none, s-medium) {
			font-size: 30px;
			line-height: 1.1333;
			margin-bottom: rem(16);
		}
	}

  img {
    margin-top: rem(18);

    @include breakpoints(none, s-medium) {
      margin-top: rem(16);
    }
  }
}

.txo-sub-nav {
	background: $purple2;
	padding: 20px 0;
  z-index: 99;

  &.is-sticky {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;

    + * {
      margin-top: 59px;
    }
  }

	@include breakpoints(none, s-medium) {
		display: none;
	}

	ul {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		text-transform: uppercase;
		font-size: 16px;
		line-height: 1.1875;
		letter-spacing: -0.01em;
		margin: -22px;
		font-weight: 800;

		li {
			padding: 22px;

			a {
				color: $white;
				text-decoration: none;
				transition: color 0.3s linear;

				&:hover {
					color: $poppy;
				}
			}
		}
	}
}

.txo-intro {
	padding: 57px 0 66px;
	text-align: center;
	background: $gray9;
	color: $steel;
	font-size: 18px;
	line-height: 1.278;

	@include breakpoints(none, s-medium) {
		font-size: 16px;
		line-height: 1.25;
		padding: 31px 0 35px;
	}

	.container {
		max-width: 850px;
	}

	h2 {
		font-size: 36px;
		line-height: 1.111;
		margin-bottom: 24px;

		@include breakpoints(none, s-medium) {
			font-size: 24px;
			line-height: 1.083;
			margin-bottom: 12px;
		}
	}

	p {
		margin-bottom: 15px;
	}

	a {
		color: $deep_ocean;
		transition: color 0.3s linear;
		text-decoration: none;

		&:hover {
			color: darken($deep_ocean, 15%);
		}
	}

	.more {
		text-decoration: none;
		display: inline-block;
		vertical-align: top;
		margin-top: 5px;
	}
}

.txo-meet-talent {

    .page-template-page_template-txo-landing & {
        padding: 56px 0;
        text-align: center;

        .header {
            max-width: 810px;
            margin: 0 auto 43px;
            font-size: 24px;
            line-height: 1.333;

            @include breakpoints(none, s-medium) {
                font-size: 18px;
                line-height: 1.222;
                margin-bottom: 25px;
            }
        }

        h2 {
            font-size: 36px;
            line-height: 1.111;
            margin-bottom: 21px;

            @include breakpoints(none, s-medium) {
                font-size: 24px;
                line-height: 1.0833;
                min-height: 10px;
            }
        }
    }

    .page-template-page_template-txo-landing-v2 & {
        padding: 72px 0;

        .header {
            position: relative;
            margin-bottom: 12px;
            overflow: hidden;

            &[data-more] {
                cursor: pointer;

                &::before {
                    content: "";
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 0;
                    width: 35px;
                    height: 8px;
                    background: #fff;
                    z-index: 2;
                }

                &::after {
                    content: "\e90d";
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 0;
                    z-index: 2;
                    font: 11px/1 "icomoon";
                    color: $steel;
                    transition: transform 0.3s linear;
                }

                h2 {
                    max-width: 80%;
                    padding-right: 20px;

                    &::after {
                        content: "";
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        left: 100%;
                        right: -9999px;
                        height: 1px;
                        background: #d0d0ce;
                        z-index: 1;
                    }
                }
            }

            &.active[data-more]::after {
                transform: translateY(-50%) rotate(180deg);
            }
        }

        h2 {
            display: inline-block;
            vertical-align: top;
            font-size: 36px;
            line-height: 1.111;
            margin: 0;
            background: #fff;
            position: relative;
            z-index: 1;

            @include breakpoints(none, s-medium) {
                font-size: 24px;
                line-height: 1.0833;
                min-height: 10px;
            }
        }

        .row {
            padding-top: 28px;

            @include breakpoints(none, s-medium) {
                padding-top: 20px;
            }
        }
    }

	@include breakpoints(none, s-medium) {
		padding: 32px 0 42px;
	}

	.highlight-txt {
		font-size: 24px;
		line-height: 1.333;
		margin-bottom: 32px;

		@include breakpoints(none, s-medium) {
			font-size: 18px;
			line-height: 1.222;
			margin-bottom: 25px;
		}
	}

	.desc {
		font-size: 18px;
		line-height: 1.278;
		padding-bottom: 30px;

		@include breakpoints(none, s-medium) {
			font-size: 16px;
		}
	}

	.row {
		display: flex;
		flex-wrap: wrap;
		margin: -20px -18px;

		@include breakpoints(none, s-medium) {
			margin: -15px;
		}

		.col {
			width: 33.33%;
			padding: 20px 18px;

			@include breakpoints(none, s-large) {
				width: 50%;
			}

			@include breakpoints(none, s-medium) {
				width: 100%;
				padding: 15px;
			}
		}
	}

	.img-wrap {
		position: relative;

    &::before {
      content: "";
      position: relative;
      display: block;
      width: 100%;
      padding-top: 62.16%;
    }

		.img {
      position: absolute;
      left: 0;
      top: 0;
			width: 100%;
      height: 100%;
      background: $poppy no-repeat 50% 50% / cover;
		}

		.play {
			position: absolute;
			right: 27px;
			bottom: 27px;
			background: $deep_ocean;
			padding: 6px 12px 6px 28px;
			font-size: 16px;
			line-height: 1.1875;
			color: $white;
			font-weight: 700;
			text-decoration: none;
			transition: background 0.3s linear;

			@include breakpoints(none, s-medium) {
				padding: 3px 12px 6px 24px;
				font-size: 15px;
				line-height: 1.2;
				right: 25px;
				bottom: 25px;
			}

			&::before {
				content: "";
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 6px 0 6px 9px;
				border-color: transparent transparent transparent $white;
				position: absolute;
				left: 13px;
				top: 50%;
				transform: translateY(-50%);

				@include breakpoints(none, s-medium) {
					border-width: 5px 0 5px 8px;
				}
			}

			&:hover {
				background: darken($deep_ocean, 10%);
			}
		}
	}

	.personal-social,
	.company-social {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		padding: 10px;

		span {
			font-size: 18px;
			line-height: 1.111;
			color: $concrete;

			@include breakpoints(none, s-medium) {
				font-size: 16px;
				line-height: 1.111;
			}
		}

		strong {
			font-size: 20px;
			line-height: 1.1;
			font-weight: 600;
			color: $steel;

			@include breakpoints(none, s-medium) {
				font-size: 16px;
				line-height: 1.111;
			}
		}

		ul {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin: 0 -6px;

			@include breakpoints(none, s-medium) {
				margin: 0 -8px;
			}

			li {
				padding: 0 6px;

				@include breakpoints(none, s-medium) {
					padding: 0 8px;
				}

				a {
					text-decoration: none;
					color: $poppy;
					transition: opacity 0.3s linear;

					&:hover {
						color: $poppy;
					}

					&.icon-web {
						font-size: 19px;

						@include breakpoints(none, s-medium) {
							font-size: 17px;
						}
					}

					&.icon-facebook-a {
						font-size: 19px;

						@include breakpoints(none, s-medium) {
							font-size: 17px;
						}
					}

					&.icon-linkedin-a {
						font-size: 17px;

						@include breakpoints(none, s-medium) {
							font-size: 15px;
						}
					}

					&.icon-twitter {
						font-size: 16px;
					}
				}
			}
		}
	}

	.personal-social {
    border-top: 1px solid $fog;
    text-align: left;

    .left-col {
      display: flex;
    }

    span {
      margin-right: 10px;
      font-weight: 400;
    }

    strong {
			font-size: 18px;
			line-height: 1.111;
      font-weight: 600;

			@include breakpoints(none, s-medium) {
				font-size: 16px;
			}
		}
	}
}

