	.container {
		max-width: 850px;
		position: relative;
		z-index: 2;
	}

	h2 {
		font-size: 38px;
		line-height: 1.052;
		color: $white;
		margin-bottom: 24px;

		@include breakpoints(none, s-medium) {
			font-size: 24px;
			line-height: 1.083;
			margin-bottom: 12px;
		}
	}

	p {
		margin-bottom: 24px;

		@include breakpoints(none, s-medium) {
			margin-bottom: 19px;
