.txo-community {
	padding: 57px 0 71px;

	@include breakpoints(none, s-medium) {
		padding: 31px 0 47px;
	}

	.header {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-end;
		border-bottom: 1px solid $fog;
		padding-bottom: 22px;
		margin-bottom: 45px;

		@include breakpoints(none, s-medium) {
			display: block;
			padding-bottom: 4px;
			margin-bottom: 24px;
		}

		h2 {
			font-size: 36px;
			line-height: 1.111;
			color: $steel;

			@include breakpoints(none, s-medium) {
				font-size: 24px;
				line-height: 1.083;
				margin-bottom: 14px;
			}
		}

    .tab-nav {
      a {
        cursor: pointer;
        background: 0 0;
        border: none;
        color: color(gray-light);
        font-size: 1rem;
        text-decoration: none;

        &.active,
        &:hover {
          color: $poppy;
          outline: 0;
        }
      }

      li {
        &.active {
          a {
            color: $poppy;
            outline: 0;
          }
        }
      }
    }

		ul {
			display: flex;
			flex-wrap: wrap;
			margin: 0 -15px;
			font-size: 18px;
			line-height: 1.222;
			color: $fog;
			font-weight: 500;

			li {
				padding: 0 15px;
				margin-bottom: 12px;
			}
		}
	}

	[class*="list-"] {
		color: $steel;
    display: flex;
    flex-flow: row wrap;
    margin-bottom: 3rem;
    width: 100%;
    justify-content: space-between;

    @supports (display: grid) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: rem(18);

      @include breakpoints(small) {
        grid-template-columns: repeat(5, 1fr);
      }

      @include breakpoints(none, s-large) {
        grid-template-columns: repeat(3, 1fr);
      }

      @include breakpoints(none, s-medium) {
        grid-template-columns: repeat(1, 1fr);
        column-gap: rem(16);
      }
    }

		li {
			-webkit-column-break-inside: avoid;
			page-break-inside: avoid;
			break-inside: avoid-column;
			display: table;

      margin-bottom: 16px;

      @include breakpoints(none, s-medium) {
        margin-bottom: 14px;
      }
		}
	}

	.list-1 {
		font-size: 20px;
		line-height: 1;
		margin-bottom: 53px;
		font-weight: 700;

		@include breakpoints(none, s-medium) {
			font-size: 16px;
			line-height: 1.125;
			margin-bottom: 25px;
		}
	}

	.list-2 {
		font-size: 18px;
		line-height: 1.222;

		@include breakpoints(none, s-medium) {
			font-size: 16px;
			line-height: 1.125;
		}

		li {
      @include breakpoints(none, s-medium) {
        margin-bottom: 16px;
      }
		}
	}
}

