			z-index: 1;
            background: $white no-repeat 50% 50% / cover;
            //box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.4);
		}

        h3 {
            color: $steel;
        }
	}

	.title {
		display: block;
		background: $grapevine;
		padding: 7px 20px;
		color: $white;
		font-size: 17px;
		line-height: 1.117;
		text-transform: uppercase;
		letter-spacing: -0.04em;
		position: relative;
		z-index: 2;
	}

	.box-content {
		padding: 19px 20px;
		position: relative;
		z-index: 2;

		@include breakpoints(none, s-medium) {
			padding: 21px 20px;
		}
	}

	h3 {
		font-size: 26px;
		line-height: 1.307;
		color: $white;
		margin-bottom: 19px;
		font-weight: 600;

		@include breakpoints(none, s-medium) {
			font-size: 20px;
			line-height: 1.3;
			margin-bottom: 17px;
		}
	}

	.by {
		display: block;
		font-size: 18px;
		line-height: 1.222;
	}

	.more {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 2;
		padding: 9px 21px;
		text-align: right;
		text-transform: uppercase;

		@include breakpoints(none, s-medium) {
			padding: 21px;
		}

		a {
			color: $white;
			text-decoration: none;
			transition: color 0.3s linear;

			&:hover {
				color: $grapevine;
			}
		}
	}

	.foot {
		font-size: 16px;
		line-height: 1.1875;
		letter-spacing: 0.1px;

		a {
			color: $grapevine;
			font-weight: 500;
			text-decoration: none;
			transition: color 0.3s linear;

			&:hover {
				color: $purple2;
			}
		}
	}
}

.txo-faq {
	padding: 57px 0 62px;

	@include breakpoints(none, s-medium) {
		padding: 37px 0;
	}

	h2 {
		font-size: 36px;
		line-height: 1.111;
		color: $steel;
		margin-bottom: 25px;

		@include breakpoints(none, s-medium) {
			font-size: 24px;
			line-height: 1.083;
			margin-bottom: 18px;
		}
	}

	.accordion {
		font-size: 16px;
		line-height: 1.6875;

		@include breakpoints(none, s-medium) {
			line-height: 1.25;
		}

		li {

			+ li {
				margin-top: 47px;

				@include breakpoints(none, s-medium) {
					margin-top: 18px;
				}
			}
		}

		a[data-more] {
			display: block;
			position: relative;
			padding-right: 50px;
			font-size: 18px;
			line-height: 1.5;
			font-weight: 700;
			color: $steel;
			text-decoration: none;
			transition: color 0.3s linear;

			@include breakpoints(none, s-medium) {
				font-size: 16px;
				line-height: 1.375;
				padding-right: 36px;
			}

			&::after {
				content: '\e90d';
				position: absolute;
				right: 0;
				position: absolute;
				top: 7px;
				font: 10px/1 "icomoon";
				transition: transform 0.3s linear, color 0.3s linear;

				@include breakpoints(none, s-medium) {
					font-size: 10px;
				}
			}

			&.active,
			&:hover {
				// stylelint-disable-next-line declaration-no-important
				color: $poppy !important;
			}

			&:focus {
				color: $steel;
			}

			&.active::after {
				transform: rotate(180deg);
			}
		}

		.slide {
			padding: 24px 50px 0 0;

			@include breakpoints(none, s-medium) {
				padding: 12px 40px 0 0;
			}
		}
	}
}

.txo-intro__rich-text-wrap {
	overflow: hidden;

	&[aria-expanded="true"] {
		max-height: 100%;
	}

	&[aria-expanded="false"] {
		max-height: rem(100);
	}
}

.txo-intro__intro {
	[aria-expanded="true"] & {
		display: none;
	}
}

.txo-intro__main {
	[aria-expanded="true"] & {
		display: block;
	}
}

.txo-intro__intro {
	[aria-expanded="false"] & {
		display: block;
	}
}

.txo-intro__main {
	[aria-expanded="false"] & {
		display: none;
	}
}

[data-team-filter] {
  cursor: pointer;
  background: 0 0;
  border: none;
  color: color(gray-light);
  font-size: 1rem;
  text-decoration: none;

  &[aria-selected=true],
  &:hover {
    color: $poppy;
    outline: 0;
  }
}

.txo-community {
  [data-team-filter-val] {
    &[aria-hidden=true] {
      display: none;
    }
  }
}

.txo-community-block {
	font-size: 18px;
	line-height: 1.277;
	padding: 57px 0 38px;

	@include breakpoints(none, s-medium) {
		padding: 40px 0 30px;
	}

	h2 {
		font-size: 36px;
		line-height: 1.111;
		color: $steel;
		margin-bottom: 14px;

		@include breakpoints(none, s-medium) {
			font-size: 28px;
			line-height: 1.083;
			margin-bottom: 14px;
		}
	}
}

h3.subhead {
	font-size: 28px;
	line-height: 1.14;
	color: $grapevine;
	margin-bottom: 17px;

	@include breakpoints(none, s-medium) {
		font-size: 24px;
		line-height: 1.14;
		margin-bottom: 15px;
	}
}

.txo-advisory-committee {
	padding-bottom: 43px;
	margin-bottom: 40px;
	position: relative;
	font-size: 18px;
	line-height: 1.277;

	.committee-members {
		display: flex;
		flex-wrap: wrap;
		margin: -10px;
		padding-top: 23px;

		.member {
      display: block;
			width: 16.66%;
			padding: 10px;
      text-decoration: none;

			@include breakpoints(none, s-large) {
				width: 25%;
			}

			@include breakpoints(none, s-medium) {
				width: 50%;
			}

			img {
				width: 100%;
			}
		}

		.avatar-wrap {
			margin-bottom: 14px;
		}

		.name {
			display: block;
			font-size: 20px;
			line-height: 1;
			color: $steel;
			font-weight: 700;
			margin-bottom: 2px;
		}

		.title {
			display: block;
			font-size: 18px;
			line-height: 1.44;
			color: $concrete;
		}
	}
}

.txo-donors {
	padding-bottom: 45px;
	margin-bottom: 39px;
	position: relative;
	font-size: 18px;
	line-height: 1.277;

	h3 {
		margin-bottom: 30px;

		@include breakpoints(none, s-medium) {
			margin-bottom: 20px;
		}
	}

	.donors-list {
		display: flex;
		flex-wrap: wrap;
		margin: -8px;
		font-size: 22px;
		line-height: 1.1818;
		font-weight: 700;
		color: $steel;

		@include breakpoints(none, s-large) {
			font-size: 20px;
		}

		@include breakpoints(none, s-medium) {
			font-size: 18px;
		}

		li {
			width: 20%;
			padding: 8px;

			@include breakpoints(none, s-large) {
				width: 33.33%;
			}

			@include breakpoints(none, s-medium) {
				width: 50%;
			}

			@media only screen and (max-width: 374px) {
				width: 100%;
			}
		}
	}
}

.txo-partners,
.txo-donors {
  p {
    margin-bottom: 24px;

    @include breakpoints(none, s-medium) {
      margin-bottom: 19px;
    }
