/*
 * Podcast page styles
 */

/* stylelint-disable selector-max-specificity */
body {
	&.page-template-page_template-podcast,
	&.page-template-page_template-podcasts-hub {
		background: $white;

		&::before {
			content: "";
			background: url('/wp-content/themes/a16z-2015/client/images/podcast/pattern.png') no-repeat 0 0 / 932px 632px;
			width: 932px;
			height: 632px;
			position: absolute;
			right: 0;
			top: 0;

			@include breakpoints(none, m-large) {
				background-size: 700px 500px;
				width: 700px;
				height: 500px;
			}

			@include breakpoints(none, s-large) {
				background-size: 500px 300px;
				width: 500px;
				height: 300px;
			}

			@include breakpoints(none, s-medium) {
				background-size: 300px 180px;
				width: 300px;
				height: 180px;
			}
		}

		#header {
			background: none;
		}

		.content-new {
			@include breakpoints(none, s-medium) {
				padding-top: 0;
			}

			> .container {
				margin: 0;
			}
		}
	}
}

.podcast-content {
	padding: 44px 23px 0;
	position: relative;
	z-index: 1;

	@include breakpoints(none, m-large) {
		padding: 44px 0 0;
	}

	@include breakpoints(none, s-large) {
		padding: 0;
	}
}

.podcast-content-intro {
	max-width: 584px;
	margin-bottom: 40px;
	color: $steel;

	h1 {
		font-size: 50px;
		line-height: 1.4;
		color: $steel;
		letter-spacing: -0.9px;
		margin-bottom: 9px;

		@include breakpoints(none, s-medium) {
			font-size: 30px;
		}
	}
}

.podcast-content-list-wrap {
	margin-bottom: 40px;
}

.podcast-content-list {
	color: #828282;

	+ .podcast-content-list {
		margin-top: 36px;
	}

	&__item {
		display: flex;
		background: $white;
		box-shadow: 0 4px 20px rgba(0, 0, 0, 0.04);

		@include breakpoints(none, s-large) {
			padding: 25px;
			display: block;
		}

		@include breakpoints(none, s-medium) {
			padding: 0;
			box-shadow: none;
			border-radius: 0;
		}
	}

	&__img {
		width: 300px;

		@include breakpoints(none, m-large) {
			width: 250px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;

			@include breakpoints(none, m-large) {
				object-fit: contain;
			}
		}
	}

	&__desc {
		flex: 1;
		flex-wrap: wrap;
		align-items: center;
		padding: 20px 30px 20px 37px;

		@include breakpoints(none, m-large) {
			padding: 25px;
		}

		@include breakpoints(none, s-large) {
			flex: none;
			width: 100%;
			padding: 25px 0 0;
			display: block;
		}
	}

	h2 {
		font-size: 24px;
		line-height: 1.625;
		color: $steel;
		margin-bottom: 14px;

		@include breakpoints(none, m-large) {
			margin-bottom: 10px;
		}
	}

	.text-wrap {
		padding-right: 130px;
		font-weight: 300;
		margin-bottom: 40px;

		@include breakpoints(none, m-large) {
			padding-right: 0;
			margin-bottom: 15px;
		}

    a {
      color: #4F4F4F;

      &:hover {
        color: $poppy;
      }
    }
	}

	.footer {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
	}

	.btn {
		transition: background 0.3s linear;
		font-weight: 600;
		border-radius: 0;
		min-width: 148px;
	}

	.btn-gray {
		background: #333;

		&:hover {
			background: $black;
		}
	}

	.btn-blue {
		background: $bayside;

		&:hover {
			background: darken($bayside, 15%);
		}
	}

	.btn-dark-blue {
		background: $deep_ocean;

		&:hover {
			background: darken($deep_ocean, 15%);
		}
	}

	.btn-magenta {
		background: $grapevine;

		&:hover {
			background: darken($grapevine, 15%);
		}
	}

	.btn-green {
		background: $avocado;

		&:hover {
			background: darken($avocado, 15%);
		}
	}

	.btn-fintech-blue {
		background: $marina;

		&:hover {
			background: darken($marina, 15%);
		}
	}

	.right-nav {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		font-size: 15px;
		line-height: 1.6;
		font-weight: 500;
		padding: 10px 0 10px 20px;
		margin-left: auto;

		@include breakpoints(none, s-medium) {
			width: 100%;
			display: block;
			padding: 20px 0 0 0;
		}

		.title {
			color: #9d9d9d;
			padding-right: 3px;
			position: relative;
		}

		.list {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include breakpoints(none, s-medium) {
				flex: none;
				width: 100%;
			}

			@media screen and(max-width: 374px) {
				font-size: 14px;
			}

			> li {
				position: relative;
				padding-left: 11px;
				margin-left: 11px;

				@media screen and(max-width: 374px) {
					padding-left: 7px;
					margin-left: 7px;
				}

				&::before {
					content: "";
					background: #c9cbce;
					width: 1px;
					height: 10px;
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
				}

				&:last-child {
					margin-left: 0;

					&::before {
						content: none;
					}
				}

				&:first-child {
					@include breakpoints(none, s-medium) {
						padding-left: 0;
						margin-left: 0;
					}

					&::before {
						@include breakpoints(none, s-medium) {
							content: none;
						}
					}
				}

				> a {
					color: #333;
					text-decoration: underline;

					&:hover {
						text-decoration: none;
					}

					&.more {
						display: block;
						padding: 5px 39px 5px 15px;
						border: 1px solid #f0f0f0;
						border-radius: 2px;
						text-decoration: none;
						position: relative;

						@media screen and(max-width: 374px) {
							padding: 5px 24px 5px 8px;
						}

						&::after {
							content: "";
							width: 0;
							height: 0;
							border-style: solid;
							border-width: 5px 4px 0 4px;
							border-color: $steel transparent transparent transparent;
							position: absolute;
							right: 13px;
							top: 50%;
							transform: translateY(-50%);

							@media screen and(max-width: 374px) {
								right: 10px;
							}
						}

						&.active + .drop {
							opacity: 1;
							visibility: visible;
						}
					}
				}

				.drop {
					transition: opacity 0.3s linear, visibility 0.3s linear;
					position: absolute;
					min-width: 190px;
					top: 100%;
					right: 0;
					padding-top: 5px;
					z-index: 100;
					opacity: 0;
					visibility: hidden;
				}

				.drop-list {
					border: 1px solid #f0f0f0;
					box-shadow: 0 6px 14px rgba(0, 0, 0, 0.07);
					border-radius: 2px;
					overflow: hidden;
					padding: 20px 22px;
					font-size: 15px;
					line-height: 1.6;
					background: $white;

					@media screen and(max-width: 374px) {
						font-size: 14px;
						padding: 18px 20px;
					}

					li {

						+ li {
							margin-top: 6px;
						}

						a {
							color: $steel;
							text-decoration: none;

							&:hover {
								color: $black;
							}
						}
					}
				}
			}
		}
	}
}

.podcast-cta {
	background: $gray7;
	padding: 28px 40px 41px;
	color: #828282;
	font-weight: 300;
	line-height: 1.625;
	letter-spacing: 0.1px;

	@include breakpoints(none, s-medium) {
		padding: 20px;
	}

	h2 {
		color: $poppy;
		font-size: 24px;
		line-height: 1.625;
		font-weight: 700;
		margin-bottom: 9px;
		letter-spacing: 0;
	}

	a {
		color: $poppy;
		text-decoration: none;
		transition: color 0.3s linear;

		&:hover {
			color: $poppy;
		}
	}

	p {

		+ p {
			margin-top: 9px;
		}
	}
}
