/*
 * FAQ Styles
 */

/* stylelint-disable */
/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */

.faq-section {
    color: $steel;
    padding: rem(50 0 112);

    @include breakpoints(none, s-large) {
        padding: rem(50 0 90);
    }

    @include breakpoints(none, s-medium) {
        padding: rem(40 0 70);
    }

    h1 {
        font-size: rem(40);
        line-height: 1.2;
        margin-bottom: rem(36);
        letter-spacing: -0.005em;

        @include breakpoints(none, s-large) {
            font-size: rem(36);
            margin-bottom: rem(32);
        }
    }

    .faq-intro {
        margin-bottom: rem(38);
    }
}

.faq-row {

    + .faq-row {
        margin-top: rem(40);
    }

    h2 {
        font-size: rem(34);
        line-height: 1.117;
        margin-bottom: rem(20);
        color: $poppy;
        letter-spacing: -0.01em;

        @include breakpoints(none, s-large) {
            font-size: rem(30);
        }
    }

    p {
        margin-bottom: rem(18);
    }

    .accordion {

        > li {
            border: solid $fog;
            border-width: 0 0 1px;

            &:first-child {
                border-width: 1px 0;
            }

            > a {
                display: block;
                font-size: rem(20);
                line-height: 1.1;
                font-weight: 700;
                text-decoration: none;
                color: $steel;
                letter-spacing: -0.015em;
                position: relative;
                padding: rem(24 30 24 0);

                @include breakpoints(none, s-large) {
                    font-size: rem(18);
                }

                &:after {
                    content: "\e90d";
                    font: 13px/1 "icomoon";
                    color: $bayside;
                    position: absolute;
                    right: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    transition: transform 0.3s linear;
                }

                &.active:after {
                    transform: translateY(-50%) rotate(180deg);
                }
            }
        }

        .slide-content {
            padding: rem(2 30 31 0);

            @include breakpoints(none, s-medium) {
                padding: rem(2 30 10 0);
            }

            p {
                margin: 0;

                + p {
                    margin-top: rem(16);
                }
            }
        }

        ul {
            padding-left: rem(12);

            li {
                position: relative;
                padding-left: rem(21);
                position: relative;

                &:before {
                    content: "";
                    width: 5px;
                    height: 5px;
                    position: absolute;
                    left: 0;
                    top: 10px;
                    background: $steel;
                }

                + li {
                    margin-top: rem(7);
                }

                .inline-link {
                    text-decoration: none;
                    color: $bayside;
                    transition: color 0.3s linear;
                    font-size: rem(18);
                    line-height: 1.333;

                    &:hover {
                        color: darken($bayside, 10%);
                    }
                }
            }
        }
    }
}

.contact-us-bar {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background: $steel;
    padding: rem(41 0);
    font-size: rem(26);
    line-height: 1.34;
    color: $white;
    position: relative;

    @include breakpoints(none, s-large) {
        font-size: rem(22);
    }

    @include breakpoints(none, s-medium) {
        text-align: center;
    }

    .container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }

    .txt-wrap {
        padding-right: rem(32);
        flex: 1;
        font-weight: 700;

        @include breakpoints(none, s-medium) {
            padding: rem(0 0 24);
            flex: none;
            width: 100%;
        }

        p {
            margin: 0;
        }
    }

    .btn {
        padding: 11px 19px;
    }
}

.summit-event-newsletter {
    padding: rem(98 0 62);
    color: $steel;

    @include breakpoints(none, s-medium) {
        padding: 50px 0 30px;
    }

    &__thanks-msg {
        text-align: center;

        h2 {
            color: $white;
            font-size: rem(36);
            margin-bottom: rem(24);
            font-weight: 600;
            line-height: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: relative;
            align-items: flex-start;
            padding-left: 65px;
            padding-top: 20px;

            @include breakpoints(none, s-medium) {
                font-size: rem(28);
                margin-bottom: rem(15);
            }

            &::before {
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                width: 45px;
                height: 45px;
                background: url("data:image/svg+xml,%3Csvg width='46' height='46' viewBox='0 0 46 46' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M35.6254 24.3987C36.4397 28.2706 35.9178 32.3041 34.145 35.8412C32.3722 39.3784 29.453 42.2104 25.8638 43.8753C22.2746 45.5401 18.2272 45.9396 14.3817 45.0085C10.5363 44.0774 7.11986 41.8707 4.68977 38.7484C2.25969 35.626 0.959377 31.7724 1.00097 27.816C1.04256 23.8597 2.42359 20.0342 4.91878 16.9637C7.41396 13.8931 10.876 11.7587 14.7402 10.9087C18.6043 10.0586 22.6425 10.5431 26.1959 12.283' stroke='%23ED8B00'/%3E%3Cpath d='M8.5 26L17.5 35L45.5 1' stroke='white'/%3E%3C/svg%3E%0A") no-repeat 50% 50% / contain;
            }
        }

        p {
            padding-left: 40px;
        }
    }

    &__container {
        max-width: 1118px;
        padding: 0 30px;
        margin: 0 auto;

        @media (min-width: 1200px) {
            padding: 0 15px;
        }
    }

    &__title {
        color: #3c3e43;
        font-size: rem(36);
        font-weight: 400;
        line-height: 1;
        margin-bottom: rem(15);
        text-align: center;

        @include breakpoints(none, s-medium) {
            font-size: rem(28);
            margin-bottom: rem(15);
        }
    }

    &__text {
        font-size: rem(26);
        font-weight: 300;
        line-height: 1.307;
        margin-bottom: rem(34);
        text-align: center;
        letter-spacing: -0.02em;

        @include breakpoints(none, s-medium) {
            font-size: rem(16);
            margin-bottom: rem(30);
        }
    }

    &__form {
        align-items: center;
        display: flex;
        justify-content: center;

        input {
            color: $concrete;
            font-size: rem(18);
            padding: rem(15);
            width: rem(370);
            color: $steel;

            ::-webkit-input-placeholder { 
                color: $concrete;
            }
            ::-moz-placeholder {
                color: $concrete;
            }
            :-ms-input-placeholder {
                color: $concrete;
            }
            :-moz-placeholder {
                color: $concrete;
                opacity: 1;
            }
        }

        button {
            appearance: none;
            border: none;
            background-color: $steel;

            &:hover,
            &:focus {
                cursor: pointer;
            }
        }
    }

    &__submit {
        height: rem(55);
        width: rem(55);
    }

    .input-addon {
        height: 50px;

        .mktoButtonRow .mktoButtonWrap button.mktoButton {
            width: 108px;
            height: 50px;

            &:hover,
            &:focus {
            }
        }
    }
}
